import RootStore from 'core/Store'
import * as api from 'core/api'

class Store extends RootStore {
    constructor(actions) {
        super();
        this.listenables = actions
		this.state = {
            api: '',
            params: {}
		}
        this.init()
    }

    init() {
        this.$on('components/services/MemoSteps',(key, data) => {
            console.log('data MemoSteps:', data)
            this.setState({
                api: data.api,
                params: data.params
            })
        })
    }

    onRemoveEvents() {
        this.clearEvents()
    }
}

export default Store



