import RootStore from 'core/Store'
import * as api from 'core/api'

class Store extends RootStore {
    constructor(actions) {
        super()
        this.listenables = actions
        this.state = {
            test:'default',
            taskTime:new Date().getFullYear() + "-" + (new Date().getMonth() + 1) + "-" + new Date().getDate() ,
            project:"",
            projectId:"",
            subproject:"",
            isShow: false,
            isShowmodal: false,
            projectSelect:{
                projectId:-1
            },
            subprojectSelect:{
                subprojectId:-1
            },
            formData:{
                "subprojectId":"",
                "openId": this.cookie.get('openId'),
                "taskType":"",
                "taskTime": "",
                "taskReply": "",
                "scheduleMembers": [
                    {
                        openId: ""
                    }
                ]
            },
            tagList:[
                {
                    key:0,
                    isCheck:false,
                    tagId: 'a',
                    label: '进度安排'
                },
                {
                    key:1,
                    isCheck:false,
                    tagId: 'b',
                    label: '出图提醒'
                },
                {
                    key:2,
                    isCheck:false,
                    tagId: 'c',
                    label: '会议通知'
                },
                {
                    key:3,
                    isCheck:false,
                    tagId: 'd',
                    label: '外部条件'
                },
                {
                    key:4,
                    isCheck:false,
                    tagId: 'e',
                    label: '内部协调'
                }
            ],
            tagSelect:{}
        }
        this.init()
    }

    init() {
        this.$on('pages/TaskAdd/taskTime',(key, { taskTime }) => {
            // let time = taskTime.year + '-' + taskTime.month + '-' + taskTime.date
            /*let delayTime = this.dateFormat(time)
            let time = `${delayTime.year}-${delayTime.month}-${delayTime.day}`*/
            this.setState({
                taskTime
            })
        })
        this.$on('pages/TaskAdd/projectSelect',(key, { projectSelect }) => {
            this.setState({
                projectSelect
            })
            console.log(projectSelect)
        })

        this.$on('pages/TaskAdd/subprojectSelect',(key, { projectSelect }) => {
            this.setState({
                subprojectSelect: projectSelect
            })
        })

        this.$on('pages/TaskAdd/memberSelect',(key, { memberSelect }) => {
            let formData = this.state.formData
            formData.scheduleMembers = memberSelect
            this.setState({
                formData
            })
            console.log(formData)

        })


    }

    onTestAction(value) {
        this.setState({
            test: value
        })
    }

    onRemoveEvents() {
        this.clearEvents()
    }

    onHandleChange(type, value, index) {
        console.log(type, value, index)
        let formData = this.state.formData
        switch (type) {
            case 'subprojectId': {
                formData[type] = value
            }
                break
            case 'taskType': {
                formData[type] = value
            }
                break
            case 'taskContent': {
                formData[type] = value
            }
                break
            case 'taskTime': {
                formData[type] = value
            }
                break
            case 'taskReply': {
                formData[type] = value
            }
                break
        }

        this.setState({
            formData
        })
        console.log(formData)
    }

    onHandleClick() {
        this.$emit('services/ModalDate/state',{
            isOpen: true
        })
    }


    onHandleCancel() {
        this.$route.goBack()
    }

    onHandleSave() {
        console.log(this.state.tagSelect)
        let formData = Object.assign({}, this.state.formData, {
            "subprojectId": this.state.subprojectSelect.subprojectId,
            "taskType": this.state.tagSelect.tagId,
            "taskTime": this.state.taskTime
        })

        this.$ajax
            .post(this.api('project/addSchedule')(),
                formData
            )
            .then( res => {
                if(res.data.result === 'success'){
                    console.log(res)
                    this.$route.push('/')
                }
            })
            .catch( error => {
                console.log('error:', error)
            })

        console.log('formData',formData)

    }

    onProjectToggle(){
        event.stopPropagation()
        let isShow = true
        this.setState({
            isShow
        })
        this.$emit('services/ProjectSelect/isShow',{
            isShow,
            isProject:true
        })
    }

    onSubprojectToggle(){
        event.stopPropagation()
        console.log()
        if(this.state.projectSelect.projectId !== -1){
            let isShow = true
            this.setState({
                isShow
            })
            this.$emit('services/ProjectSelect/isShow',{
                isShow,
                isProject:false
            })

        }

    }

    onTagToggle(){
        event.stopPropagation()
        let isShowmodal = true
        this.setState({
            isShowmodal
        })
    }

    onMemberToggle(){
        event.stopPropagation()
        let subprojectId = this.state.subprojectSelect.subprojectId
        if( subprojectId !== -1){
            let isShow = true
            this.setState({
                isShow
            })
            this.$emit('services/TaskmemberSelect/isShow',{
                isShow,
                subprojectId
            })
        }
    }

    onHandleCheck(list){
        this.setState({
            tagList:list
        })
    }

    onConfirm(){
        console.log(this.state.tagList)
        let list = this.state.tagList,
            tagSelect = {},
            index = 0

        list.map((item, i) => {
            if(item.isCheck === true){
                tagSelect = {
                    tagId: item.tagId,
                    label: item.label
                }
                index ++
            }
        })

        this.setState({
            isShowmodal: false,
            tagSelect
        })
        console.log(tagSelect)
    }

    onCancel(){
        this.setState({
            isShowmodal: false
        })
    }
}


export default Store



