import RootStore from 'core/Store'
import * as api from 'core/api'

class Store extends RootStore {
    constructor(actions) {
        super()
        this.listenables = actions
        this.state = {
            isShow:false,
            projectList:[],
            subprojectList: [],
            projectSelect:{},
            curprojectId: -1,
            cursubprojectId: -1,
            projectName:"",
            projectId:123,
            isProject:true
        }
        this.init()
    }

    init() {
        this.$on('services/ProjectSelect/isShow',(key, { isShow, isProject }) => {
            console.log(isProject)
            if(isShow){
                this.onInsertProjectList(isProject)
            }
            this.setState({
                // isShow,
                isProject
            })

        })

        /*this.$on('pages/TaskAdd/projectSelect',(key, projectSelect ) => {
            this.setState({
                projectSelect,
                projectName: projectSelect.label,
                formData: Object.assign({}, this.state.formData, {"subprojectId": projectSelect.subprojectId})
            })
            console.log(projectSelect)
        })*/
    }

    onTestAction(value) {
        this.setState({
            test: value
        })
    }

    onRemoveEvents() {
        this.clearEvents()
    }

    insertProjectList(){
        this.$ajax
            .get(this.api('mine/project')(),{
                params: {
                    openId: this.cookie.get('openId')
                }
            })
            .then( res => {
                if(res.status === 200){
                    let projects = res.data.project,
                        projectList = new Array()
                    for (let i=0; i<projects.length;i++){
                        var item = {
                            'isCheck': projects[i].projectId === this.state.curprojectId ? true  : false,
                            'key': i,
                            'projectId':projects[i].projectId,
                            'label': projects[i].project
                        }
                        projectList[i]=item
                    }
                    this.setState({
                        projectList,
                        isShow: true
                    })
                    console.log(projectList)
                }
            })
            .catch( error => {
                console.log('error:', error)
            })
    }

    insertsubProjectList(){
        this.$ajax
            .get(this.api('mine/subproject')(),{
                params: {
                    projectId: this.state.projectId
                }
            })
            .then( res => {
                if(res.status === 200){
                    let subprojects = res.data.subproject,
                        projectList = new Array()
                    for (let i=0; i<subprojects.length;i++){
                        var item = {
                            'isCheck': subprojects[i].projectId === this.state.cursubprojectId ? true  : false,
                            'key': i,
                            'subprojectId':subprojects[i].subprojectId,
                            'label': subprojects[i].subproject
                        }
                        projectList[i]=item
                    }
                    this.setState({
                        projectList,
                        isShow: true
                    })
                    console.log(res)
                }
            })
            .catch( error => {
                console.log('error:', error)
            })
    }

    onInsertProjectList(isProject){
        if (isProject){
            this.insertProjectList()
        }else {
            this.insertsubProjectList()
            console.log(isProject)
        }
    }

    onHandleCheck(list) {
        for(let i=0; i<list.length; i++){
            if(list[i].isCheck === true){

                this.setState({
                    projectSelect: list[i]
                })
            }
        }


    }

    onConfirm(){
        this.setState({
            isShow: false
        })

        let projectSelect = this.state.projectSelect,
            projectId = projectSelect.projectId
        if(this.state.isProject){
            this.setState({
                curprojectId:projectId
            })
            console.log('isProject',this.state.isProject)
            console.log(projectId)
            this.$emit('pages/TaskAdd/projectSelect', { projectSelect })
        }else {
            this.$emit('pages/TaskAdd/subprojectSelect', { projectSelect })
        }

        console.log(projectSelect)
    }

    onCancel(){
        this.setState({
            isShow: false
        })
    }
}

export default Store



