import RootStore from 'core/Store'
import * as api from 'core/api'

class Store extends RootStore {
    constructor(actions) {
        super()
        this.listenables = actions
        this.state = {
            isShow: false,
            timeList: [
                {
                    label:'近1个月',
                    isCheck: true,
                    name: 'month'
                },
                {
                    label:'近1个星期',
                    isCheck: false,
                    name: 'week'
                },
                {
                    label:'近一年',
                    isCheck: false,
                    name: 'year'
                }
            ],
            time: {}
        }
        this.init()
    }

    init() {
        this.$on('services/ModalTime/state', (key, { isShow }) => {
            this.setState({
                isShow
            })
        })
    }

    getCurrentItem() {
        this.state.timeList.forEach( (value) => {
            if(value.isCheck) {
                this.setState({
                    time: value
                })
                this.$emit('services/ModalTime/data', value)
            }
        })
    }

    onDidMount() {
        this.getCurrentItem()
    }

    onHandleCheck(item, index) {
        let timeList = this.state.timeList.map( (value, key) => {
            if(index === key){
                value.isCheck = true
            } else {
                value.isCheck = false
            }
            return value
        })
        this.setState({
            timeList
        })
    }

    onConfirm() {
        this.setState({
            isShow: false
        })
        this.getCurrentItem()
    }

    onRemoveEvents() {
        this.clearEvents()
    }
}

export default Store



