import React, { PropTypes } from 'react'
import Reflux from 'reflux'
import Store from './store'
import Actions from './actions'
import './styles'

import TeamHeader from 'services/TeamHeader'
import TeamFooter from 'services/TeamFooter'
import TeamTaskStatisticsDetail from 'services/TeamTaskStatisticsDetail'

class TeamTaskStatisticsPage extends React.Component {
    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store(this.actions)
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })

        this.tabs = [{
            link: '/team/task/people/statistics',
            name: 'statistics',
            label: '统计'
        },{
            link: '/team/task/people/analysis',
            name: 'analysis',
            label: '分析'
        }]
    }

    componentDidMount() {

    }

    componentWillUnmount() {
        // no del
        this.actions.removeEvents()
    }

    render() {
        return (
            <div className="root-page-teamtaskstatistics">
                <TeamTaskStatisticsDetail 
                    type={this.props.params.type}
                />
            </div>
        )
    }

}

export default TeamTaskStatisticsPage



