import RootStore from 'core/Store'

class Store extends RootStore {
    constructor(actions) {
        super()
        this.state = {
            test:'default'
        }
        this.listenables = actions

        this.init()
    }

    init() {

    }

    onRemoveEvents() {
        this.clearEvents()
    }

    onTestAction(value) {
        this.setState({
            test:`change ${value} value`
        })
    }
}

export default Store



