import RootStore from 'core/Store'
import * as api from 'core/api'

import Icon from 'widgets/Icon'

class Store extends RootStore {
    constructor(actions) {
        super()
        this.listenables = actions
        this.state = {
            params: {
                openId: this.cookie.get('openId'),
            },
            formData: {
                "project":"",
                "teamId":"",
                "subprojects":[
                    {
                        "subproject":"",
                        "teamStatus":"前期工作",
                        "projectMembers": [
                            {
                                "openId":"",
                                "roleType":""
                            }
                        ]
                    }
                ]
            },
            inputs: [
                {
                    key: 'subprojects_0',
                    placeholder: "子项目名称:",
                    leftIcon: 'add',
                    rightIcon: 'add-user' ,
                    status:'',
                    value: ''
                }
            ],
            list: [
                {
                    key: 'color-1',
                    label: '前期工作',
                    color: '#c740fc'
                },{
                    key: 'color-2',
                    label: '方案设计',
                    color: '#f85f57'
                },{
                    key: 'color-3',
                    label: '初步设计',
                    color: '#41d119'
                },{
                    key: 'color-4',
                    label: '施工图设计',
                    color: '#02b0f9'
                },{
                    key: 'color-5',
                    label: '施工配合',
                    color: '#176ceb'
                },{
                    key: 'color-6',
                    label: '暂缓',
                    color: '#71757b'
                },{
                    key: 'color-7',
                    label: '完成',
                    color: '#fcff04'
                }
            ],
            teamSelect: {
                teamId:-1
            },
            memberSelect: [],
            memberSelects:[],
            teamName: '',
            subprojects:[],
            isShowstage:false,
            dtCurrent: false,
            dtIndex:-1
        }

        this.init()
    }

    init() {
        console.log(this.state.formData)
        this.$on('pages/ProjectAdd/teamSelect',(key, teamSelect ) => {
            this.setState({
                teamSelect,
                teamName: teamSelect.label,
                formData: Object.assign({}, this.state.formData, {"teamId": teamSelect.teamId})
            })
            console.log(teamSelect)
        })

        this.$on('pages/ProjectAdd/memberSelects',(key,{ memberSelects}  ) => {
            let subprojects = this.state.formData.subprojects
            memberSelects.map((item, i) => {
                memberSelects[i].map((v, j) => {
                    /*subprojects[i].projectMembers[j].openId = '' || v.openId
                    subprojects[i].projectMembers[j].roleType = '' || v.roleType*/
                    subprojects[i].projectMembers[j] = {
                        openId: v.openId,
                        roleType: v.roleType
                    }
                })
            })

            this.setState({
                memberSelects
            })
        })

        this.$on('services/DropdownColor/item', (key, {item, index}) => {
            let formData = this.state.formData,
                isShowstage = !this.state.isShowstage,
                inputs = this.state.inputs,
                list = this.state.list,
                dtCurrent = !this.state.dtCurrent

            formData.subprojects[index].teamStatus = item.label
            list.map((v, i) => {
                if(item.label == v.label){
                    inputs[index].status = v.color
                }
            })

            this.setState({
                formData,
                isShowstage,
                inputs,
                dtCurrent
            })
            this.$emit('services/DropdownColor/state',{
                isShow:isShowstage
            })
            console.log('formData', formData)
        })
    }

    onAddSub() {
        let inputs = this.state.inputs,
            formData = this.state.formData,
            index = inputs.length - 1
        inputs[index]['leftIcon'] = 'color'
        inputs[index + 1] = {
            key: 'subprojects_' + (index + 1),
            placeholder: "子项目名称:",
            leftIcon: 'add',
            rightIcon: 'add-user' ,
            status:'',
            value: ''
        }
        formData['subprojects'][index + 1] = {
            "subproject":"",
            "teamStatus":"前期工作",
            "projectMembers": [
                {
                    "openId":"",
                    "roleType":""
                }
            ]
        }

        this.setState({
            inputs,
            formData
        })
    }

    onHandleChange(type, value, index) {
        console.log(type, value, index)
        let formData = this.state.formData
        switch (type) {
            case 'teamId': {
                formData[type] = value
            }
                break
            case 'project': {
                formData[type] = value
            }
                break
            case 'subprojects': {
                let inputs = this.state.inputs
                inputs[index]['value'] = value
                this.setState({
                    inputs
                })
                formData[type][index]['subproject'] = value
            }
                break
        }

        this.setState({
            formData
        })
        console.log(formData)
    }

    onHandleCancel() {
        this.$route.goBack()
    }

    onHandleSave() {
        console.log('this.state.formData:',this.state.formData)
        let formData = this.state.formData,
            subprojects = formData.subprojects,
            length = 0
        if(formData.project == '' || formData.teamId == ''){
            console.error('none')
            return false
        }else {
            if(subprojects.length = 1){
                length = 1
            }else {
                length = subprojects.length-1
            }
            for(let i=0; i<length; i++){
                if(subprojects[i].subproject == ''){
                    console.error('none')
                    return false
                }
            }
        }
        let params = Object.assign({}, formData , this.state.params)
        console.log(params)
        this.$ajax
            .post(this.api('project/add')(),
                params
            )
            .then( res => {
                console.log(Object.assign({}, formData , this.state.params))
                console.log(this.cookie.get('openId'))
                if(res.data.result === 'success'){
                    this.$route.push('/')
                }

            })
            .catch( error => {
                console.log('error:', error)
            })
    }

    onRemoveEvents() {
        this.clearEvents()
    }

    onToggle(event) {
        event.stopPropagation()
        let isShow = true
        this.setState({
            isShow
        })
        this.$emit('services/TeamSelect/state',{
            isShow
        })
    }

    onMemberToggle(event, index, teamId) {
        event.stopPropagation()
        let isShow = true,
            memberSelects = this.state.memberSelects
        if(this.state.teamSelect.teamId !== -1){
            this.setState({
                isShow
            })
            this.$emit('services/MemberSelect/memberSelects',{
                isShow,
                index,
                memberSelects,
                teamId
            })
        }

    }

    onStageToggle(event, index) {
        let {left, top, width, height} = event.target.getBoundingClientRect(),
            style = {
            left: left + "px",
            top: (top + height) +"px"
        },
            isShowstage = !this.state.isShowstage,
            dtCurrent = !this.state.dtCurrent

        this.setState({
            isShowstage,
            dtCurrent,
            dtIndex:index
        })
        this.$emit('services/DropdownColor/state',{
            isShow:isShowstage,
            style,
            index
        })
    }
}

export default Store



