import React, { PropTypes } from 'react'
import Store from './store'
import Actions from './actions'
import './styles'

import TeamTaskProjectAnalysisDetail from 'services/TeamTaskProjectAnalysisDetail'

class TeamTaskProjectAnalysisPage extends React.Component {
    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store(this.actions)
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })
    }

    componentDidMount() {

    }

    componentWillUnmount() {
        // no del
        this.actions.removeEvents()
    }

    render() {
        return (
            <div className="root-page-teamtaskprojectanalysis">
                <TeamTaskProjectAnalysisDetail />
            </div>
        )
    }

}

export default TeamTaskProjectAnalysisPage



