import { createActions } from 'reflux'

class Actions {
    constructor() {
        return createActions([
            'removeEvents',
            'handleCancel',
            'handleSave',
            'insertInfo',
            'stageToggle',
            'addSub',
            'memberToggle',
            'changeProject',
            'changeSubpro'
        ])
    }
}

export default Actions



