import React, { PropTypes } from 'react'
import Reflux from 'reflux'
import classnames from 'classnames'
import Store from './store'
import Actions from './actions'
import './styles'

import Checkbox from 'widgets/Checkbox'

class MaskUserAssignPage extends React.Component {
    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store(this.actions)
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })
    }

    componentDidMount() {

    }

    componentWillUnmount() {
        // no del
        this.actions.removeEvents()
    }

    renderTitle() {
        return (
            <ul className="maskuserassign-title">
                <li
                    className={ classnames({'current': this.state.current === 'son' })}
                    onClick={ () => { this.actions.handleSelect('son') } }>
                    <span>子项目成员</span>
                </li>
                <li
                    className={ classnames({'current': this.state.current === 'all' })}
                    onClick={ () => { this.actions.handleSelect('all') } }>
                    <span>全部成员</span>
                </li>
            </ul>
        )
    }

    renderContent() {
        return (
            <div className="maskuserassign-content">
                <div className="scroll">
                {
                    this.state.userList &&
                    this.state.userList.map((item, index) => {
                        return (
                            <dl key={ index } className={ item.name }>
                                <dt>{ item.title }</dt>
                                <dd>
                                    <Checkbox
                                        list={ item.users }
                                        onCheck={ (list)=> {console.log(list)}}
                                    />
                                </dd>
                            </dl>
                        )
                    })
                }
                </div>
            </div>
        )
    }

    render() {
        const classNames = classnames("root-page-maskuserassign",{
            "display": this.state.isDisplay,
            "show": this.state.isShow
        })
        return (
            <div className={ classNames }>
                <div className="maskuserassign-mask" onClick={ () => { this.actions.close() } }></div>
                <div className="maskuserassign-wrapper">
                    { this.renderTitle() }
                    { this.renderContent() }
                </div>
            </div>
        )
    }

}

export default MaskUserAssignPage



