import React, { PropTypes } from 'react'
import Reflux from 'reflux'
import Store from './store'
import Actions from './actions'
import utils from 'core/utils'
import './styles'

import DatePicker from 'react-mobile-datepicker'
import Modal from 'widgets/Modal'

class ModalDatePage extends React.Component {
    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store(this.actions)
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })
    }

    componentDidMount() {

    }

    componentWillUnmount() {
        // no del
        this.actions.removeEvents()
    }

    render() {
        return (
            <div className="root-page-modaldate">
                <DatePicker
                    dateFormat={ ['YYYY', 'MM', 'DD'] }
                    value={ !utils.isEmptyObject(this.state.time) ? this.state.time : new Date() }
                    isOpen={ this.state.isOpen }
                    onSelect={ (time) => { this.actions.handleSelect(time) } }
                    onCancel={ () => { this.actions.handleCancel() } }
                />
            </div>
        )
    }

}

export default ModalDatePage



