import RootStore from 'core/Store'
import * as api from 'core/api'

class Store extends RootStore {
    constructor(actions) {
        super();
        this.state = {
            isShow: false,
            moreId:0,
            task:{},
            style: {
                left: '0px',
                top: '0px',
            }
        }
        this.listenables = actions

        this.initListen()
    }

    initListen() {
        this.$on('services/DropdownMore/state', (key, data) => {
            console.log("data", data)
            this.setState({
                isShow: data.isShow,
                moreId : data.moreId,
                style: data.style,
                task: data.task,
                subprojectId: data.task.subprojectId
            })
        })
    }


    onComplete(event) {
        event.preventDefault()
        event.stopPropagation()
        console.log('onComplete')
        this.$emit('services/ModalComplete/state',{
            isOpen: true,
            moreId: this.state.moreId,
            subprojectId: this.state.subprojectId
        })
    }

    onAssign(event) {
        //console.log("this.state.subprojectId", this.state.subprojectId)
        event.preventDefault()
        event.stopPropagation()
        this.$emit('services/ModalMember/state',{
            isShow: true,
            moreId: this.state.moreId,
            subprojectId: this.state.subprojectId
        })
    }

    onDelay(event) {
        event.preventDefault()
        event.stopPropagation()
        this.$emit('services/ModalDate/state',{
            isOpen: true,
            moreId: this.state.moreId,
            subprojectId: this.state.subprojectId
        })
    }

    onMessage(event) {
        event.preventDefault()
        event.stopPropagation()
        this.cookie.set('task',JSON.stringify(this.state.task))
        this.$route.push('/project/Task/reply')
    }

    onRemoveEvents() {
        this.clearEvents()
    }
}

export default Store



