import React, { PropTypes } from 'react'
import { Link } from 'react-router'
import Reflux from 'reflux'
import Store from './store'
import Actions from './actions'
import './styles'

import ItemDot from 'widgets/ItemDot'
import PullUp from 'widgets/PullUp'
import Button from 'widgets/Button'
import ItemButton from 'widgets/ItemButton'
import DropdownMore from 'services/DropdownMore'
import RoundSearch from 'services/RoundSearch'
import ModalDate from 'services/ModalDate'
import ModalComplete from 'services/ModalComplete'
import ModalMember from 'services/ModalMember'

class TeamTaskStatisticsDetailPage extends React.Component {
    static propTypes = {
        type: PropTypes.string
    }

    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store(this.actions)
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })

        this.list= [
            {
                key: 1,
                label: '测试啊及',
                isCheck: true
            },{
                key: 2,
                label: '测试aa啊及',
                isCheck: true
            },{
                key: 3,
                label: '测试==啊及',
                isCheck: true
            },{
                key: 4,
                label: '测试比啊',
                isCheck: true
            }
        ]

        this.listData = [
            {
                style: "grey",
                title: "界面风格确定",
                name: "Wechat|PM",
                type: "UI设计",
                tag: "近期出图回文",
                message: "今天天气不错......",
                date: "2016年12月20日"
            },
            {
                style: "red",
                title: "界面风格确定",
                name: "Wechat|PM",
                type: "UI设计",
                tag: "近期出图回文",
                message: "今天天气不错......",
                date: "2016年12月20日"
            },
            {
                style: "blue",
                title: "界面风格确定",
                name: "Wechat|PM",
                type: "UI设计",
                tag: "近期出图回文",
                message: "今天天气不错......",
                date: "2016年12月20日"
            }
        ]

        this.tags = {
            a:"进度安排",
            b:"出图提醒",
            c:"会议通知",
            d:"外部条件",
            e:"内部协调"
        }

        this.colos = {
            a:"red",
            b:"blue",
            c:"violet",
            d:"yellow",
            e:"purple",
            f:"green",
            h:"grey"
        }
    }

    componentDidMount() {
        this.actions.didMount()
    }

    componentWillUnmount() {
        // no del
        this.actions.removeEvents()
    }

    renderItem = (item, index) => {
        return (
            <ItemDot
                key={ index }
                style={  item.isOverTime === 1 ? "red" : "blue" }
                title={ item.scheduleName}
                name={ item.projectName }
                type={ item.subprojectName }
                tag={ this.tags[item.tag] }
                message={ item.message }
                date={item.endDate}
                btnMore={
                    <Button
                        icon="more"
                        type="icon"
                        onClick={ (event)=> { this.actions.moreToggle(event, item) }}
                    />
                }
            />
        )
    }

    render() {
        return (
            <div className="root-page-teamtaskstatisticsdetail">
                <PullUp
                    api={ this.state.api }
                    params={ this.state.params }
                    dataField="data"
                    renderItem={ this.renderItem }
                    onScroll={ this.actions.handleScroll }
                >
                    <ItemButton label="创建新任务" onDetail={ () => { this.actions.createTask()  } } />
                    <div className="search-box">
                        <RoundSearch
                            isCheckBox={true}
                            list={this.list}
                        />
                    </div>
                </PullUp>
                <DropdownMore />
                <ModalDate />
                <ModalComplete />
                <ModalMember />
            </div>
        )
    }

}

export default TeamTaskStatisticsDetailPage



