import React, { PropTypes } from 'react'
import Reflux from 'reflux'
import Store from './store'
import Actions from './actions'
import './styles'

// test Input
import Input from '../index'
import Icon from 'widgets/Icon'

class Demo extends React.Component {
    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store(this.actions)
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })
    }

    componentDidMount() {

    }

    componentWillUnmount() {
        this.actions.removeEvents()
    }

    render() {
        return (
            <div className="root-widget-input__demo__">
                <Input
                    leftIcon={ <Icon icon="plan" /> }
                    rightIcon={ <Icon icon="add-user" />}
                    value={ this.state.value }
                    onChange={ (e, value) => { this.actions.handleChange(e, value) } }
                />
                <Input
                    leftIcon={ <Icon icon="plan" /> }
                    rightIcon={ <Icon icon="add-user" />}
                    value={ this.state.value }
                    onChange={ (e, value) => { this.actions.handleChange(e, value) } }
                />
                <Input
                    leftIcon={ <Icon icon="plan" /> }
                    rightIcon={ <Icon icon="add-user" />}
                    value={ this.state.value }
                    onChange={ (e, value) => { this.actions.handleChange(e, value) } }
                />
                <Input
                    rightIcon={ <Icon icon="add-user" />}
                    value={ this.state.value }
                    onChange={ (e, value) => { this.actions.handleChange(e, value) } }
                />
                <Input
                    leftIcon={ <Icon icon="plan" /> }
                    value={ this.state.value }
                    onChange={ (e, value) => { this.actions.handleChange(e, value) } }
                />
                <Input
                    value={ this.state.value }
                    onChange={ (e, value) => { this.actions.handleChange(e, value) } }
                />
            </div>
        )
    }
}

export default Demo



