import { createActions } from 'reflux'

class Actions {
    constructor() {
        return createActions([
            'removeEvents',
            'testAction' ,
            'handleCheck',
            'insertTeamList',
            'confirm',
            'cancel'
        ])
    }
}

export default Actions



