import React, { PropTypes } from 'react'
import Reflux from 'reflux'
import Store from './store'
import Actions from './actions'
import './styles'

// test ItemThumb
import ItemThumb from '../index'

class Demo extends React.Component {
    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store(this.actions)
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })
    }

    componentDidMount() {

    }

    componentWillUnmount() {
        this.actions.removeEvents()
    }

    handleEdit = () => {

    }

    render() {
        return (
            <div className="root-widget-itemthumb__demo__">
                <ItemThumb
                    onEdit={ this.handleEdit }
                    thumb={ <div style={{'height':'100%','backgroundColor': 'red'}}></div> }
                    title="WECHATPM  UI设计"
                    desc="负责人：汪主任，当前阶段：前期工作"
                />
                <ItemThumb
                    thumb={ <div style={{'height':'100%','backgroundColor': 'red'}}></div> }
                    title="WECHATPM  UI设计"
                    desc="负责人：汪主任，当前阶段：前期工作"
                />
            </div>
        )
    }
}

export default Demo



