import RootStore from 'core/Store'

class Store extends RootStore {
    constructor(actions) {
        super()
        this.listenables = actions
        this.state = {
            isShow: false
        }
        this.init()
    }

    init() {

    }

    onTestAction(value) {
        this.setState({
            test:`change ${value} value`
        })
    }

    onRemoveEvents() {
        this.clearEvents()
    }

    onToggle(event) {
        event.stopPropagation()
        let isShow = true
        this.setState({
            isShow
        })
        this.$emit('services/MemberSelect/state',{
            isShow
        })
        console.log(this.state.isShow)
    }

}

export default Store



