import React, { PropTypes } from 'react'
import Reflux from 'reflux'
import Store from './store'
import Actions from './actions'
import './styles'

import TeamHeader from 'services/TeamHeader'
import TeamFooter from 'services/TeamFooter'
import TeamTaskAnalysisDetail from 'services/TeamTaskAnalysisDetail'

class TeamTaskAnalysisPage extends React.Component {
    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store(this.actions)
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })
    }

    componentDidMount() {

    }

    componentWillUnmount() {
        // no del
        this.actions.removeEvents()
    }

    render() {
        return (
            <div className="root-page-teamtaskanalysis">
                <TeamTaskAnalysisDetail 
                    type={this.props.params.type}
                />
            </div>
        )
    }

}

export default TeamTaskAnalysisPage



