import React, { PropTypes } from 'react'
import Reflux from 'reflux'
import Store from './store'
import Actions from './actions'
import './styles'

import Dropdown from 'widgets/Dropdown'
import Checkbox from 'widgets/Checkbox'

class DropdownStepsPage extends React.Component {
    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store(this.actions)
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })
    }

    componentDidMount() {

    }

    componentWillUnmount() {
        // no del
        this.actions.removeEvents()
    }

    render() {
        return (
            <Dropdown
                className="root-page-dropdownsteps"
                isShow={ this.state.isShow }
                style={ this.state.style }>
                <Checkbox
                    list={ this.state.list }
                    onCheck={ (list)=> { this.actions.handleCheck(list)}}
                    one={ true }
                />
            </Dropdown>
        )
    }

}

export default DropdownStepsPage



