/**
 * PullUp
 */

import React, { PropTypes } from 'react'
import classnames from 'classnames'
import axios from 'axios'
import Im from 'immutable'

class PullUp extends React.Component {
    static propTypes = {
        className: PropTypes.string,
        height: PropTypes.string,
        triggerHeight: PropTypes.number,
        children: PropTypes.any,
        onScroll: PropTypes.func,
        renderItem: PropTypes.func,
        response: PropTypes.func,
        //flag: PropTypes.bool,
        //loadingStatus: PropTypes.string,
        api: PropTypes.string,
        params: PropTypes.object,
        dataField: PropTypes.string
    }

    static defaultProps = {
        height:'100%',
        triggerHeight: 3,
        //flag: true,
        //loadingStatus: 'more',
        params: {},
        api: ''
    }

    constructor(props) {
        super(props)
        this.totalSize = 0
        this.state = {
            height: props.height,
            triggerHeight: props.triggerHeight,
            flag: true,
            loadingStatus: 'more',
            data:[],
            params: props.params,
            api: props.api,
            dataField: props.dataField,
            loadingText: {
                more: '上拉加载更多',
                loading: '加载中...',
                none: '没有了'
            }
        }
    }

    refresh = ({ flag, loadingStatus }) => {
        this.setState({
            flag: flag ,
            loadingStatus: loadingStatus
        })
    }

    handleScroll = (e) => {
        this.props.onScroll &&  this.props.onScroll(e)
        let { clientHeight, scrollHeight, scrollTop } = this.refs.scroll

        if (this.state.flag && (scrollHeight - clientHeight) < (scrollTop + this.state.triggerHeight)){
            console.log('srcoll')

            if(this.state.params.currentPageNumber > 1){
                this.getListData();
            }
            //if(this.state.params.currentPageNumber > 1){
            //    this.getListData(() => {
            //        if(Math.ceil(this.totalSize / this.state.params.pageSize) > this.state.params.currentPageNumber){
            //            console.log("8***", this.state.params.currentPageNumber + 1)
            //            this.setState({
            //                flag: true,
            //                loadingStatus: 'more',
            //                params: Object.assign({}, this.state.params, {
            //                    currentPageNumber: this.state.params.currentPageNumber + 1
            //                })
            //            })
            //            //this.state.params.currentPageNumber ++
            //        } else {
            //            this.setState({
            //                flag: false,
            //                loadingStatus: 'none'
            //            })
            //        }
            //    })
            //}






            //this.props.onInfiniteLoad && this.props.onInfiniteLoad(this.refresh)
        }
    }

    getListData() {
        this.setState({
            flag: false,
            loadingStatus: 'loading'
        })

        axios.get(this.state.api, {
                params: this.state.params
            })
            .then( res => {
                if(res.status === 200){
                    var data = []
                    let projects =  res.data[this.state.dataField]
                    this.totalSize = res.data.totalSize
                    console.log("===", projects)
                    if(this.state.params.currentPageNumber === 1){
                        data = projects
                    } else {
                        let oldData = this.state.data
                        data = oldData.concat(projects)
                    }
                    this.props.response && this.props.response(res.data)
                    this.setState({
                        data
                    })
                    if(Math.ceil(this.totalSize / this.state.params.pageSize) > this.state.params.currentPageNumber){
                       // this.state.params.currentPageNumber ++
                        this.setState({
                            flag: true,
                            loadingStatus: 'more',
                            params: Object.assign({}, this.state.params, {
                                currentPageNumber: this.state.params.currentPageNumber + 1
                            })
                        })
                    } else {
                        this.setState({
                            flag: false,
                            loadingStatus: 'none'
                        })
                    }

                }
                console.log('res:', res)
            })
            .catch( error => {
                console.log('error:', error)
            })
    }


    componentDidMount() {
        this.getListData()
        //this.props.onInfiniteLoad && this.props.onInfiniteLoad()
        this.refs.scroll.addEventListener('scroll', this.handleScroll, false)
    }

    componentWillReceiveProps({ params, api }) {
        //console.log('componentWillReceiveProps:',  params, api)
        this.setState({
            params,
            api
        },() => {
            this.getListData()
        })
    }

    //shouldComponentUpdate(nextProps,  nextState){
    //    console.log('================shouldComponentUpdate=============',!Im.is(Im.fromJS(nextState.data), Im.fromJS(this.state.data)) || !Im.is(Im.fromJS(nextState.params), Im.fromJS(this.state.params)))
    //   return !Im.is(Im.fromJS(nextState.data), Im.fromJS(this.state.data)) || !Im.is(Im.fromJS(nextProps.params), Im.fromJS(this.props.params))
    //}

    componentWillUnmount() {
        this.refs.scroll.removeEventListener('scroll', this.handleScroll, true)
    }

    render() {
        const classNames = classnames(
                'root-widget-pullup',
                'scroll',
                this.props.className
            )
        const style= {
            "height": this.state.height
        }

        return (
            <main className={ classNames } style={ style }
                  ref="scroll" >
                {
                    this.props.children &&
                    this.props.children
                }
                {
                    this.state.data &&
                    this.state.data.length > 0 ?
                    this.state.data.map((item, index) => {
                        return this.props.renderItem &&
                            this.props.renderItem(item, index)
                    }) : null
                }
                <div className="loading">
                    <span>{ this.state.loadingText[this.state.loadingStatus]}</span>
                </div>
            </main>
        )
    }
}

export default PullUp;



