import RootStore from 'core/Store'

class Store extends RootStore {
    constructor(actions) {
        super()
        this.state = {
            test:'default',
            isShow: false
        }
        this.listenables = actions
    }

    onRemoveEvents() {
        this.clearEvents()
    }

    onTestAction(value) {
        this.setState({
            test:`change ${value} value`
        })
    }

    onClose() {
        this.setState({
            isShow: !this.state.isShow
        })
    }
}

export default Store



