const deepClone = ( obj ) => {
    var key, i, result;

    // if it's a primitive, do nothing
    if ( typeof obj !== 'object' ) {
        return obj;
    }

    // if it's an array, iterate
    if ( Object.prototype.toString.call( obj ) === '[object Array]' ) {
        result = [];

        i = obj.length;
        while ( i-- ) {
            if ( obj.hasOwnProperty( i ) ) {
                if ( typeof obj[i] === 'object' ) {
                    result[i] = deepClone( obj[i] );
                }

                else {
                    result[i] = obj[i];
                }
            }
        }
    }

    // if it's an object, enumerate
    else {
        result = {};

        for ( key in obj ) {
            if ( obj.hasOwnProperty( key ) ) {
                if ( typeof obj[ key ] === 'object' ) {
                    result[ key ] = deepClone( obj[ key ] );
                }

                else {
                    result[ key ] = obj[ key ];
                }
            }
        }
    }

    return result;
}

const isEmptyObject = (e) => {
    var t;
    for (t in e)
        return !1;
    return !0
}
/**
 * 对Date的扩展，将 Date 转化为指定格式的String
 * 月(M)、日(d)、小时(h)、分(m)、秒(s)、季度(q) 可以用 1-2 个占位符，
 * 年(y)可以用 1-4 个占位符，毫秒(S)只能用 1 个占位符(是 1-3 位的数字)
 * 例子：
 * (new Date()).Format("yyyy-MM-dd hh:mm:ss.S") ==> 2006-07-02 08:09:04.423
 * (new Date()).Format("yyyy-M-d h:m:s.S")      ==> 2006-7-2 8:9:4.18
 * @param fmt
 * @returns {*}
 */
const format = function (date, fmt) { //author: meizz
    let myDate = new Date(date)
    var o = {
        "M+": myDate.getMonth() + 1, //月份
        "d+": myDate.getDate(), //日
        "h+": myDate.getHours(), //小时
        "m+": myDate.getMinutes(), //分
        "s+": myDate.getSeconds(), //秒
        "q+": Math.floor((myDate.getMonth() + 3) / 3), //季度
        "S": myDate.getMilliseconds() //毫秒
    };
    if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (myDate.getFullYear() + "").substr(4 - RegExp.$1.length));
    for (var k in o)
        if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
    return fmt;
}

// arr.sort(compare("age"))
// {name: "zlw", age: "24"}
const compare = (prop) => {
    return function (obj1, obj2) {
        var val1 = obj1[prop];
        var val2 = obj2[prop];
        if (val1 > val2) {
            return -1;
        } else if (val1 < val2) {
            return 1;
        } else {
            return 0;
        }
    }
}

const  getOneMonth = type => {
    let myDate = new Date()
    let year = myDate.getFullYear(),
        month = myDate.getMonth() + 1,
        day = myDate.getDate()

    day = day < 10 ? '0' + day : day

    if(type ===  'startTime'){
        month = myDate.getMonth()
        if(month == 0) {
            year = year - 1
            month = 12
        } else {
            month = month
        }
    }
    month = month < 10 ? '0' + month : month
    return `${year}-${month}-${day}`
}


const  getOneYear = type => {
    let myDate = new Date()
    let year = myDate.getFullYear(),
        month = myDate.getMonth() + 1,
        day = myDate.getDate()

    day = day < 10 ? '0' + day : day

    if(type ===  'startTime'){
        year = myDate.getFullYear() - 1
    }
    month = month < 10 ? '0' + month : month
    return `${year}-${month}-${day}`
}

const  getOneWeek = type => {
    let myDate = new Date()
    let year = myDate.getFullYear(),
        month = myDate.getMonth() + 1,
        day = myDate.getDate()

    if(type ===  'startTime'){
        day = myDate.getDate() - 7

        if(day == 0) {
            month = month - 1
            day = new Date(year,month,0).getDate();
        } else if( day < 0) {
            month = month - 1
            let lastDay = new Date(year,month,0).getDate();
            day = lastDay - day
        }

        if(month == 0) {
            year = year - 1
            month = 12
        } else {
            month = month
        }
    }

    day = day < 10 ? '0' + day : day
    month = month < 10 ? '0' + month : month
    return `${year}-${month}-${day}`
}

export default {
    deepClone,
    isEmptyObject,
    format,
    compare,
    getOneMonth,
    getOneYear,
    getOneWeek
}


