import React, { PropTypes } from 'react'
import Reflux from 'reflux'
import Store from './store'
import Actions from './actions'
import './styles'

import MineTabHeader from 'services/MineTabHeader'
import Mainer from 'layouts/Mainer'

class MinePage extends React.Component {
    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store(this.actions)
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })
    }

    componentDidMount() {

    }

    componentWillUnmount() {
        // no del
        this.actions.removeEvents()
    }

    render() {
        return (
            <div className="root-page-mine">
                <MineTabHeader />
                <Mainer paddingTop paddingBottom>
                    { this.props.children }
                </Mainer>
            </div>
        )
    }
}

export default MinePage



