/**
 * FooterTab
 */

import React, { PropTypes } from 'react'
import classnames from 'classnames'

import { Link } from 'react-router'
import Icon from 'widgets/Icon'

class FooterTab extends React.Component {
    static propTypes = {
        className: PropTypes.string,
        children: PropTypes.any,
        onHandleClick: PropTypes.func,
        tabs: PropTypes.array
    }

    static defaultProps = {
        tabs: []
    }

    constructor(props) {
        super()
        this.state = {
            test: props.test
        }
    }

    componentDidMount() {

    }

    componentWillUnmount() {

    }

    render() {
        const { className, tabs } = this.props
        return (
            <footer className={ classnames('root-widget-footertab',className) } >
                <ul>
                    {
                        tabs.map( (item, index)=> {
                            return (
                                <li key={ index }
                                    onClick={ () => this.props.onHandleClick(item) }
                                    className={ classnames({'current': item.name === this.props.current})}>
                                    <Link to={ item.link }>
                                        <Icon icon={ item.icon } />
                                        <span>{ item.label }</span>
                                    </Link>
                                </li>
                            );
                        })
                    }
                </ul>
            </footer>
        )
    }
}

export default FooterTab;



