import RootStore from 'core/Store'
import * as api from 'core/api'

class Store extends RootStore {
    constructor(actions) {
        super();
        this.state = {
            params: {
                openId: this.cookie.get('openId'),
                currentPageNumber:1,
                pageSIze:10
            },
            api: this.api('mine/undone')()
        }
        this.listenables = actions

        this.init()
    }

    init() {

    }

    onTestAction(value) {
        this.setState({
            test: value
        })
    }

    onRemoveEvents() {
        this.clearEvents()
    }

}

export default Store



