import React, { PropTypes } from 'react'
import Reflux from 'reflux'
import Store from './store'
import Actions from './actions'
import './styles'

import TeamProjectContent from 'services/TeamProjectContent'
import DropdownSteps from 'services/DropdownSteps'

class TeamTaskProjectPage extends React.Component {
    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store(this.actions)
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })
    }

    componentDidMount() {
        this.actions.didMount()
    }

    componentWillUnmount() {
        // no del
        this.actions.removeEvents()
    }

    render() {
        return (
            <div className="root-page-teamtaskproject">
                <TeamProjectContent />
                <DropdownSteps />
            </div>
        )
    }

}

export default TeamTaskProjectPage



