import RootStore from 'core/Store'

class Store extends RootStore {
    constructor(actions) {
        super()
        this.state = {
            isShow: false
        }
        this.listenables = actions

        this.initListen()
    }

    initListen() {
        this.$on('services/DropdownMore/item', (key, { item })=> {
            console.log('item',item)
        })
    }

    onToggle(event) {
        let {left, top, width, height} = event.target.getBoundingClientRect()

        let style = {
            left: (left + width)+ "px",
            top: (top + height) +"px",
            transform: "translateX(-100%)"
        }

        let isShow = !this.state.isShow
        this.setState({
            isShow
        })
        this.$emit('services/DropdownMore/state', {
            isShow,
            style
        })
    }

    onRemoveEvents() {
        this.clearEvents()
    }

}

export default Store



