import RootStore from 'core/Store'
import * as api from 'core/api'

class Store extends RootStore {
    constructor(actions) {
        super();
        this.state = {
            isShow: false,
            style:{},
            list: [
                {
                    key: 'filter-1',
                    label: '合同备案进度',
                    isCheck: true
                },{
                    key: 'filter-2',
                    label: '付款进度',
                    isCheck: false
                },{
                    key: 'filter-3',
                    label: '分配进度',
                    isCheck: false
                },{
                    key: 'filter-4',
                    label: '计划进度',
                    isCheck: false
                },{
                    key: 'filter-5',
                    label: '近期出图回文',
                    isCheck: false
                },{
                    key: 'filter-6',
                    label: '近期会议组织',
                    isCheck: false
                },{
                    key: 'filter-7',
                    label: '外部设计条件',
                    isCheck: false
                },{
                    key: 'filter-8',
                    label: '内部落实',
                    isCheck: false
                },{
                    key: 'filter-9',
                    label: '总结交流',
                    isCheck: false
                }
            ]
        }
        this.listenables = actions

        this.initListen()
    }

    onRemoveEvents() {
        this.clearEvents()
    }

    initListen() {
        this.$on('services/DropdownFilter/state', (key, { isShow, style }) => {
            console.log('isShow',isShow)
            this.setState({
                isShow,
                style
            })
        })
    }

    onHandleCheck(list) {
        this.$emit('services/DropdownFilter/selectList', {
            selectList: list
        })
    }
}

export default Store



