import React, { PropTypes } from 'react'
import Reflux from 'reflux'
import Store from './store'
import Actions from './actions'
import './styles'
import utils from 'core/utils'

import ItemButton from 'widgets/ItemButton'
import ItemNumber from 'widgets/ItemNumber'

class NoteListPage extends React.Component {
    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store(this.actions)
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })
    }

    componentDidMount() {
        this.actions.didMount()
    }

    componentWillUnmount() {
        // no del
        this.actions.removeEvents()
    }

    render() {
        return (
            <div className="root-page-notelist">
                <ItemButton label="创建新随手记" onDetail={ () => { this.actions.add()  } } />
                {
                    this.state.listData &&
                    this.state.listData
                        .sort(utils.compare("number"))
                        .map( (item, index) => {
                        return (
                            <ItemNumber
                                onAdd={ ()=>{ this.actions.handleAdd(item.recordId) }  }
                                onDel={ () => { this.actions.handleDel(item.recordId, index) } }
                                onEdit={ () => { this.actions.handleEdit(item.recordId, index) } }
                                onChange={ (e) => { this.actions.handleChange(e, item.recordId) } }
                                key={ index }
                                title={ item.content }
                                number={ item.number }
                                isCurrent={ item.isCurrent }
                            />
                        )
                    })
                }
            </div>
        )
    }

}

export default NoteListPage



