import axios from 'axios'
import Cookie from 'js-cookie'

class Ajax {
    constructor() {
        console.log('init ajax')
        this.inintDefaults()
        this.interceptors()
    }

    inintDefaults() {
        //axios.defaults.baseURL = 'http://120.25.240.91/wechatEE';
        axios.defaults.baseURL = 'http://www.chenlinallen.com';
        // axios.defaults.headers.common['Authorization'] = AUTH_TOKEN;
        // axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';
    }

    interceptors() {
        axios.interceptors.request.use((config) => {
            //全局追加openid
            //Cookie.set('openId','dev-chenlin') //chenbilin dev-chenlin
            config.params = (
                Object.assign((config.params ? config.params : {}),{
                    "SESSION": Cookie.get('openId')
                })
            )
            return config
        },(error) =>{
            return Promise.reject(error)
        })
        // Add a request interceptor
        //axios.interceptors.request.use(function (config) {
        //    // Do something before request is sent
        //    return config;
        //}, function (error) {
        //    // Do something with request error
        //    return Promise.reject(error);
        //});

        // Add a response interceptor
        axios.interceptors.response.use(function (response) {
            console.log('interceptors:',response)
            // Do something with response data
            return response;
        }, function (error) {
            // Do something with response error
            return Promise.reject(error);
        });
    }

}

export default Ajax


