import React, { PropTypes } from 'react'
import Reflux from 'reflux'
import Store from './store'
import Actions from './actions'
import './styles'

import Input from 'widgets/Input'
import Icon from 'widgets/Icon'
import ButtonGroup from 'widgets/ButtonGroup'
import TeamSelect from 'services/TeamSelect'
import MemberSelect from 'services/MemberSelect'
import DropdownColor from 'services/DropdownColor'

class ProjectAddPage extends React.Component {
    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store(this.actions)
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })
    }

    componentDidMount() {

    }

    componentWillUnmount() {
        // no del
        this.actions.removeEvents()
    }

    renderIconColor(index, status) {
        console.log(status)
        return (
            <div>
                <span style={{backgroundColor:status}} className="icon-color" onClick={(event)=> { this.actions.stageToggle(event, index) }}></span>
                <DropdownColor />
            </div>
        )
    }

    renderIconAdd() {
        return (
            <span className="add"
                onClick={ () => { this.actions.addSub() } }
            >
                <Icon icon="add" />
            </span>
        )
    }
    render() {
        return (
            <div className="root-page-projectadd scroll">
                <div className="project-add-list">
                    <Input
                        placeholder="选择所在团队:"
                        leftIcon={ <Icon icon="plan" /> }
                        rightIcon={ <Icon icon="show" onClick={ (event)=> { this.actions.toggle(event) }}/>}
                        value={ this.state.teamName }
                        onChange={ (e, value) => { this.actions.handleChange('teamId', value) } }
                        disabled
                    />
                    <TeamSelect />
                    <Input
                        placeholder="项目名称:"
                        leftIcon={ <Icon icon="project" /> }
                        value={ this.state.formData.project }
                        onChange={ (e, value) => { this.actions.handleChange('project' ,value) } }
                    />

                    {
                        this.state.inputs.map((item, index) => {
                            return (
                                <Input
                                    key={ index }
                                    placeholder=" 子项目名称:"
                                    leftIcon={ item.leftIcon === 'add' ? this.renderIconAdd(): this.renderIconColor(index, item.status) }
                                    rightIcon={ <Icon icon={ item.rightIcon } onClick={ (event)=> { this.actions.memberToggle(event, index, this.state.teamSelect.teamId) }}/>}
                                    value={ item.value }
                                    isCurrent = { this.state.dtCurrent && this.state.dtIndex == index }
                                    onChange={ (e, value) => { this.actions.handleChange('subprojects',value, index) } }
                                />
                            )
                        })
                    }
                    <MemberSelect />
                </div>
                <div className="project-add-bottom">
                    <ButtonGroup
                        onConfirm={ ()=> { this.actions.handleSave() }}
                        onCancel={ ()=> { this.actions.handleCancel() }}
                    />
                </div>
            </div>
        )
    }

}

export default ProjectAddPage



