import RootStore from 'core/Store'
import * as api from 'core/api'

class Store extends RootStore {
    constructor(actions) {
        super()
        this.listenables = actions
        this.state = {
            isShow: false,
            moreId: 0,
            subprojectId: 0
        }
        this.init()
    }

    init() {

    }

    onDidMount() {
        this.$on('services/ModalComplete/state',(key, data) => {
             console.log('services/ModalComplete/state', data)
            this.setState({
                isShow: data.isOpen,
                moreId: data.moreId,
                subprojectId: data.subprojectId
            })

        })
    }

    //project/task/complete
    settingComplete({taskId, subprojectId}) {
        this.$ajax.post(this.api('project/task/complete')(),{
            id: taskId, //（必须传）
            subprojectId,
            openId: this.cookie.get("openId"), //(转移该任务的人)，
            status:"c" //"a/b/c 任务状态(a.未开始，b.进行中，c.已完成，d.超期，e.无效，f.延期)"（必须传）
        }).then( res => {
            this.setState({
                isShow: false
            })
            if(res.data.result === "success"){

            }
            //console.log("settingComplete", res)
        }).catch( error => {
            this.setState({
                isShow: false
            })
        })
    }

    onHandleConfirm() {
        let moreId = this.state.moreId,
            subprojectId = this.state.subprojectId

        this.settingComplete({
            taskId: moreId,
            subprojectId
        })
        //console.log('moreId', moreId)
    }

    onRemoveEvents() {
        this.clearEvents()
    }
}

export default Store



