import React, { PropTypes } from 'react'
import Reflux from 'reflux'
import Store from './store'
import Actions from './actions'
import './styles'

import ProjectTaskList from 'services/ProjectTaskList'

class ProjectTaskPage extends React.Component {
    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store(this.actions)
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })
    }

    componentDidMount() {
        this.actions.mount(this.props)
    }

    componentWillUnmount() {
        // no del
        this.actions.removeEvents()
    }

    render() {
        return (
            <div className="root-page-projecttask mainer fixed-top fixed-bottom">
                <ProjectTaskList />
            </div>
        )
    }

}

export default ProjectTaskPage



