import RootStore from 'core/Store'
// import * as api from 'core/api'

class Store extends RootStore {
    constructor(actions) {
        super()
        this.listenables = actions
        this.state = {
            isShow:false,
            memberList:[],
            openIds:[],
            scheduleId: 0,
            subprojectId: 0
        }
        this.init()
    }

    init() {
        this.$on('services/ModalMember/state',(key, { isShow, moreId, subprojectId }) => {
            console.log('services/ModalMember/state',isShow, moreId, subprojectId )
            this.setState({
                isShow,
                scheduleId: moreId,
                subprojectId
            })
        })
    }

    onRemoveEvents() {
        this.clearEvents()
    }

    getMemberList(scheduleId){
        this.$ajax
            .get(this.api('project/scheduleMember')(),{
                params: {
                    scheduleId
                }
            })
            .then( res => {
                if(res.status === 200){
                    let memberList =  res.data.scheduleMember
                    memberList.map( (item, index) => {
                        item.isCheck = false
                        item.key = index
                        item.label = item.userName
                        return item
                    })

                    this.setState({
                        memberList
                    })
                }
            })
            .catch( error => {
                console.log('error:', error)
            })
    }

    onDidMount(){
        if(this.state.memberList.length === 0) {
            let scheduleId = this.state.scheduleId || 272
            this.getMemberList(scheduleId)
        }
    }

    onHandleCheck(list) {
        let openIds = []
        list.forEach( (item) => {
            openIds.push({
                openId: item.openId
            })
        })
        this.setState({
            openIds
        })
    }

    onConfirm(){
        this.$ajax.post(this.api('project/transferSchedule')(),{
                params: {
                    scheduleId: this.state.scheduleId,
                    subprojectId: this.state.subprojectId,
                    openId: this.cookie.get("openId"),
                    transferMembers: this.state.openIds
                }
            })
            .then( res => {
                this.setState({
                    isShow: false
                })
            })
            .catch( error => {
                this.setState({
                    isShow: false
                })
                console.log('error:', error)
            })
    }

    onCancel(){
        this.setState({
            isShow: false
        })
    }
}

export default Store



