import RootStore from 'core/Store'
import * as api from 'core/api'

class Store extends RootStore {
    constructor(actions) {
        super();
        this.state = {
            isShow: false,
            style:{},
            list: [
                {
                    key: 'tags-1',
                    label: '合同备案进度'
                },{
                    key: 'tags-2',
                    label: '付款进度'
                },{
                    key: 'tags-3',
                    label: '分配进度'
                },{
                    key: 'tags-4',
                    label: '计划进度'
                },{
                    key: 'tags-5',
                    label: '近期出图回文'
                },{
                    key: 'tags-6',
                    label: '近期会议组织'
                },{
                    key: 'tags-7',
                    label: '外部设计条件'
                },{
                    key: 'tags-8',
                    label: '内部落实'
                },{
                    key: 'tags-9',
                    label: '总结交流'
                }
            ]
        }
        this.listenables = actions

        this.initListen()
    }

    initListen() {
        this.$on('services/DropdownTags/state', (key, { isShow, style }) => {
            this.setState({
                isShow,
                style
            })
        })
    }

    onSelectItem(event, item) {
        event.preventDefault()
        event.stopPropagation()
        this.$emit('services/DropdownTags/item',{
            item
        })
    }

    onTestAction(value) {
        this.setState({
            test: value
        })
    }

    onRemoveEvents() {
        this.clearEvents()
    }
}

export default Store



