import RootStore from 'core/Store'
import utils from 'core/utils'

class Store extends RootStore {
    constructor(actions) {
        super()
        this.listenables = actions
        this.state = {
            data: [],
            params: {
                "currentPageNumber": 1,
                "pageSize": 10,
                "openId": this.cookie.get('openId'),
                "teamId": this.cookie.get('teamId') || 1,
                "subprojectNames": "",
                "subprojectStatus": "",
                "startTime": utils.getOneMonth('startTime'),
                "endTime":  utils.getOneMonth('endTime')
            },
            api: this.api('team/task/project')(),
            currentTeam: {
                teamName: this.cookie.get('teamName') || '顶级管理员',
                teamId: this.cookie.get('teamId') || 1
            },
            time: {
                label: '近1个月',
                name: 'month'
            }
        }
        this.init()
    }

    init() {

        this.$on('components/services/RoundSearch/teamProject', (key, {value}) => {
            this.setState({
                params: Object.assign({},this.state.params,{
                    subprojectNames: value
                })
            })
        })

        this.$on('pages/ProjectAdd/teamSelect', (key, teamSelect) => {
            this.setState({
                currentTeam: {
                    teamName: teamSelect.label,
                    teamId: teamSelect.teamId
                },
                params: Object.assign({},this.state.params,{
                    teamId: teamSelect.teamId
                })
            })
            this.cookie.set('teamName', teamSelect.label)
            this.cookie.set('teamId', teamSelect.teamId)
        })
        //this.$emit('services/DropdownSteps/selectItem', selectItem)
        this.$on('services/DropdownSteps/selectItem', (key, selectItem) => {
            console.log('selectItem', selectItem, selectItem.label)
            this.setState({
                params: Object.assign({},this.state.params,{
                    subprojectStatus: selectItem.label
                })
            })

        })

        this.$on('services/ModalTime/data', (key, {label, name}) => {
            this.setState({
                time: Object.assign({}, this.state.time, {
                    label,
                    name
                })
            })
            switch (name) {
                case 'year' : {
                    this.setState({
                        params: Object.assign({} , this.state.params, {
                            "startTime": utils.getOneYear('startTime'),
                            "endTime": utils.getOneYear('endTime')
                        })
                    })
                    console.log("year")
                }
                    break
                case 'month' : {
                    this.setState({
                        params: Object.assign({} , this.state.params, {
                            "startTime": utils.getOneMonth('startTime'),
                            "endTime": utils.getOneMonth('endTime')
                        })
                    })
                    console.log("month")
                }
                    break
                case 'week' : {
                    console.log("week")
                    this.setState({
                        params: Object.assign({} , this.state.params, {
                            "startTime": utils.getOneWeek('startTime'),
                            "endTime": utils.getOneWeek('endTime')
                        })
                    })
                }
                    break
            }
        })
    }

    onSelectTime() {
        this.$emit('services/ModalTime/state', {
            isShow: true,
            name: this.state.time.name
        })
    }

    getProjectList() {
        this.$ajax.get(this.state.api, {
            params: this.state.params
        }).then(res => {
            let data = res.data.data
            let tmp = []
            data.map(item => {
                tmp.push(item.taskOverTime + item.taskUnfinished)
                return item
            })

            let max = Math.max(...tmp)
            data.map(item => {
                item.percentagetOverTime = max ? (item.taskOverTime / max * 100) : 0
                item.percentagetUnfinished = max ? (item.taskUnfinished / max * 100) : 0
                return item
            })

            this.setState({
                data
            })
            console.log('team/task/people', res)
        }).catch(error => {

        })
    }

    onDetail(subprojectId) {
        this.cookie.set('subprojectId', subprojectId)
        this.$route.push(`/team/taskProjectDetail/statistics/${subprojectId}`)
    }

    onDidMount(){
        this.$emit('components/services/RoundSearch/type', {
            type: 'teamProject'
        })
    }

    onToggleTeam(event) {
        event.stopPropagation()
        let isShow = true
        this.setState({
            isShow
        })
        this.$emit('services/TeamSelect/state',{
            isShow
        })
    }

    onToggleFilter(event) {
        let {left, top, width, height} = event.target.getBoundingClientRect()
        let style = {
            left: (left + width)+ "px",
            top: (top + height) +"px",
            transform: "translateX(-100%)"
        }
        let isShow = !this.state.isShow
        this.setState({
            isShow
        })
        this.$emit('services/DropdownSteps/state',{
            isShow,
            style
        })
        console.log('click onToggleFilter')
    }

    onRemoveEvents() {
        this.clearEvents()
    }
}

export default Store



