import RootStore from 'core/Store'
import utils from 'core/utils'

class Store extends RootStore {
    constructor(actions) {
        super();
        this.state = {
	        params: {
		        openId: this.cookie.get('openId'),
		        currentPageNumber:1,
		        pageSIze:10,
		        taskTime: this.getCurrentTime()
	        },
	        api: this.api('mine/schedule')()
        }
        this.listenables = actions

        this.init()
    }

    init() {
	    this.$on('services/DatePicker/checkTime', (key, data) => {
		    console.log("services/DatePicker/checkTime", data)
		    this.setState({
			    params: Object.assign({}, this.state.params, {
				    currentPageNumber: 1,
				    pageSIze: 10,
				    taskTime: data.taskTime
			    })
		    })
	    })
    }

	getCurrentTime() {
		return utils.getOneMonth("startTime")
	}

	onAdd() {
		this.$route.push('/mine/schedule/add')
	}

    onRemoveEvents() {
        this.clearEvents()
    }

}

export default Store



