import RootStore from 'core/Store'

class Store extends RootStore {
    constructor(actions) {
        super()
        this.state = {
            test:'default'
        }
        this.listenables = actions

        this.init()
    }

    init() {

    }

    onShow() {
        this.$emit('services/MaskUserAssign/state',{})
    }

    onRemoveEvents() {
        this.clearEvents()
    }

}

export default Store



