import React, { PropTypes } from 'react'
import Reflux from 'reflux'
import Store from './store'
import Actions from './actions'
import './styles'

import HeaderTab from 'widgets/HeaderTab'

class MineTabHeaderPage extends React.Component {
    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store(this.actions)
	    this.state = this.store.getState()
	    this.store.listen((...data) => {
		    this.setState(...data)
	    })
	    this.tabs = [
		    {
			    name: 'undone',
			    label:'未完成/超期',
			    link:'/mine/undone'
		    },
		    {
			    name: 'removed',
			    label:'已转移',
			    link:'/mine/removed'
		    },
		    {
			    name: 'schedule',
			    label:'日程',
			    link:'/mine/schedule'
		    }
	    ]
    }

    componentDidMount() {
	    this.actions.setCurrentRoute(this.tabs)
    }

    componentWillUnmount() {
        // no del
        this.actions.removeEvents()
    }

    render() {
        return (
            <div className="root-page-minetabheader">
	            <HeaderTab
		            tab={ this.tabs }
		            current={ this.state.current }
		            onHandleClick={ () => this.actions.setCurrentRoute(this.tabs) }
	            />
            </div>
        )
    }

}

export default MineTabHeaderPage



