import React, { PropTypes } from 'react'
import Store from './store'
import Actions from './actions'
import './styles'
import cookie from 'js-cookie'

import TeamHeader from 'services/TeamHeader'

class TeamTaskProjectDetailPage extends React.Component {
    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store(this.actions)
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })

        this.tabs = [{
            link: '/team/taskProjectDetail/statistics/' + cookie.get('subprojectId') || '',
            name: 'statistics',
            label: '统计'
        },{
            link: '/team/taskProjectDetail/analysis/' + cookie.get('subprojectId') || '',
            name: 'analysis',
            label: '分析'
        }]
    }

    componentDidMount() {
        this.actions.setCurrentRoute(this.tabs)
    }

    componentWillUnmount() {
        // no del
        this.actions.removeEvents()
    }

    render() {
        return (
            <div className="root-page-teamtaskprojectdetaill">
                <TeamHeader
                    tabs={this.tabs}
                    current={ this.state.current }
                    onHandleClick={ () => { this.actions.setCurrentRoute(this.tabs) } }
                />
                { this.props.children }
            </div>
        )
    }

}

export default TeamTaskProjectDetailPage



