import React, { PropTypes } from 'react'
import Reflux from 'reflux'
import Store from './store'
import Actions from './actions'
import './styles'

// test ItemSwitch
import ItemSwitch from '../index'
import Button from 'widgets/Button'

class Demo extends React.Component {
    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store(this.actions)
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })
    }

    componentDidMount() {

    }

    componentWillUnmount() {
        this.actions.removeEvents()
    }

    renderOperation = () => {
        return [
            <Button className="btn-delete" icon="delete" type="icon">
                <span>删除</span>
            </Button>,
            <Button className="btn-add" icon="note" type="icon">
                <span>新建任务</span>
            </Button>
        ]
    }

    render() {
        return (
            <div className="root-widget-itemswitch__demo__">
                <div className="item">
                <ItemSwitch
                    onRenderOperation={ this.renderOperation }
                >
                    <div style={{'height':'50px'}}></div>
                </ItemSwitch>
                </div>
            </div>
        )
    }
}

export default Demo



