import React, { PropTypes } from 'react'
import Store from './store'
import Actions from './actions'
import './styles'

import Modal from 'widgets/Modal'

class ModalCompletePage extends React.Component {
    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store(this.actions)
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })
    }

    componentDidMount() {
        this.actions.didMount()
    }

    componentWillUnmount() {
        // no del
        this.actions.removeEvents()
    }

    render() {
        return (
            <div className="root-page-modalcomplete">
                <Modal
                    isShow={ this.state.isShow }
                    onConfirm={ () => {this.actions.handleConfirm() }}
                >
                    <div className="complete-text">
                        <p>是否已经完成任务</p>
                        <h5>界面展示设计</h5>
                    </div>
                </Modal>
            </div>
        )
    }

}

export default ModalCompletePage



