import RootStore from 'core/Store'

class Store extends RootStore {
    constructor(actions) {
        super()
        this.listenables = actions
        this.state = {
            test:'default'
        }
        this.init()
    }

    init() {

    }

    onHandleClick() {
        this.$emit('services/ModalTime/state',{
            isShow: true
        })
    }

    onRemoveEvents() {
        this.clearEvents()
    }

}

export default Store



