import { createActions } from 'reflux'

class Actions {
    constructor() {
        return createActions([
            'removeEvents',
            'handleCancel',
            'handleSave',
            'handleChange',
            'addSub',
            'toggle',
            'memberToggle',
            'stageToggle'
        ])
    }
}

export default Actions



