import React, { PropTypes } from 'react'
import Reflux from 'reflux'
import Store from './store'
import Actions from './actions'
import './styles'

import FooterTab from 'widgets/FooterTab'

class TeamFooterPage extends React.Component {
    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store(this.actions)
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })

        this.tabs = [
            {
                name: 'task',
                label:'任务强度',
                icon:'reports',
                link:'/team/task'
            },
            {
                name: 'manpower',
                label:'人力分布',
                icon:'team',
                link:'/team/manpower'
            },
            {
                name: 'plan',
                label:'计划安排',
                icon:'scheduling',
                link:'/team/plan'
            }
        ];

    }

    componentDidMount() {
        this.actions.setCurrentRoute(this.tabs)
    }

    componentWillUnmount() {
        // no del
        this.actions.removeEvents()
    }

    render() {
        return (
            <div className="root-page-teamfooter">
                <FooterTab 
                    tabs={this.tabs}
                    onHandleClick={ () => { this.actions.setCurrentRoute(this.tabs) }}
                    current={ this.state.current }
                />
            </div>
        )
    }

}

export default TeamFooterPage



