import RootStore from 'core/Store'

class Store extends RootStore {
    constructor(actions) {
        super();
        this.state = {
            isOpen: false,
            time: new Date(),
            moreId: 0
        }
        this.listenables = actions

        this.init()
    }

    init() {
        this.initListen()
    }

    initListen() {
        this.$on('services/ModalDate/state', (key, { isOpen, moreId, subprojectId }) => {
            this.setState({ isOpen, moreId, subprojectId })
        })
    }

    onHandleCancel() {
        this.setState({
            isOpen: false
        })
    }

    dateFormat(time) {
        let weekday = ["星期天","星期一","星期二","星期三","星期四","星期五","星期六"],
            myDate = new Date(time) ,
            year = myDate.getFullYear(),
            month = myDate.getMonth() + 1,
            day = myDate.getDate(),
            week = weekday[myDate.getDay()]
        return {
            year,
            month,
            day,
            week
        }
    }

    onHandleSelect(time) {
        let delayTime = this.dateFormat(time)
        let date = `${delayTime.year}-${delayTime.month}-${delayTime.day}`
        console.log("delayTimedelayTimedelayTime", date)
        this.setState({
            time
        })

        this.$emit('pages/TaskAdd/taskTime',{
            taskTime:date
        })

        this.$ajax.post(this.api('project/task/delay')(),{
            id: this.state.moreId, //"任务id"（必须传）
            date, //"日期"（必须传2017-3-21）
            "openId": this.cookie.get("openId"),
            "subprojectId": this.state.subprojectId,
            "scheduleId": this.state.moreId
        }).then( res => {
            this.$emit('services/ProjectTaskList/delay',{})
            this.setState({
                isOpen: false
            })
        }).catch( error => {
            this.setState({
                isOpen: false
            })
        })

    }

    onRemoveEvents() {
        this.clearEvents()
    }
}

export default Store



