/**
 * ItemSwitch
 */

import React, { PropTypes } from 'react'
import classnames from 'classnames'

class ItemSwitch extends React.Component {
    static propTypes = {
        className: PropTypes.string,
        children: PropTypes.any,
        btnWidth: PropTypes.number,
        operation: PropTypes.any,
        onRenderOperation: PropTypes.func,
        moveX: PropTypes.number,
        onHandleChange: PropTypes.func
    }

    static defaultProps = {
        moveX: 0
    }

    constructor(props) {
        super()
        this.state = {
            btnWidth: 50,
            style: {},
            moveX: props.moveX
        }
        this.startClientX = null
        this.preMoveX = 0
        this.speed = .8
    }

    setStyles(moveX,flag) {
        let style = {}
        if(flag){
            style = {
                "transform": `translateX(${ moveX }px)`,
                "transition": "all .2s",
            }
        } else {
            style = {
                "transform": `translateX(${ moveX }px)`
            }
        }
        this.setState({
            style
        })
        //this.props.onHandleChange && this.props.onHandleChange(moveX)
    }

    transitionEnd = () => {
        this.setStyles(this.state.moveX, false)
    }

    handleMove = (event) => {
        let moveClientX = event.touches[0].clientX
        let moveX = this.preMoveX + ((moveClientX - this.startClientX) * this.speed)

        if(moveX <= 0 && moveX >= - this.state.btnWidth){
            this.setState({
                moveX
            })
            this.setStyles(moveX, false)
        }

    }

    handleStart = (event) => {
        this.startClientX = event.touches[0].clientX
    }

    handleEnd = (event) => {
        let btnWidth = this.state.btnWidth
        let moveX = this.state.moveX
        this.preMoveX =  moveX

        if(moveX < 0 && moveX > -btnWidth/2){
            this.setState({
                moveX: 0
            })
            this.setStyles(0, true)
        } else if(moveX < -btnWidth/2 && moveX > -btnWidth ){
            this.setState({
                moveX: -btnWidth
            })
            this.setStyles(-btnWidth, true)
        }
    }


    componentDidMount() {
        this.refs.children.addEventListener('transitionEnd', this.transitionEnd)
        this.refs.children.addEventListener('webkitTransitionEnd', this.transitionEnd)
        this.setState({
            btnWidth: this.refs.operation.clientWidth
        })
        //this.props.onHandleChange && this.props.onHandleChange(moveX)
    }

    componentWillReceiveProps({ moveX }) {
        this.setState({
            moveX
        })
    }

    componentWillUnmount() {

    }

    render() {
        const { className } = this.props

        return (
            <div className={ classnames('root-widget-itemswitch',className) }
                onTouchMove={ this.handleMove }
                onTouchStart={ this.handleStart }
                onTouchEnd={ this.handleEnd }
            >
                <div className="children" ref="children" style={ this.state.style }>
                        {this.props.children}
                </div>
                <div className="operation">
                    <ul className="li-left right" ref="operation">
                        {
                            this.props.onRenderOperation &&
                            this.props.onRenderOperation().map ((item, index) => {
                                return (
                                    <li key={ index }>
                                        {
                                            item
                                        }
                                    </li>
                                )
                            })
                        }
                    </ul>
                </div>
            </div>
        )
    }
}

export default ItemSwitch;



