import RootStore from 'core/Store'
import * as api from 'core/api'

class Store extends RootStore {
    constructor(actions) {
        super();
        this.state = {
            checkItem: 0,
            nowPeople: [
                '王五', '赵二','王五', '赵二','王五', '赵二','王五', '赵二','王五', '赵二',
                '王五', '赵二','王五', '赵二','王五', '赵二','王五', '赵二'
            ],
            projectList: [
                {
                    label: 'A',
                    value: 0 ,
                    childItem: {

                    }
                },
                {
                    label: 'B',
                    value: 1 ,
                    childItem: {

                    }
                },
                {
                    label: 'C',
                    value: 2,
                    childItem: {

                    }
                },
            ]
        }
        this.listenables = actions

        this.init()
    }

    init() {

    }

    onTestAction(value) {
        this.setState({
            test: value
        })
    }

    onRemoveEvents() {
        this.clearEvents()
    }

}

export default Store



