/**
 * Checkbox
 */

import React, { PropTypes } from 'react'
import classnames from 'classnames'
import Icon from 'widgets/Icon'

class Checkbox extends React.Component {
    static propTypes = {
        className: PropTypes.string,
        list: PropTypes.array,
        onCheck: PropTypes.func
    }

    static defaultProps = {
        list: []
    }

    constructor(props) {
        super()
        this.state = {
            list: props.list
        }
    }

    componentDidMount() {
        this.props.onCheck && this.props.onCheck(this.state.list)
    }

    componentWillReceiveProps({ list }) {
        this.setState({
            list
        })
    }

    componentWillUnmount() {

    }

    toggle(event ,item, index, one=false) {
        event.preventDefault();
        event.stopPropagation()
        if(one){
            this.state.list.map( (v, i) => {
                if (v.isCheck) {
                    v.isCheck = !v.isCheck
                }
            })
        }

        console.log('list',this.state.list)
        let list = this.state.list.map( (v, i) => {
            if(index === i) {
                v.isCheck = !v.isCheck
            }
            return v
        })
        this.setState({
            list
        })

        this.props.onCheck && this.props.onCheck(list)

    }

    renderLabel(item) {
        return (
            <label className={ classnames({'current': item.isCheck}) }>{ item.label }</label>
        )
    }

    renderItem(item, index, one) {
        return (
            <li key={ index }
                onClick={ (event)=> { this.toggle(event, item, index, one) }}
            >
                { item.label ? this.renderLabel(item) : null }
                <span className={ classnames({'current': item.isCheck}) }>
                    <Icon icon="tick" />
                </span>
            </li>
        )
    }

    render() {
        const { className, one } = this.props
        return (
            <ul className={ classnames('root-widget-checkbox',className) }
            >
                {
                    this.state.list.length > 0 &&
                    this.state.list.map( (item, index) => {
                        return this.renderItem(item, index, one)
                    })
                }
            </ul>
        )
    }
}

export default Checkbox;



