import React, { PropTypes } from 'react'
import Reflux from 'reflux'
import Store from './store'
import Actions from './actions'
import './styles'

import ItemThumb from 'widgets/ItemThumb'
import PullUp from 'widgets/PullUp'
import RoundSearch from 'services/RoundSearch'

class MemoListPage extends React.Component {
    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store(this.actions)
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })

        this.stageColor = {
            "red": "#cf4b55",
            "blue": "#6da0eb",
            "violet": "#176ceb",
            "yellow": "#fcff04",
            "purple": "#c740fc",
            "green": "#41d119",
            "grey": "#666"
        }
        //前期工作，方案设计，初步设计，施工图设计，施工配合，暂缓,完成)
        this.stageColor = {
            "前期工作": "#c740fc",
            "方案设计": "#f85f57",
            "施工配合": "#176ceb",
            "初步设计": "#41d119",
            "施工图设计": "#02b0f9",
            "暂缓": "#71757b",
            "完成": "#fcff04"
        }
    }

    componentDidMount() {
        console.log('444======')
    }

    componentWillUnmount() {
        // no del
        this.actions.removeEvents()
    }

    getUserTs(userTs) {
        if (userTs.length === 0) return
        let usersName = []
        for (var i = 0; i < userTs.length; i++) {
            usersName.push(userTs[i].userName)
        }
        return usersName.join(',')
    }

    renderItem = (item, index) => {
        console.log('item=item', item)
        return (
            <ItemThumb
                key={ index }
                onDetail={ ()=> {this.actions.handleShowDetail(item.subprojectId)}}
                thumb={
                        <div style={{
                        'height':'100%',
                        'backgroundColor': this.stageColor[item.stage]
                        }}></div>
                    }
                title={ `${item.projectName} | ${item.subprojectName}`  }
                desc={ `负责人:${item.principalList.join(',')}, 当前阶段:${ item.stage }` }
            />
        )
    }

    render() {
        return (
            <div className="root-page-memolist">
                <PullUp
                    api={ this.state.api }
                    params={ this.state.params }
                    dataField="data"
                    renderItem={ this.renderItem }
                >
                    <RoundSearch />
                </PullUp>
            </div>
        )
    }

}

export default MemoListPage



