/**
 * Button
 */

import React, { PropTypes } from 'react'
import classnames from 'classnames'
import Icon from 'widgets/Icon'

class Button extends React.Component {
    static propTypes = {
        className: PropTypes.string,
        label: PropTypes.string,
        children: PropTypes.any,
        icon: PropTypes.string,
        type: PropTypes.string
    }

    static defaultProps = {
        label: ''
    }

    constructor(props) {
        super()
        this.state = {
            label: props.label
        }
    }

    componentDidMount() {

    }

    componentWillReceiveProps({ label }) {
        this.setState({
            label
        })
    }

    componentWillUnmount() {

    }

    renderLabel(label) {
        return (
            <label>{ label }</label>
        )
    }

    renderIcon(icon) {
        return (
            <Icon icon={ icon } />
        )
    }

    render() {
        const { className, type, children, ...props } = this.props
        return (
            <button className={ classnames('root-widget-button',className,type) } { ...props }>
                { this.props.icon ? this.renderIcon(this.props.icon) : null }
                { this.state.label ? this.renderLabel(this.state.label) : null }
                { this.props.children ? this.props.children : null}
            </button>
        )
    }
}

export default Button;



