import React, { PropTypes } from 'react'
import Reflux from 'reflux'
import Store from './store'
import Actions from './actions'
import './styles'

import TeamReportsContent from 'widgets/TeamReportsContent'
import RoundSearch from 'services/RoundSearch'
import Icon from 'widgets/Icon'
import Checkbox from 'widgets/Checkbox'
import Button from 'widgets/Button'

class TeamContentPage extends React.Component {
    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store(this.actions)
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })

        this.list= [
            {
                key: 1,
                label: '测试啊及',
                isCheck: true
            },{
                key: 2,
                label: '测试aa啊及',
                isCheck: true
            },{
                key: 3,
                label: '测试==啊及',
                isCheck: true
            },{
                key: 4,
                label: '测试比啊',
                isCheck: true
            }
        ]
    }

    componentDidMount() {

    }

    componentWillUnmount() {
        // no del
        this.actions.removeEvents()
    }


    renderTop() {
        return (
            <div className="reports-top">
                <div className="top-nav">
                    <div className="top-nav-team">
                        <span>{ this.state.currentTeam.teamName}</span>
                        <Button
                            icon="show"
                            type="icon"
                            className="right"
                            onClick={ (event)=> { this.actions.toggleTeam(event) }}
                        >
                        </Button>
                    </div>
                    <div className="top-nav-filer">
                        <Button
                            icon="filter"
                            type="icon"
                            className="right"
                            onClick={ (event)=> { this.actions.toggleFilter(event) }}
                        >
                        </Button>
                    </div>
                    <div className="top-nav-search">
                        <RoundSearch />
                    </div>
                </div>
                <div className="time-box">
                    <Icon icon="time" />
                    <span> 时间区间：{this.props.time}</span>
                </div>
            </div>
        );
    }


    render() {
        return (
            <div className="root-page-teamcontent">
                { this.renderTop() }
                <TeamReportsContent 
                    data={this.state.data}
                    current={this.props.current} 
                    list={this.list}
                    onCheck={(list) => console.log(list)}
                />
            </div>
        )
    }

}

export default TeamContentPage



