import RootStore from 'core/Store'
import utils from 'core/utils'

class Store extends RootStore {
    constructor(actions) {
        super();
        this.state = {
            api: this.api('team/plan')(),
            params: {
                "currentPageNumber": 1,
                "pageSize": 10,
                "teamId": this.cookie.get('teamId'),
                "openId": this.cookie.get('openId'),
                "scheduleType":"e",//（ 任务类型(e或f，必选)：e.近期出图回文、f.近期会议组织）
                "startTime": utils.getOneMonth('startTime'), //（可选，默认为一个月前）
                "endTime": utils.getOneMonth('endTime') //（可选，默认为今天）
            },
            currentTeam: {
                teamName: this.cookie.get('teamName') || '顶级管理员',
                teamId: this.cookie.get('teamId') || 1
            },
            time: {
                label: '近1个月',
                name: 'month'
            }
        }
        this.listenables = actions

        this.init()
    }

    init() {
        this.$on('components/services/RoundSearch/planPlot', (key, {value}) => {
            this.setState({
                params: Object.assign({},this.state.params,{
                    subprojectName: value
                })
            })
        })

        this.$on('pages/ProjectAdd/teamSelect', (key, teamSelect) => {

            this.setState({
                currentTeam:{
                    teamName: teamSelect.label,
                    teamId: teamSelect.teamId
                },
                params: Object.assign({},this.state.params,{
                    teamId: teamSelect.teamId
                })
            })

            this.cookie.set('teamName', teamSelect.label)
            this.cookie.set('teamId', teamSelect.teamId)
            if(this.state.isShow) {
                this.setState({
                    isShow: false
                })
            }
        })

        this.$on('services/ModalTime/data', (key, {label, name}) => {
            this.setState({
                time: Object.assign({}, this.state.time, {
                    label,
                    name
                })
            })
            switch (name) {
                case 'year' : {
                    this.setState({
                        params: Object.assign({} , this.state.params, {
                            "startTime": utils.getOneYear('startTime'),
                            "endTime": utils.getOneYear('endTime')
                        })
                    })
                    console.log("year")
                }
                    break
                case 'month' : {
                    this.setState({
                        params: Object.assign({} , this.state.params, {
                            "startTime": utils.getOneMonth('startTime'),
                            "endTime": utils.getOneMonth('endTime')
                        })
                    })
                    console.log("month")
                }
                    break
                case 'week' : {
                    console.log("week")
                    this.setState({
                        params: Object.assign({} , this.state.params, {
                            "startTime": utils.getOneWeek('startTime'),
                            "endTime": utils.getOneWeek('endTime')
                        })
                    })
                }
                    break
            }
        })
    }

    onSelectTime() {
        this.$emit('services/ModalTime/state', {
            isShow: true,
            name: this.state.time.name
        })
    }

    onDidMount() {
        this.$emit('components/services/RoundSearch/type', {
            type: 'planPlot'
        })
    }

    onMoreToggle(event, task) {
        console.log("task",task)
        event.preventDefault()
        event.stopPropagation()
        let {left, top, width, height} = event.target.getBoundingClientRect(),
            moreId = task.scheduleId,
            subprojectId = task.subprojectId

        let style = {
            left: (left + width)+ "px",
            top: (top + height) +"px",
            transform: "translateX(-100%)"
        }

        if(moreId === this.state.moreId){
            let isShow = !this.state.isShow
            this.setState({
                isShow
            })
            this.$emit('services/DropdownMore/state', {
                isShow,
                style,
                subprojectId
            })
        } else {
            this.setState({
                isShow: true,
                moreId
            })
            //console.log('services/DropdownMore/state',moreId,style)
            this.$emit('services/DropdownMore/state', {
                isShow : true,
                moreId,
                task,
                style
            })
        }
    }

    onToggleTeam(event) {
        event.stopPropagation()
        event.preventDefault()
        let isShow = !this.state.isShow
        this.setState({
            isShow
        })
        this.$emit('services/TeamSelect/state',{
            isShow
        })
    }

    onRemoveEvents() {
        this.clearEvents()
    }

}

export default Store



