import React, { PropTypes } from 'react'
import Reflux from 'reflux'
import Store from './store'
import Actions from './actions'
import './styles'

// test Steps
import Steps from '../index'

class Demo extends React.Component {
    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store(this.actions)
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })

	    this.list =
		    {
			    listTitle: {
			        titleDate:'2016.01.01',
                    titleCon:'WECHATE | UI设计'
                },
			    listCon:'项目成立'
		    }

    }

    componentDidMount() {

    }

    componentWillUnmount() {
        this.actions.removeEvents()
    }

    render() {
        return (
            <div className="root-widget-steps__demo__">
                <Steps listTitle={this.list.listTitle} listCon={this.list.listCon}/>
            </div>
        )
    }
}

export default Demo



