import RootStore from 'core/Store'
import * as api from 'core/api'

class Store extends RootStore {
    constructor(actions) {
        super();
        this.state = {
            current:'undone'
        }
        this.listenables = actions
    }

    onSetCurrentRoute(tabs) {
        let currentPathName = window.location.pathname
        tabs.forEach((item, index) => {
            if(new RegExp(item.link).test(currentPathName)){
                this.setState({
                    current: item.name
                })
            }
        })

        this.$emit('services/MineTabHeader/route',{ currentRoute : this.state.current })
    }

    onRemoveEvents() {
        this.clearEvents()
    }
}

export default Store



