import React, { PropTypes } from 'react'
import Reflux from 'reflux'
import classnames from 'classnames'
import Store from './store'
import Actions from './actions'
import './styles'

import TeamFooter from 'services/TeamFooter'
import Icon from 'widgets/Icon'
import RoundSearch from 'widgets/RoundSearch'
import { Link } from 'react-router'
import PullUp from 'widgets/PullUp'
import Button from 'widgets/Button'
import DropdownSteps from 'services/DropdownSteps'

class TeamManpowerPage extends React.Component {
    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store(this.actions)
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })

        this.list= [
            {
                key: 1,
                label: '测试啊及',
                isCheck: true
            },{
                key: 2,
                label: '测试aa啊及',
                isCheck: true
            },{
                key: 3,
                label: '测试==啊及',
                isCheck: true
            },{
                key: 4,
                label: '测试比啊',
                isCheck: true
            }
        ]
    }

    componentDidMount() {

    }

    componentWillUnmount() {
        // no del
        this.actions.removeEvents()
    }

    renderItem = (item, index) => {
        console.log('item=item', item)
        let numberOfPeopleArr = []
        for(var i =0; i < item.numberOfPeople; i++) {
            numberOfPeopleArr.push(i)
        }
        return (
            <div className="item" key={ index }>
                <div className="left">
                    <div className="text-box">
                        <p>UI设计</p>
                        <p>Wechat PM</p>
                    </div>
                    <span className="icon-square">{ item.numberOfPeople }</span>
                </div>
                <div className="right">
                    <Link to="/team/manpower/setting">
                        {
                            numberOfPeopleArr.map( (v,i) => <Icon key={i} icon="head" />)
                        }
                    </Link>
                </div>
            </div>
        )
    }

    renderHeader() {
        return (
            <div className="root-page-teammanpower-header">
                <ul className="right li-left">
                    <li>
                        <Button
                            className="btn-filter"
                            icon="filter"
                            type="icon"
                            onClick={ (event)=> { this.actions.toggle(event) }}
                        >
                        </Button>
                    </li>
                </ul>
                <div className="auto">
                    <div className={ classnames('input-search',{'show': this.state.isSearch}) }>
                        <Icon
                            icon="search"
                            onClick={ ()=> { this.actions.search() }}
                        />
                        <input
                            type="text"
                            value={ this.state.inputValue }
                            onChange={ (e) => this.actions.inputChange(e)}
                            onKeyDown={ (e)=> this.actions.enter(e) }
                        />
                    </div>
                </div>
                <DropdownSteps />
            </div>
        )
    }

    render() {
        return (
            <div className="root-page-teammanpower">
               <div className="search-box">
                   {
                       this.renderHeader()
                   }
                </div>
                <div className="content-box">
                    <div className="bottom-box">
                        <PullUp
                            api={ this.state.api }
                            params={ this.state.params }
                            dataField="data"
                            renderItem={ this.renderItem }
                        >
                            <div className="top-box">
                                <div className="left">
                                    <Icon icon="project" />
                                    项目
                                </div>
                                <div className="right">
                                    <Icon icon="project" />
                                    当前参与人数
                                </div>
                            </div>
                        </PullUp>
                    </div>
                </div>
            </div>
        )
    }

}

export default TeamManpowerPage



