import React, { PropTypes } from 'react'
import Reflux from 'reflux'
import Store from './store'
import Actions from './actions'
import './styles'

// test HeaderTab
import HeaderTab from '../index'

class Demo extends React.Component {
    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store(this.actions)
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })

        this.tab = [
	        {
		        name: 'event',
		        label:'大事记',
		        link:'/'
	        },
	        {
		        name: 'personal',
		        label:'个人',
		        link:'/personal'
	        }
        ]
	    this.current = 'event'
    }

    componentDidMount() {

    }

    componentWillUnmount() {
        this.actions.removeEvents()
    }

    render() {
        return (
            <div className="root-widget-headertab__demo__">
                <HeaderTab tab={ this.tab } current={ this.current }/>
            </div>
        )
    }
}

export default Demo



