/**
 * HeaderTab
 */

import React, { PropTypes } from 'react'
import classnames from 'classnames'
import { Link } from 'react-router'

class HeaderTab extends React.Component {
    static propTypes = {
        className: PropTypes.string,
        children: PropTypes.any,
	    current: PropTypes.string.isRequired,
        onHandleClick: PropTypes.func,
        tab: PropTypes.array
    }

    constructor(props) {
        super()
        this.state = {
            current: props.current
        }
    }

    componentDidMount() {

    }

    componentWillReceiveProps({ current }) {
        this.setState({
            current
        })
    }

    componentWillUnmount() {

    }

    render() {
        const { className, tab } = this.props
        return (
            <div className={ classnames('root-widget-headertab',className) }  >
                <ul>
                    {
	                    tab.map( (item, index)=> {
		                    return (
                                <li key={ index }
                                    onClick={ () => this.props.onHandleClick(item) }
                                >
                                    <Link to={ item.link } className={ classnames({'current': item.name === this.state.current})}>
                                        <span>{ item.label }</span>
                                    </Link>
                                </li>
		                    );
	                    })
                    }
                </ul>
            </div>
        )
    }
}

export default HeaderTab;



