import RootStore from 'core/Store'
import * as api from 'core/api'

class Store extends RootStore {
    constructor(actions) {
        super();
	    this.state = {
		    list: [
			    {
				    listTitle: {
					    titleDate:'2016.01.01',
					    titleCon:'WECHATE | UI设计'
				    },
				    listCon:'项目成立项目成立项目成立项目成立项目成立项目成立项目成立项目成立项目成立项目成立项目成立项目成立项目成立项目成立'
			    },
			    {
				    listTitle: {
					    titleDate:'2016.01.01',
					    titleCon:'WECHATE | UI设计'
				    },
				    listCon:'项目成立项目成立项目成立项目成立项目成立项目成立项目成立项目成立项目成立项目成立项目成立项目成立项目成立项目成立项目成立'
			    },
			    {
				    listTitle: {
					    titleDate:'2016.01.01',
					    titleCon:'WECHATE | UI设计'
				    },
				    listCon:'项目成立'
			    },
			    {
				    listTitle: {
					    titleDate:'2016.01.01',
					    titleCon:'WECHATE | UI设计'
				    },
				    listCon:'项目成立项目成立项目成立项目成立项目成立项目成立项目成立项目成立项目成立项目成立项目成立项目成立项目成立项目成立项目成立'
			    },
			    {
				    listTitle: {
					    titleDate:'2016.01.01',
					    titleCon:'WECHATE | UI设计'
				    },
				    listCon:'项目成立'
			    },
			    {
				    listTitle: {
					    titleDate:'2016.01.01',
					    titleCon:'WECHATE | UI设计'
				    },
				    listCon:'项目成立项目成立项目成立项目成立项目成立项目成立项目成立项目成立项目成立项目成立项目成立项目成立项目成立项目成立项目成立'
			    },
			    {
				    listTitle: {
					    titleDate:'2016.01.01',
					    titleCon:'WECHATE | UI设计'
				    },
				    listCon:'项目成立'
			    },
			    {
				    listTitle: {
					    titleDate:'2016.01.01',
					    titleCon:'WECHATE | UI设计'
				    },
				    listCon:'项目成立'
			    },
			    {
				    listTitle: {
					    titleDate:'2016.01.01',
					    titleCon:'WECHATE | UI设计'
				    },
				    listCon:'项目成立'
			    },
			    {
				    listTitle: {
					    titleDate:'2016.01.01',
					    titleCon:'WECHATE | UI设计'
				    },
				    listCon:'项目成立'
			    }
		    ],
		    api: this.api('memo/personal')(),
		    params: {
			    "currentPageNumber": 1,
			    "pageSize": 10,
			    "openId": this.cookie.get('openId'),
			    "searchString": ""
		    }
	    }
        this.listenables = actions

        this.init()
    }

    init() {
	    this.$on('components/services/RoundSearch/personal', (key, { value }) => {
		    console.log('components/services/RoundSearch/personal', value)
		    this.setState({
			    params: Object.assign({}, this.state.params, {
				    "currentPageNumber": 1,
				    "searchString": value
			    })
		    })
		    let timer = setTimeout(() => {
			    this.$emit('components/services/MemoSteps', {
				    api: this.state.api,
				    params: this.state.params,
				    type: 'personal'
			    })
			    clearTimeout(timer)
			    //console.log('this.state.params', this.state.params)
		    }, 0)
	    })
    }

	onDidMount() {
		console.log('--s-d-fs-dfsf-sd-f',  this.state.api)
		this.$emit('components/services/MemoSteps', {
			api: this.state.api,
			params: this.state.params,
			type: 'personal'
		})

		this.$emit('components/services/RoundSearch/type', {
			type: 'personal'
		})
	}

    onRemoveEvents() {
        this.clearEvents()
    }

}

export default Store



