import RootStore from 'core/Store'
import * as api from 'core/api'

class Store extends RootStore {
    constructor(actions) {
        super();
        this.state = {
            isShow: false,
            style:{},
            list: [
                {
                    key: 'color-1',
                    label: '前期工作',
                    color: '#c740fc'
                },{
                    key: 'color-2',
                    label: '方案设计',
                    color: '#f85f57'
                },{
                    key: 'color-3',
                    label: '初步设计',
                    color: '#41d119'
                },{
                    key: 'color-4',
                    label: '施工图设计',
                    color: '#02b0f9'
                },{
                    key: 'color-5',
                    label: '施工配合',
                    color: '#176ceb'
                },{
                    key: 'color-6',
                    label: '暂缓',
                    color: '#71757b'
                },{
                    key: 'color-7',
                    label: '完成',
                    color: '#fcff04'
                }
            ],
            index:0
        }
        this.listenables = actions

        this.initListen()
    }

    initListen() {
        this.$on('services/DropdownColor/state',(key, { isShow, style, index }) => {
            this.setState({
                isShow,
                style,
                index
            })
        })
    }

    onSelectItem(event, item) {
        event.preventDefault()
        event.stopPropagation()
        console.log('select')
        let index = this.state.index
        this.$emit('services/DropdownColor/item',{
            item,
            index
        })
    }

    onRemoveEvents() {
        this.clearEvents()
    }
}

export default Store



