import RootStore from 'core/Store'
// import * as api from 'core/api'

class Store extends RootStore {
    constructor(actions) {
        super()
        this.listenables = actions
        this.state = {
            isShow:false,
            teamList:[],
            teamSelect:[]
        }
        this.init()
    }

    init() {
        this.$on('services/TeamSelect/state',(key, { isShow }) => {
            console.log('services/TeamSelect/state',isShow )
            this.setState({
                isShow
            })
        })
    }

    onTestAction(value) {
        this.setState({
            test: value
        })
    }

    onRemoveEvents() {
        this.clearEvents()
    }


    getTeamList(){
        let teamId = this.cookie.get('teamId') || -1
        this.$ajax
            .get(this.api('project/team')(),{
                params: {}
            })
            .then( res => {
                if(res.status === 200){
                    let teams =  res.data.teams
                    let teamList = new Array()

                    for (let i=0; i<teams.length;i++){
                        var item = {
                            'isCheck': teams[i].teamId === teamId ? true  : false,
                            'key': i,
                            'teamId':teams[i].teamId,
                            'label': teams[i].teamName
                        }
                        teamList[i]=item
                    }

                    this.setState({
                        teamList
                    })
                }
            })
            .catch( error => {
                console.log('error:', error)
            })
    }

    onInsertTeamList(){
        if(this.state.teamList.length === 0) {
            this.getTeamList()
        }
    }

    onHandleCheck(list) {
        for(let i=0; i<list.length; i++){
            if(list[i].isCheck === true){
                /*this.$emit('services/TeamSelect/selectList', {
                 teamSelect: list[i]
                })*/
                this.setState({
                    teamSelect: list[i]
                })
            }
        }


    }

    onConfirm(){
        this.setState({
            isShow: false
        })
        let teamSelect = this.state.teamSelect,
            teamId = teamSelect.teamId
        this.$emit('pages/ProjectAdd/teamSelect', teamSelect)
        this.$emit('services/MemberSelect/teamId', {teamId})
        console.log(teamSelect.teamId)
    }

    onCancel(){
        this.setState({
            isShow: false
        })
    }
}

export default Store



