/**
 * React Static Boilerplate
 */

import Root from './Root'
import Mainer from 'pages/Mainer'
import childRoutes from 'src/routes'

const routes = {
    path: '/',
    component: Root,
    indexRoute: {
        component: Mainer,
        onEnter: ({ params }, replace) => replace('/project')
    },
    childRoutes: childRoutes
}

export default routes



