import React, { PropTypes } from 'react'
import Reflux from 'reflux'
import Im from 'immutable'
import Store from './store'
import Actions from './actions'
import './styles'

import ItemDot from 'widgets/ItemDot'
import PullUp from 'widgets/PullUp'
import Button from 'widgets/Button'
import ItemButton from 'widgets/ItemButton'
import DropdownMore from 'services/DropdownMore'
import ModalDate from 'services/ModalDate'
import ModalComplete from 'services/ModalComplete'
import ModalMember from 'services/ModalMember'

class ProjectTaskListPage extends React.Component {
    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store(this.actions)
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })
        this.tags = {
            a:"进度安排",
            b:"出图提醒",
            c:"会议通知",
            d:"外部条件",
            e:"内部协调"
        }
        this.colos = {
            a:"red",
            b:"blue",
            c:"violet",
            d:"yellow",
            e:"purple",
            f:"green",
            h:"grey"
        }
    }

    componentDidMount() {

    }

    componentWillUnmount() {
        // no del
        this.actions.removeEvents()
    }

    shouldComponentUpdate(nextProps,  nextState){
        console.log("dddd====",Im.is(Im.fromJS(nextState),Im.fromJS(this.state)))
        //return Im.is(Im.fromJS(nextState),Im.fromJS(this.state))
        //console.log('nextState', nextState)
        return nextState.isShow !== this.state.isShow || !Im.is(Im.fromJS(this.state.params),Im.fromJS(nextState.params))
        //return nextState.isShow !== this.state.isShow || this.state.params !== nextState.params
    }

    renderItem = (item, index) => {
        return (
            <ItemDot
                key={ index }
                style={ this.colos[item.taskType]}
                title={ item.taskContent}
                name={ item.project }
                type={ item.subproject }
                tag={ this.tags[item.taskType] }
                message={ item.taskReply }
                date={item.taskTime}
                btnMore={
                    <Button
                        icon="more"
                        type="icon"
                        onClick={ (event)=> { this.actions.moreToggle(event, item) }}
                    />
                }
            />
        )
    }

    render() {
        return (
            <div className="root-page-projecttasklist">
                <PullUp
                    api={ this.state.api }
                    params={ this.state.params }
                    dataField="schedules"
                    renderItem={ this.renderItem }
                    onScroll={ this.actions.handleScroll }
                >
                    <ItemButton label="创建新任务" onDetail={ () => { this.actions.add()  } } />
                </PullUp>
                <DropdownMore />
                <ModalDate />
                <ModalComplete />
                <ModalMember />
            </div>
        )
    }

}

export default ProjectTaskListPage



