import React, { PropTypes } from 'react'
import Store from './store'
import Actions from './actions'
import './styles'

import Icon from 'widgets/Icon'
import Modal from 'widgets/Modal'

class ModalTimePage extends React.Component {
    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store(this.actions)
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })
    }

    componentDidMount() {
        this.actions.didMount()
    }

    componentWillUnmount() {
        // no del
        this.actions.removeEvents()
    }

    renderTimeList() {
        return (
           <ul className="time-list">
               {
                   this.state.timeList.map( (item, index) => (
                       <li key={ index }
                           className={ item.isCheck ? 'current' : ''}
                           onClick={ () => {this.actions.handleCheck(item, index) }  }
                       >
                           <span className="icon-box"><Icon icon="tick" /></span>
                           <span className="label">{ item.label }</span>
                       </li>
                   ))
               }
           </ul>
        )
    }

    render() {
        return (
            <div className="root-page-modaltime">
                <Modal isShow={ this.state.isShow }
                       onConfirm={()=> { this.actions.confirm() }}
                >
                    { this.renderTimeList()}
                </Modal>
            </div>
        )
    }

}

export default ModalTimePage



