import React from 'react'
import ReactDOM from 'react-dom'
import { Router, browserHistory } from 'react-router'
import routes from 'core/routes'
import Ajax from 'core/Ajax'
import axios from 'axios'
import Cookie from 'js-cookie'

//window.location.href = 'http://www.chenlinallen.com/login/getOauth/'
//Cookie.set('openId','dev-chenlin') //chenbilin dev-chenlin

new Ajax()

var $_GET = (function(){
    var url = window.document.location.href.toString();
    var u = url.split("?");
    if(typeof(u[1]) == "string"){
        u = u[1].split("&");
        var get = {};
        for(var i in u){
            var j = u[i].split("=");
            get[j[0]] = j[1];
        }
        return get;
    } else {
        return {};
    }
})();

if(!Cookie.get('openId')) {
    window.location.href = 'http://www.chenlinallen.com/login/getOauth?state=http://www.chenlinallen.com'
    let openId = $_GET['openId']
    if(openId) {
        Cookie.set('openId', openId)
    }
}


ReactDOM.render(
    <Router routes={routes} history={ browserHistory } />,
    document.getElementById('wrapper')
)



