import React, { PropTypes } from 'react'
import Reflux from 'reflux'
import Store from './store'
import Actions from './actions'
import './styles'

import TeamHeader from 'services/TeamHeader'
import Mainer from 'layouts/Mainer'

class TeamTaskPage extends React.Component {
    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store(this.actions)
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })

         this.tabs = [{
            link: '/team/task/people',
            name: 'people',
            label: '人员'
        },{
            link: '/team/task/project',
            name: 'project',
            label: '项目'
        }]
    }

    componentDidMount() {
        this.actions.setCurrentRoute(this.tabs)
    }

    componentWillUnmount() {
        // no del
        this.actions.removeEvents()
    }

    render() {
        return (
            <div className="root-page-teamtask">
               <TeamHeader 
                    tabs={this.tabs}
                    current={ this.state.current }
                    onHandleClick={ () => { this.actions.setCurrentRoute(this.tabs) } }
                />
                <Mainer paddingTop paddingBottom>
                    { this.props.children }
                </Mainer>
            </div>
        )
    }

}

export default TeamTaskPage



