import RootStore from 'core/Store'

class Store extends RootStore {
    constructor(actions) {
        super()
        this.state = {
            value: ''
        }
        this.listenables = actions

        this.init()
    }

    init() {

    }

    onHandleChange = (e, value) => {
        console.log(value)
        this.setState({
            value
        })
    }

    onRemoveEvents() {
        this.clearEvents()
    }

}

export default Store



