import RootStore from 'core/Store'
import utils from 'core/utils'

class Store extends RootStore {
    constructor(actions) {
        super();
        this.state = {
            isShow: false,
            moreId: 0,
            api: this.api('team/task/people/statistics')(),
            params:{
                "currentPageNumber": 1,
                "pageSize": 10,
                "teamId": this.cookie.get('teamId'),
                "openId": this.cookie.get('openId'),
                //"memberOpenId": this.getMemberOpenId(),
                "startTime": utils.getOneMonth('startTime'),
                "endTime":  utils.getOneMonth('endTime')
            }
        }
        this.listenables = actions

        this.init()
    }

    init() {

    }

    onDidMount() {
        let currentTeamTaskType = this.cookie.get('currentTeamTaskType')
        if(currentTeamTaskType === 'people') {
            this.setState({
                api: this.api('team/task/people/statistics')(),
                params: Object.assign({}, this.state.params, {
                    "currentPageNumber": 1,
                    "memberOpenId": this.getMemberOpenId()
                })
            })
        } else if(currentTeamTaskType === 'project') {
            this.setState({
                api: this.api('team/task/project/statistics')(),
                params:{
                    "currentPageNumber": 1,
                    "subprojectId":this.getMemberOpenId(), //（子项目id）
                    "scheduleType":"", //（任务类型，即标签，a.合同备案进度、b.付款进度、c.分配进度、d.计划进度、e.近期出图回文、f.近期会议组织、g.外部设计条件、h.内部落实、i.总结交流）
                }
            })
        }
    }

    getMemberOpenId() {
        let pathname = this.$route.getCurrentLocation().pathname
        let params = pathname && pathname.split('/statistics/')[1]
        return params
    }


    onMoreToggle(event, task) {
        console.log("task",task)
        event.preventDefault()
        event.stopPropagation()
        let {left, top, width, height} = event.target.getBoundingClientRect(),
            moreId = task.scheduleId,
            subprojectId = task.subprojectId

        let style = {
            left: (left + width)+ "px",
            top: (top + height) +"px",
            transform: "translateX(-100%)"
        }

        if(moreId === this.state.moreId){
            let isShow = !this.state.isShow
            this.setState({
                isShow
            })
            this.$emit('services/DropdownMore/state', {
                isShow,
                style,
                subprojectId
            })
        } else {
            this.setState({
                isShow: true,
                moreId
            })
            //console.log('services/DropdownMore/state',moreId,style)
            this.$emit('services/DropdownMore/state', {
                isShow : true,
                moreId,
                task,
                style
            })
        }
    }

    onCreateTask() {
        this.$route.push('/mine/schedule/add')
    }

    onRemoveEvents() {
        this.clearEvents()
    }
}

export default Store



