import RootStore from 'core/Store'

class Store extends RootStore {
    constructor(actions) {
        super();
        //this.listenToMany(actions);
        this.state = {
            test:'default'
        };
        this.listenables = actions;

    }

    onGoBack = event => {
        event.preventDefault()
        this.$route.goBack()
    }

    onTestAction() {
        this.test()
        this.setState({
            test:'wujian'
        })

        this.add('about/add',{
            "aaa":"bbb"
        })

    }

    test() {
        console.log('test function')
    }
}

export default Store;


