import React, { PropTypes } from 'react'
import Store from './store'
import Actions from './actions'
import './styles'
import { Link } from 'react-router'

import RoundSearch from 'services/RoundSearch'
import Icon from 'widgets/Icon'
import Checkbox from 'widgets/Checkbox'
import Button from 'widgets/Button'
import PullUp from 'widgets/PullUp'
import TeamSelect from 'services/TeamSelect'
import ModalTime from 'services/ModalTime'

class TeamPeopleContentPage extends React.Component {
    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store(this.actions)
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })

        this.max = 0
    }

    componentDidMount() {
        this.actions.didMount()
    }

    componentWillUnmount() {
        // no del
        this.actions.removeEvents()
    }


    renderTop() {
        return (
            <div className="reports-top">
                <div className="top-nav">
                    <div className="top-nav-team">
                        <span>{ this.state.currentTeam.teamName}</span>
                        <Button
                            icon="show"
                            type="icon"
                            className="right"
                            onClick={ (event)=> { this.actions.toggleTeam(event) }}
                        >
                        </Button>
                    </div>
                    <div className="top-nav-search">
                        <RoundSearch />
                    </div>
                </div>
                <div className="time-box" onClick={ () => { this.actions.selectTime() }}>
                    <Icon icon="time" />
                    <span> 时间区间：{this.state.time.label}</span>
                </div>
            </div>
        );
    }

    response = (data) => {
        this.max = data.max
    }

    renderItem = (item, index) => {
        let max = this.max;
        item.percentagetOverTime = max ? (item.taskOverTime / max * 100) : 0
        item.percentagetUnfinished = max ? (item.taskUnfinished / max * 100) : 0
        return (
            <div className="item" key={'r_' + index}>
                <span className="label">{ item.memberName }</span>
                <div className="data-box" onClick={ ()=> this.actions.detail(item.memberOpenId) }>
                    <Link>
                        <p className="timeout" style={{width: item.percentagetOverTime + '%' }} >{item.taskOverTime ? item.taskOverTime : ''}</p>
                        <p className="undone" style={{width: item.percentagetUnfinished + '%' }} >{item.taskUnfinished ? item.taskUnfinished : ''}</p>
                    </Link>
                </div>
            </div>
        )
    }

    renderBottom() {
        return (
            <div className="reports-bottom">
                <div className="tag-box">
                    <p>已超期</p>
                    <p>未完成</p>
                </div>
            </div>
        )
    }

    render() {
        return (
            <div className="root-page-teampeoplecontent">
                <PullUp
                    dataField="data"
                    api={ this.state.api }
                    params={ this.state.params }
                    renderItem={ this.renderItem }
                    response={ this.response }
                >
                    { this.renderTop() }
                    { this.renderBottom()}
                </PullUp>
                <TeamSelect />
                <ModalTime />
            </div>
        )
    }

}

export default TeamPeopleContentPage



