/**
 * ItemDot
 */

import React, { PropTypes } from 'react'
import classnames from 'classnames'

import Icon from 'widgets/Icon'

class ItemDot extends React.Component {
    static propTypes = {
        className: PropTypes.string,
        //itemData: PropTypes.object
        btnHelp: PropTypes.any,
        style: PropTypes.string,
        title: PropTypes.string,
        name: PropTypes.string,
        type: PropTypes.string,
        tag: PropTypes.string,
        message: PropTypes.string,
        date: PropTypes.string
    }

   /* static defaultProps = {
        style: "",
        title: "",
        name: "",
        type: "",
        tag: "",
        message: "",
        date: ""
    }
*/
    constructor(props) {
        super()
        this.state = {
            style: props.style,
            title: props.title,
            name: props.style,
            type: props.type,
            tag: props.tag,
            message: props.message,
            date: props.date
        }
    }

    componentDidMount() {

    }

    componentWillReceiveProps({ style, title, name, type, tag, message, date }) {
        this.setState({
            style,
            title,
            name,
            type,
            tag,
            message,
            date
        })
    }

    componentWillUnmount() {

    }

    render() {
        const { className } = this.props
        let { style, title, name, type, tag, message, date } = this.state
        return (
            <div className={ classnames('root-widget-itemdot',style,className) } >
                <dl>
                    <dt>
                        <div className="title">
                            <div className="left icon-dot"></div>
                            {
                                this.props.btnHelp &&
                                <div className="right btn-help">{ this.props.btnHelp }</div>
                            }
                            <div className="auto">{ title }</div>
                        </div>
                        <p>{ name }</p>
                        <p>{ type }</p>
                    </dt>
                    <dd>
                        <Icon icon="tag" />
                        <p>{ tag }</p>
                    </dd>
                    <dd>
                        <Icon icon="message" />
                        <p>{ message }</p>
                    </dd>
                    <dd className="date">
                        <Icon icon="clock" />
                        <p>{ date }</p>
                    </dd>
                </dl>
                {
                    this.props.btnMore &&
                    <div className="btn-more">{ this.props.btnMore }</div>
                }
            </div>
        )
    }
}

export default ItemDot;



