import RootStore from 'core/Store'

class Store extends RootStore {
    constructor(actions) {
        super()
        this.state = {
            test:'default'
        }
        this.listenables = actions

        this.initListen()
    }

    initListen() {
        this.$on('services/DropdownTags/item', (key, { item })=> {
            console.log('item',item)
        })
    }

    onToggle(event) {
        let {left, top, width, height} = event.target.getBoundingClientRect()
        let style = {
            left: left+ "px",
            top: (top + height) +"px"
        }
        let isShow = !this.state.isShow
        this.setState({
            isShow
        })
        this.$emit('services/DropdownTags/state', {
            isShow,
            style
        })
    }


    onTestAction(value) {
        this.setState({
            test:`change ${value} value`
        })
    }

    onRemoveEvents() {
        this.clearEvents()
    }

}

export default Store



