import { createActions } from 'reflux'

class Actions {
    constructor() {
        return createActions([
            'removeEvents',
            'handleChange',
            'add',
            'handleDel',
            'handleAdd',
            'handleEdit',
            'didMount'
        ])
    }
}

export default Actions



