import RootStore from 'core/Store'
import * as api from 'core/api'

class Store extends RootStore {
    constructor(actions) {
        super();
        this.state = {
            data: [],
            peopleParams: {
                "currentPageNumber": 1,
                "pageSize": 9999,
                "openId": this.cookie.get('openId'),
                "teamId": JSON.parse(this.cookie.get('currentTeam')).teamId,
                "memberName": "",
                "startTime": "2017-03-02",
                "endTime": "2017-04-02"
            },
            projectParams: {
                "currentPageNumber": 1,
                "pageSize": 9999,
                "openId": this.cookie.get('openId'),
                "teamId": JSON.parse(this.cookie.get('currentTeam')).teamId,
                "subprojectNames": "",
                "subprojectStatus": "",
                "startTime": "2017-03-02",
                "endTime": "2017-04-02"
            },
            type: 'people',
            currentTeam: {
                teamName: '',
                teamId: 0
            }
        }
        this.listenables = actions
        this.init()
        this.timer = null
    }

    init() {

        this.setState({
            currentTeam: JSON.parse(this.cookie.get('currentTeam'))
        })

        this.$on('components/services/TeamContent/type', (key, { type }) => {
            this.setState({
                type
            })
            this.timer = setTimeout( () => {
                if (type === 'people') {
                    this.getPeopleList()
                } else if (type === 'project') {
                    this.getProjectList()
                }
            },0)

        })

        this.$on('components/services/RoundSearch/teamPeople', (key, {value}) => {
            this.setState({
                peopleParams: Object.assign({},this.state.peopleParams,{
                    memberName: value
                })
            })
            this.getPeopleList()
            // console.log('search teamPeople', value)
        })

        this.$on('components/services/RoundSearch/teamProject', (key, {value}) => {
            // console.log('search teamProject', value)
            this.setState({
                projectParams: Object.assign({},this.state.projectParams,{
                    subprojectNames: value
                })
            })
            this.getProjectList()
        })

        this.$on('pages/ProjectAdd/teamSelect', (key, teamSelect) => {
            let currentTeam = {
                teamName: teamSelect.label,
                teamId: teamSelect.teamId
            }
            this.setState({
                currentTeam,
                peopleParams: Object.assign({},this.state.peopleParams,{
                    teamId: teamSelect.teamId
                }),
                projectParams: Object.assign({},this.state.projectParams,{
                    teamId: teamSelect.teamId
                })
            })
            this.cookie.set('currentTeam', JSON.stringify(currentTeam))
            //this.getPeopleList()
        })
    }

    getPeopleList() {
        this.$ajax.get(this.api('team/task/people')(), {
            params: this.state.peopleParams
        }).then(res => {
            let data = res.data.data
            let tmp = []
            data.map(item => {
                item.link = '/team/taskDetail/analysis'
                console.log('i', item)
                tmp.push(item.taskOverTime + item.taskUnfinished)
                return item
            })
            let max = Math.max(...tmp)
            data.map(item => {
                item.percentagetOverTime = item.taskOverTime / max * 100
                item.percentagetUnfinished = item.taskUnfinished / max * 100
                return item
            })

            //console.log(tmp,   Math.max(...tmp))

            this.setState({
                data
            })
            console.log('team/task/people', res)
        }).catch(error => {

        })
    }

    getProjectList() {
        this.$ajax.get(this.api('team/task/project')(), {
            params: this.state.projectParams
        }).then(res => {
            let data = res.data.data
            let tmp = []
            data.map(item => {
                item.link = '/team/taskDetail/statistics'
                tmp.push(item.taskOverTime + item.taskUnfinished)
                return item
            })

            let max = Math.max(...tmp)
            data.map(item => {
                item.percentagetOverTime = item.taskOverTime / max * 100
                item.percentagetUnfinished = item.taskUnfinished / max * 100
                return item
            })

            this.setState({
                data
            })
            console.log('team/task/people', res)
        }).catch(error => {

        })
    }

    onToggleTeam(event) {
        event.stopPropagation()
        let isShow = true
        this.setState({
            isShow
        })
        this.$emit('services/TeamSelect/state',{
            isShow
        })

    }

    onToggleFilter(event) {
        console.log('click onToggleFilter')
    }

    onRemoveEvents() {
        clearTimeout(this.timer)
        this.clearEvents()
    }
}

export default Store



