import React, { PropTypes } from 'react'
import Reflux from 'reflux'
import Store from './store'
import Actions from './actions'
import './styles'

// test TeamReportsContent
import TeamReportsContent from '../index'

class Demo extends React.Component {
    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store(this.actions)
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })

        this.data = [{
            label: '张三',
            data: {
                timeout: 5,
                undone: 8,
                link: '/'
            }
        }, {
            label: '李四',
            data: {
                timeout: 4,
                undone: 7,
                link: '/'
            }
        }]
    }

    componentDidMount() {

    }

    componentWillUnmount() {
        this.actions.removeEvents()
    }

    render() {
        return (
            <div className="root-widget-teamreportscontent__demo__">
                <TeamReportsContent data={this.data} />
            </div>
        )
    }
}

export default Demo



