import RootStore from 'core/Store'

class Store extends RootStore {
    constructor(actions) {
        super();
        this.state = {
            listData:[/*
                {
                    id:7,
                    number: 7,
                    title: "天气好，不迟到",
                    isCurrent: false
                },
                {
                    id:6,
                    number: 6,
                    title: "天气好，不迟到",
                    isCurrent: false
                },
                {
                    id:5,
                    number: 5,
                    title: "天气好，不迟到",
                    isCurrent: false
                },
                {
                    id:4,
                    number: 4,
                    title: "天气好，不迟到",
                    isCurrent: false
                },
                {
                    id:3,
                        number: 3,
                    title: "天气好，不迟到",
                    isCurrent: false
                },
                {
                    id:2,
                        number: 2,
                    title: "天气好，不迟到",
                    isCurrent: false
                },
                {
                    id:1,
                        number: 1,
                    title: "天气好，不迟到",
                    isCurrent: false
                }
            */],
            params: {
                openId: this.cookie.get('openId'), //this.cookie.get('openId')
                currentPageNumber: 1,
                pageSize: 20
            },
            maxRecordId: 0,
            currentRecord: {
                openId: this.cookie.get('openId'),
                content: "",
                recordId: ""
            },
            saveType: null
        }
        this.listenables = actions

        this.isSave = true
        this.isDelete = true

        this.initListen()
    }

    onDidMount() {
        this.initRecordList()
    }

    initRecordList() {
        this.$ajax.get(this.api('/record/list')(),{
            params: this.state.params
        }).then( res => {
            let recordList = res.data.recordList
            let recordIds = []
            recordList.map( (item, index) => {
                item.isCurrent = false
                item.number = index + 1
                item.moveX = 0
                return item
            })
            recordList.forEach( v => {
                recordIds.push(v.recordId)
            })

            this.setState({
                listData: recordList,
                maxRecordId: Math.max(...recordIds)
            })

        }).catch( error => {

        })
    }

    initListen() {
        this.$on('pages/Note/save',() => {
            if(!this.isSave) return false

            if(this.state.currentRecord.content === ''){
                alert('不能为空!')
                return false
            }
            this.setState({
                listData: this.state.listData.map((item, index) => {
                    if(item.isCurrent){
                        if(item.content !== ''){
                            item.isCurrent = false
                        }
                    }
                    return item
                })
            })


            if(this.state.currentRecord.openId && this.state.currentRecord.content ){
                this.isSave = false
                if(this.state.saveType === 'add'){
                    this.$ajax.post(
                        this.api('/record/add')(),
                        this.state.currentRecord
                    ).then( res => {
                        this.isSave = true
                        this.initRecordList()
                        console.log('add success' ,res)
                    }).catch( error => {
                        this.isSave = true
                    })
                } else{
                    this.$ajax.post(
                        this.api('/record/edit')(),
                        this.state.currentRecord
                    ).then( res => {
                        this.isSave = true
                        console.log('edit success' ,res)
                    }).catch( error => {
                        this.isSave = true
                    })
                }


            }

            console.log('currentRecord', this.state.currentRecord)
        })
    }

    onAdd() {
        let listData = this.state.listData,
            length = listData.length,
            currentRecordId = this.state.maxRecordId + 1
        console.log('maxRecordId=============', currentRecordId)
        listData.unshift({
            number: length + 1,
            recordId: currentRecordId,
            content: "",
            isCurrent: true
        })

        console.log("ddd======",listData)

        this.setState({
            listData,
            saveType: 'add',
            currentRecord: Object.assign({}, this.state.currentRecord, {
                content: ""
            })
        })

        console.log('onAdd')
    }

    onHandleAdd(recordId) {
        this.cookie.set('recordId', recordId)
        this.$route.push('/mine/schedule/add')
    }

    onHandleDel(recordId, index) {
        //arr.splice(1,1);
        console.log('onHandleDel')
        if(!this.isDelete) return false
        this.isDelete = false
        this.$ajax.post(
            this.api('/record/del')(),
            {
                recordId
            }
        ).then( res => {
            this.isDelete = true
            let listData = this.state.listData
            listData.splice(index,1)
            this.setState({
                listData
            })

        }).catch( error => {
            this.isDelete = true
        })
    }

    onHandleEdit(recordId, index) {
        console.log('onHandleEdit', recordId, index)
        this.setState({
            listData: this.state.listData.map((item, index) => {
                if (item.recordId === recordId) {
                    item.isCurrent = true
                } else {
                    item.isCurrent = false
                }
                return item
            }),
            currentRecord: Object.assign({}, this.state.currentRecord, {
                content: this.getCurrentContent(recordId)
            })
        })
    }

    getCurrentContent(recordId) {
        let content = ''
        this.state.listData.forEach( item => {
            if(recordId === item.recordId){
                content = item.content
            }
        })
        return content
    }

    onHandleChange(e, recordId) {
        let { value } = e.target
        this.setState({
            listData: this.state.listData.map((item, index) => {
                if (item.recordId === recordId) {
                    item.content = value
                }
                return item
            }),
            currentRecord:  Object.assign({}, this.state.currentRecord, {
                recordId,
                content: value,
            })
        })
    }

    onRemoveEvents() {
        this.clearEvents()
    }
}

export default Store



