import { createActions } from 'reflux'

class Actions {
    constructor() {
        return createActions([
            'removeEvents',
            'testAction' ,
            'handleChange',
            'handleClick',
            'handleSave',
            'handleCheck',
            'handleCancel',
            'projectToggle',
            'subprojectToggle',
            'memberToggle',
            'tagToggle',
            'confirm',
            'cancel'
        ])
    }
}

export default Actions



