import React, { PropTypes } from 'react'
import Reflux from 'reflux'
import Store from './store'
import Actions from './actions'
import './styles'

//component
import HeaderTab from 'widgets/HeaderTab'

class TeamHeaderPage extends React.Component {
    static propTypes = {
        onHandleClick: PropTypes.func,
        tabs: PropTypes.array,
        current: PropTypes.string,
    }
    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store()
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })

    }

    componentDidMount() {

    }

    componentWillReceiveProps({current}) {
        this.setState({
            current
        })
    }

    componentWillUnmount() {
        // no del
        this.actions.removeEvents()
    }

    render() {
        return (
            <div className="root-page-teamheader">
                <HeaderTab
                    className="team-header"
                    tab={ this.props.tabs }
                    onHandleClick={() => { this.props.onHandleClick() }}
                    current={this.props.current}
                />
            </div>
        )
    }

}

export default TeamHeaderPage



