import RootStore from 'core/Store'
import * as api from 'core/api'

class Store extends RootStore {
    constructor(actions) {
        super();
        this.state = {
            isDisplay:false,
            isShow: false,
            current:'all',
            userList: [
                {
                    name: 'design',
                    title: '设计',
                    users: [
                        {
                            id: 1,
                            label:'王五',
                            isCheck: false
                        },
                        {
                            id: 2,
                            label:'王五',
                            isCheck: false
                        },
                        {
                            id: 3,
                            label:'王五',
                            isCheck: false
                        },
                        {
                            id: 4,
                            label:'王五',
                            isCheck: false
                        },
                        {
                            id: 5,
                            label:'王五d',
                            isCheck: false
                        }
                    ]
                },
                {
                    name: 'frontend',
                    title: '前端',
                    users: [
                        {
                            id: 1,
                            label:'王五',
                            isCheck: false
                        },
                        {
                            id: 2,
                            label:'王五',
                            isCheck: false
                        },
                        {
                            id: 3,
                            label:'王五',
                            isCheck: false
                        },
                        {
                            id: 4,
                            label:'王五',
                            isCheck: false
                        },
                        {
                            id: 5,
                            label:'王五',
                            isCheck: false
                        },
                        {
                            id: 6,
                            label:'王五',
                            isCheck: false
                        },
                        {
                            id: 7,
                            label:'王五',
                            isCheck: false
                        }
                    ]
                },
                {
                    name: 'frontend',
                    title: '前端',
                    users: [
                        {
                            id: 1,
                            label:'王五',
                            isCheck: false
                        },
                        {
                            id: 2,
                            label:'王五',
                            isCheck: false
                        },
                        {
                            id: 3,
                            label:'王五',
                            isCheck: false
                        },
                        {
                            id: 4,
                            label:'王五',
                            isCheck: false
                        },
                        {
                            id: 5,
                            label:'王五',
                            isCheck: false
                        }
                    ]
                }
            ]
        }
        this.listenables = actions

        this.init()

        this.timer = null
    }

    init() {
        this.$on('services/MaskUserAssign/state',() => {
            this.setState({
                isDisplay: true
            })
            this.timer = setTimeout(()=> {
                this.setState({
                    isShow: true
                })
                clearTimeout(this.timer)
            },10)
        })
    }

    onClose() {
        this.setState({
            isShow: false
        })
        this.timer = setTimeout(()=> {
            this.setState({
                isDisplay: false
            })
            clearTimeout(this.timer)
        },500)
    }

    onHandleSelect(current) {
        this.setState({
            current
        })
    }

    onRemoveEvents() {
        this.clearEvents()
    }
}

export default Store



