import React, { PropTypes } from 'react'
import Reflux from 'reflux'
import Store from './store'
import Actions from './actions'
import './styles'

// test PullUp
import PullUp from '../index'
import ItemThumb from 'widgets/ItemThumb'
import ItemButton from 'widgets/ItemButton'

class Demo extends React.Component {
    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store(this.actions)
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })
    }

    componentDidMount() {

    }

    componentWillUnmount() {
        this.actions.removeEvents()
    }

    handleInfiniteLoad = (refresh) => {
        setTimeout( ()=> {
            refresh()
        },2000)
    }

    renderItem = (item, index) => {
        return (
            <ItemThumb
                key={ index }
                thumb={
                        <div style={{
                        'height':'100%',
                        'backgroundColor': item.color
                        }}></div>
                    }
                title={ item.title }
                desc={ item.desc }
            />
        )
    }

    render() {
        return (
            <div className="root-widget-pullup__demo__">
                <PullUp
                    fixedTop fixedBottom
                    onInfiniteLoad={ this.handleInfiniteLoad }
                    data={ this.state.listData }
                    renderItem={ this.renderItem }
                >
                    <ItemButton label="创建新项目" onClick={ () => {  } } />
                </PullUp>
            </div>
        )
    }
}

export default Demo



