import React, { PropTypes } from 'react'
import Reflux from 'reflux'
import classnames from 'classnames'
import Store from './store'
import Actions from './actions'
import './styles'

class DatePickerPage extends React.Component {
    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store(this.actions)
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })
    }

    componentDidMount() {
        this.actions.didMount()
    }

    componentWillUnmount() {
        // no del
        this.actions.removeEvents()
    }

    render() {
        return (
            <div className="root-page-datepicker">
                <div className="root-page-datepicker-header">
                    <div className="btn-prev" onClick={ () => { this.actions.handleCheckMonth('prev') }}>prev</div>
                    <div className="btn-next" onClick={ () => { this.actions.handleCheckMonth('next') }}>next</div>
                    <div className="label-auto">
                        <p>{ `${this.state.date.year}.${this.state.date.month}.${this.state.date.day}` }</p>
                    </div>
                </div>

                <dl>
                    {
                        this.state.weekList && false &&
                        this.state.weekList.map((item, index) => {
                            return (
                                <dt key={ index }
                                    className={ classnames({'current': item.isCurrent}) }
                                >
                                    <span>{ item.label }</span>
                                </dt>
                            )
                        })
                    }
                    {
                        this.state.dateList &&
                        this.state.dateList.map((item, index) => {
                            return (
                                <dd key={ index }
                                    className={ classnames({
                                        //'is-month': item.isMonth,
                                        'is-date': item.day == this.state.today,
                                        'is-task': item.isTask === 'true'
                                    }) }
                                    onClick={ () => { this.actions.showDetail(item.taskTime)} }
                                >
                                    <span>{ item.day }</span>
                                </dd>
                            )
                        })
                    }
                </dl>
            </div>
        )
    }

}

export default DatePickerPage



