import RootStore from 'core/Store'

class Store extends RootStore {
    constructor(actions) {
        super()
        this.listenables = actions
        this.state = {
            isShow: false
        }
        this.init()
        this.initListen()
    }

    init() {
        console.log(this.state.isShow)
    }

    initListen() {
        /*this.$on('services/TeamSelect/selectList', (key, { selectList }) => {
            console.log('selectList', selectList)
        })*/

    }

    onTestAction(value) {
        this.setState({
            test:`change ${value} value`
        })
    }

    onRemoveEvents() {
        this.clearEvents()
    }

    onToggle(event) {
        event.stopPropagation()
        let isShow = true
        this.setState({
            isShow
        })
        this.$emit('services/TeamSelect/state',{
            isShow
        })

    }

}

export default Store



