import RootStore from 'core/Store'
import utils from 'core/utils'

class Store extends RootStore {
    constructor(actions) {
        super();
        this.state = {
            isShow: false,
            style:{},
            list: [/*
                {
                    key: 'type-1',
                    label: '设计',
                    isCheck: false
                },{
                    key: 'type-2',
                    label: '前端',
                    isCheck: false
                },{
                    key: 'type-3',
                    label: '后台',
                    isCheck: false
                }*/
                {
                    key: 'a',
                    label: '进度安排',
                    isCheck: false
                },{
                    key: 'b',
                    label: '出图提醒',
                    isCheck: false
                },{
                    key: 'c',
                    label: '会议通知',
                    isCheck: false
                },{
                    key: 'd',
                    label: '外部条件',
                    isCheck: false
                },{
                    key: 'e',
                    label: '内部协调',
                    isCheck: false
                }
            ]
        }
        this.listenables = actions

        this.initListen()
    }

    initListen() {
        this.$on('services/DropdownType/state', (key, { isShow, style }) => {
            this.setState({
                isShow,
                style
            })
            if(!isShow) {
                this.setState({
                    list: this.state.list.map( v => {
                        v.isCheck = false
                        return v
                    })
                })
                this.$emit('services/ProjectTaskList/reset', {})
            }
        })
    }

    onHandleCheck(list) {
        let selectItem = {}
        list.forEach((item) => {
            if(item.isCheck) {
                selectItem = item
            }
        })
        if(!utils.isEmptyObject(selectItem)){
            this.$emit('services/DropdownType/selectItem', selectItem)
        }
    }

    onRemoveEvents() {
        this.clearEvents()
    }

}

export default Store



