import RootStore from 'core/Store'
import * as api from 'core/api'

import ItemThumb from 'widgets/ItemThumb'

class Store extends RootStore {
    constructor(actions) {
        super();
        this.state = {
            isShow: false,
            inputValue: '',
            isSearch: false
        }
        this.listenables = actions

        this.initListen()
    }

    initListen() {
        this.$on('services/DropdownSteps/selectList', (key, { selectList }) => {
            console.log('selectList', selectList)
        })
    }

    onEnter(event) {
        let inputValue = this.state.inputValue
        if(event.keyCode == 13) {
            this.$emit('services/ProjectHeader/search',{
                inputValue
            })
        }
    }

    onInputChange(e) {
        let inputValue = e.target.value
        this.setState({
            inputValue
        })
    }

    onSearch() {

        let isSearch = !this.state.isSearch
        this.setState({
            isSearch
        })
        if(!isSearch) {
            this.$emit('services/ProjectList/reset', {})
        }
    }

    onToggle(event) {
        let {left, top, width, height} = event.target.getBoundingClientRect()

        let style = {
            left: (left + width)+ "px",
            top: (top + height) +"px",
            transform: "translateX(-100%)"
        }

        let isShow = !this.state.isShow
        this.setState({
            isShow
        })
        this.$emit('services/DropdownSteps/state',{
            isShow,
            style
        })
    }

    onRemoveEvents() {
        this.clearEvents()
    }
}

export default Store



