import RootStore from 'core/Store'
import * as api from 'core/api'

class Store extends RootStore {
    constructor(actions) {
        super();
        this.state = {
            test:'default',
            subprojectId: 0,
            projectId: 0,
            /*formData: {
                "project":"",
                "teamId":"",
                "subprojects":[
                    {
                        "subproject":"",
                        "teamStatus":"前期工作",
                        "projectMembers": [
                            {
                                "openId":"",
                                "roleType":""
                            }
                        ]
                    }
                ]
            },*/
            formData: {
                "projectId":"",
                "project":"",
                "teamId":"",
                "openId":this.cookie.get('openId'),
                "subprojects":[
                    {
                        "subprojectId":"",
                        "subproject":"",
                        "teamStatus":"前期工作",
                        "projectMembers": [
                            {
                                "openId":"",
                                "roleType":""
                            }
                        ],
                        "removeList":[]
                    }
                ],
                "removeList":[]
            },
            inputs: [
                {
                    key: 'subprojects_0',
                    placeholder: "子项目名称:",
                    leftIcon: 'add',
                    rightIcon: 'add-user' ,
                    status:'',
                    value: ''
                }
            ],
            list: [
                {
                    key: 'color-1',
                    label: '前期工作',
                    color: '#c740fc'
                },{
                    key: 'color-2',
                    label: '方案设计',
                    color: '#f85f57'
                },{
                    key: 'color-3',
                    label: '初步设计',
                    color: '#41d119'
                },{
                    key: 'color-4',
                    label: '施工图设计',
                    color: '#02b0f9'
                },{
                    key: 'color-5',
                    label: '施工配合',
                    color: '#176ceb'
                },{
                    key: 'color-6',
                    label: '暂缓',
                    color: '#71757b'
                },{
                    key: 'color-7',
                    label: '完成',
                    color: '#fcff04'
                }
            ],
            memberSelects:[],
            dtCurrent: false,
            dtIndex:-1,
            curproject:[],
            project:''
        }
        this.listenables = actions

        this.init()
    }

    init() {
        let curproject = JSON.parse(this.cookie.get('curproject')),
            project = curproject.project,
            list = this.state.list,
            status = ''

        list.map((item, i) => {
            if (curproject.teamStatus == item.label){
                status = item.color
            }
        })
        console.log(curproject)
        let inputs = [
                {
                    key: 'subprojects_0',
                    placeholder:'',
                    leftIcon: 'color',
                    rightIcon:'add-user',
                    status:status,
                    value:curproject.subproject
                },
                {
                    key: 'subprojects_1',
                    placeholder: "子项目名称:",
                    leftIcon: 'add',
                    rightIcon: 'add-user' ,
                    status:'',
                    value: ''
                }
            ]

        this.setState({
            curproject,
            inputs,
            project
        })

        this.$on('pages/ProjectEdit/memberSelects',(key,{ memberSelects}  ) => {
            let subprojects = this.state.formData.subprojects
            memberSelects.map((item, i) => {
                console.log(memberSelects)
                memberSelects[i].map((v, j) => {
                    console.log(subprojects)
                    if(subprojects[i] == undefined){
                        this.onAddSub()
                    }
                    subprojects[i].projectMembers[j] = {
                        openId: v.openId,
                        roleType: v.roleType
                    }
                })
            })

            this.setState({
                memberSelects
            })
        })

        this.$on('services/DropdownColor/item', (key, {item, index}) => {
            console.log(item)
            let formData = this.state.formData,
                isShowstage = !this.state.isShowstage,
                inputs = this.state.inputs,
                list = this.state.list,
                dtCurrent = !this.state.dtCurrent

            formData.subprojects[index].teamStatus = item.label
            list.map((v, i) => {
                if(item.label == v.label){
                    inputs[index].status = v.color
                }
            })

            this.setState({
                formData,
                isShowstage,
                inputs,
                dtCurrent
            })
            this.$emit('services/DropdownColor/state',{
                isShow:isShowstage
            })
            console.log('formData', formData)
        })
    }

    onTestAction(value) {
        this.setState({
            test: value
        })
    }

    onRemoveEvents() {
        this.clearEvents()
    }

    onInsertInfo(){

    }

    onHandleCancel() {
        this.$route.goBack()
    }

    onHandleSave(){
        console.log('this.state.formData:',this.state.formData)
        let formData = this.state.formData,
            subprojects = formData.subprojects

        this.state.inputs.map((item, i) => {
            if(i<this.state.inputs.length - 1){
                subprojects[i].subproject = item.value
            }

        })
        subprojects[0].subprojectId = this.state.curproject.subprjectId
        formData = Object.assign({}, formData, {
            project: this.state.project,
            projectId: this.state.curproject.projectId,
            teamId:this.state.curproject.teamId,
            subprojects:subprojects
        })
        let params = Object.assign({}, formData , this.state.params)
        console.log('params',params)
        this.$ajax
            .post(this.api('project/update')(), params)
            .then( res => {
                console.log(Object.assign({}, formData , this.state.params))
                console.log(this.cookie.get('openId'))
                if(res.data.result === 'success'){
                    this.$route.push('/')
                }

            })
            .catch( error => {
                console.log('error:', error)
            })
    }

    onChangeProject(e, value){
        let project = this.state.project
        this.setState({
            project:value
        })
        console.log('project',project)
    }

    onChangeSubpro(e, value, index){
        let inputs = this.state.inputs
        inputs[index].value = value
        this.setState({
            inputs
        })
        console.log(inputs)
    }

    onStageToggle(event, index) {
        let {left, top, width, height} = event.target.getBoundingClientRect(),
            style = {
            left: left + "px",
            top: (top + height) +"px"
        },
            isShowstage = !this.state.isShowstage,
            dtCurrent = !this.state.dtCurrent
        console.log(dtCurrent)
        console.log(index)
        this.setState({
            isShowstage,
            dtCurrent,
            dtIndex:index
        })
        this.$emit('services/DropdownColor/state',{
            isShow:isShowstage,
            style,
            index
        })
    }

    onAddSub() {
        let inputs = this.state.inputs,
            formData = this.state.formData,
            index = inputs.length - 1
        console.log(index)
        inputs[index]['leftIcon'] = 'color'
        inputs[index + 1] = {
            key: 'subprojects_' + (index + 1),
            placeholder: "子项目名称:",
            leftIcon: 'add',
            rightIcon: 'add-user' ,
            value: ''
        }
        formData['subprojects'][index] = {
            "subproject":"",
            "teamStatus":"前期工作",
            "projectMembers": [
                {
                    "openId":"",
                    "roleType":""
                }
            ],
            "removeList":[]
        }

        this.setState({
            inputs,
            formData
        })
        console.log(formData)
    }

    getCurMembers(){
        this.$ajax
            .get(this.api('project/subMembers')(),{
                params: {subprojectId: this.state.subprojectId}
            })
            .then( res => {
                if(res.status === 200){
                    console.log(res)
                    this.$emit('services/MemberEdit/curMembers',{
                        curMembers: res.data.subprojectMembers
                    })
                }
            })
            .catch( error => {
                console.log('error:', error)
            })
    }

    onMemberToggle(event, index) {
        this.getCurMembers()
        let projectId = this.state.curproject.projectId,
            subprjectId = this.state.curproject.subprjectId
        event.stopPropagation()
        let isShow = true,
            memberSelects = this.state.memberSelects
        this.setState({
            isShow
        })
        this.$emit('services/MemberEdit/projectId',{
            projectId,
            subprjectId
        })
        this.$emit('services/MemberEdit/memberSelects',{
            isShow,
            index,
            memberSelects
        })
    }

    updateProject(){
        this.$ajax
            .post(this.api('project/edit')(),{

            })
            .then( res => {
                if(res.data.result === 'success'){
                    console.log(res)
                }
            })
            .catch( error => {
                console.log('error:', error)
            })
    }
}

export default Store



