import RootStore from 'core/Store'
import * as api from 'core/api'

class Store extends RootStore {
    constructor(actions) {
        super();
        this.state = {
            data: [],
            params: {
                "currentPageNumber": 1,
                "pageSize": 10,
                "teamId": this.cookie.get("teamId"),
                "subprojectNames": "",
                "subprojectStatus": "" //(可在筛选中对子项目状态进行筛选(前期工作，方案设计，初步设计，施工图设计，施工配合，暂缓，完成))
            },
            api: this.api('team/manpower')(),
            isShow: false,
            inputValue: '',
            isSearch: false
        }
        this.listenables = actions
        this.initListen()
    }

    initListen() {
        this.$on('services/DropdownSteps/selectItem', (key, selectItem) => {
            console.log('selectItem', selectItem)
            this.setState({
                params: Object.assign({}, this.state.params, {
                    subprojectStatus: selectItem.label
                })
            })
        })
    }

    onEnter(event) {
        let inputValue = this.state.inputValue

        if(event.keyCode == 13) {
            this.setState({
                params: Object.assign({}, this.state.params, {
                    subprojectNames: inputValue
                })
            })
        }
    }

    onInputChange(e) {
        let inputValue = e.target.value
        this.setState({
            inputValue
        })
    }

    onSearch() {

        let isSearch = !this.state.isSearch
        this.setState({
            isSearch
        })
        if(!isSearch) {
            this.setState({
                params: Object.assign({}, this.state.params, {
                    subprojectNames: ""
                })
            })
        }
    }

    onToggle(event) {
        let {left, top, width, height} = event.target.getBoundingClientRect()

        let style = {
            left: (left + width)+ "px",
            top: (top + height) +"px",
            transform: "translateX(-100%)"
        }

        let isShow = !this.state.isShow
        this.setState({
            isShow
        })
        this.$emit('services/DropdownSteps/state',{
            isShow,
            style
        })
    }

    onRemoveEvents() {
        this.clearEvents()
    }

}

export default Store



