import RootStore from 'core/Store'

class Store extends RootStore {
    constructor(actions) {
        super();
        this.state = {
            isShow: false,
            moreId: 0,
            api: this.api('project/task')(),
            params: {
                currentPageNumber: 1,
                pageSize: 5,
                projectId: this.getParmas('projectId'),
                subprojectId: this.getParmas('subprojectId')/*,
                taskType:"任务类型(a:进度安排;b:出图提醒；c:会议通知；d:外部条件；e:内部协调)",
                taskContent:"任务类容"*/
            }
        }
        this.listenables = actions
        this.timer = null
        this.init()
    }

    getParmas(type) {
        let pathname = this.$route.getCurrentLocation().pathname
        let params = pathname && pathname.split('/task/')[1].split('/')
        if(type === 'projectId') {
            return params[0]
        } else if(type === 'subprojectId') {
            return params[1]
        } else {
            return
        }
    }

    init() {
        this.initListen()
    }

    initListen() {

        this.$on('services/DropdownType/selectItem', (key, selectItem) => {
            this.setState({
                params: Object.assign({}, this.state.params, {
                    taskType: selectItem.key,
                    taskContent: null,
                    currentPageNumber: 1
                })
            })
        })

        this.$on('services/ProjectTaskHeader/search', (key, { inputValue }) => {
            this.setState({
                params: Object.assign({}, this.state.params, {
                    taskType: null,
                    taskContent: inputValue,
                    currentPageNumber: 1
                })
            })
        })

        this.$on('services/ProjectTaskList/reset', (key, {}) => {
            this.setState({
                params: Object.assign({}, this.state.params, {
                    taskType: null,
                    taskContent: null,
                    currentPageNumber: 1
                })
            })
        })

        this.$on('services/ProjectTaskList/delay',(key, {}) => {
            this.setState({
                params: this.state.params
            })
            //console.log('services/ProjectTaskList/delay',data)
        })
    }


    onMoreToggle(event, task) {
        console.log("task==",task)
        event.preventDefault()
        event.stopPropagation()
        let {left, top, width, height} = event.target.getBoundingClientRect(),
            moreId = task.scheduleId,
            subprojectId = task.subprojectId

        let style = {
            left: (left + width)+ "px",
            top: (top + height) +"px",
            transform: "translateX(-100%)"
        }

        if(moreId === this.state.moreId){
            let isShow = !this.state.isShow
            this.setState({
                isShow
            })
            this.$emit('services/DropdownMore/state', {
                isShow,
                moreId,
                subprojectId,
                task,
                style
            })
        } else {
            this.setState({
                isShow: true,
                moreId
            })
            //console.log('services/DropdownMore/state',moreId,style)
            this.$emit('services/DropdownMore/state', {
                isShow : true,
                moreId,
                subprojectId,
                task,
                style
            })
        }
    }

    onHandleScroll(event){
        this.setState({
            isShow: false
        })
        this.$emit('services/DropdownMore/state', {
            isShow : false
        })
    }

    onAdd() {
        this.$route.push('/mine/schedule/add')
    }

    onRemoveEvents() {
        clearTimeout(this.timer)
        this.clearEvents()
    }
}

export default Store



