import RootStore from 'core/Store'
import * as api from 'core/api'

class Store extends RootStore {
    constructor(actions) {
        super();
        this.state = {
            test:'default'
        }
        this.listenables = actions

        this.init()
    }

    init() {

    }

    onDidMount() {
        this.$emit('components/services/TeamContent/type',{
            type: 'teamProject'
        })
    }

    onRemoveEvents() {
        this.clearEvents()
    }

}

export default Store



