import React, { PropTypes } from 'react'
import Reflux from 'reflux'
import Store from './store'
import Actions from './actions'
import './styles'

import MineList from 'services/MineList'
import DatePicker from 'services/DatePicker'
import ItemButton from 'widgets/ItemButton'

class MineSchedulePage extends React.Component {
    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store(this.actions)
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })
    }

    componentDidMount() {

    }

    componentWillUnmount() {
        // no del
        this.actions.removeEvents()
    }

    render() {
        return (
            <div className="root-page-mineschedule">
                <MineList
                    params={ this.state.params }
                    api={ this.state.api }
                >
                    <DatePicker  />
                    <ItemButton label="创建新任务"  onDetail={ () => { this.actions.add()  } }/>
                </MineList>
            </div>
        )
    }

}

export default MineSchedulePage



