import RootStore from 'core/Store'
import * as api from 'core/api'

class Store extends RootStore {
    constructor(actions) {
        super()
        this.listenables = actions
        this.state = {
            isShow:false,
            memberList:[],
            memberSelect:[],
            memberSelects:[],
            curMembers:[],
            index:0,
            projectId:-1,
            subprojectId:-1,
            roleList: [
                {
                    roleType:'a',
                    isCurrent:true,
                    roleName:"A. 项目负责人"
                },{
                    roleType:'b',
                    isCurrent:false,
                    roleName:"B. 项目助理"
                },{
                    roleType:'c',
                    isCurrent:false,
                    roleName:"C. 设计人员"
                },{
                    roleType:'d',
                    isCurrent:false,
                    roleName:"D. 其他"
                }

            ]
        }
        this.init()
    }

    init() {
        /*this.$on('services/MemberEdit/teamId',(key,{ teamId }) =>{
            this.onInsertMemberList(teamId)
        })*/
        this.$on('services/MemberEdit/projectId',(key,{ projectId, subprjectId }) =>{
            this.setState({
                projectId,
                subprojectId:subprjectId
            })
        })

        this.$on('services/MemberEdit/memberSelects',(key, { isShow,index,memberSelects }) => {
            if (isShow){
                this.onInsertMemberList()
            }
            this.setState({
                isShow,
                index,
                memberSelects
            })
            console.log('memberselects',memberSelects)

        })
    }

    onTestAction(value) {
        this.setState({
            test: value
        })
    }

    onRemoveEvents() {
        this.clearEvents()
    }

    onInsertSubMembers(){
        let curMembers = this.state.curMembers

    }

    onInsertMemberList(){
        this.$ajax
            .get(this.api('project/member')(),{
                params: {
                    projectId: this.state.projectId,
                    subprojectId: this.state.subprojectId
                }
            })
            .then( res => {
                if(res.status === 200){
                    console.log(res)
                    let members =  res.data.members,
                        memberList = new Array(),
                        index = this.state.index

                    for (let i=0; i<members.length;i++){
                        var item = {
                            'isCheck': false,
                            'key': i,
                            'openId':members[i].openId,
                            'label': members[i].userName
                        }
                        memberList[i]=item
                    }


                    let selects = this.state.memberSelects[index] || [],
                        roleList = this.state.roleList
                    roleList.map((item, index) => {
                        if(item.roleType == 'a'){
                            item.isCurrent = true
                        }else {
                            item.isCurrent = false
                        }
                    })
                    memberList.map((item, i) => {
                        item.isCheck = false
                    })
                    if(selects){
                        selects.map((item, i) => {
                            memberList.map((v, j) => {
                                if(item.roleType == 'a' && v.openId == item.openId){
                                    v.isCheck = true
                                }
                            })
                        })
                    }
                    console.log(memberList)
                    this.setState({
                        memberList,
                        roleList
                    })
                }
            })
            .catch( error => {
                console.log('error:', error)
            })

    }

    onHandleCheck(list) {
        let memberSelect = this.state.memberSelect,
            memberSelects = this.state.memberSelects,
            index = this.state.index,
            roleList = this.state.roleList,
            roleType = '',
            select = [],
            j = 0

        roleList.map((item, i) => {
            if(item.isCurrent == true){
                roleType = item.roleType
            }
        })
        list.map((item,i) => {
            if(item.isCheck){
                select[j] = Object.assign({},list[i],{ roleType: roleType})
                j ++
            }
        })
        if(memberSelect.length !== 0 && select.length!==0){
            memberSelect.map((v, j) => {
                select.map((m, n) => {
                    if(v.roleType == roleType && v.openId == m.openId){
                        select.splice(n, 1)
                    }
                })

            })
        }
        memberSelect = memberSelect.concat(select)

        memberSelects[index] = memberSelect
        this.setState({
            memberSelect: memberSelect,
            memberSelects: memberSelects
        })

    }

    onConfirm(){
        let memberSelect = [],
            memberSelects = this.state.memberSelects,
            members = [],
            index = 0

        memberSelects[this.state.index].map((v, i) => {
            /*members.concat({
                'openId': v.openId,
                'roleType': v.roleType
            })*/
            console.log('v',v)
            members[i] = {
                'openId': v.openId,
                'roleType': v.roleType
            }
        })
        this.setState({
            isShow: false,
            memberSelect
        })
        console.log('memberSelects',memberSelects)
        this.$emit('pages/ProjectEdit/memberSelects', { memberSelects })
        this.setState({
            memberSelects
        })

    }

    onCancel(){
        this.setState({
            isShow: false
        })
    }

    onSwitchTab( roleType ){
        let roleList = this.state.roleList,
            memberList = this.state.memberList,
            index = this.state.index,
            memberSelects = this.state.memberSelects,
            selects = memberSelects[index]
        roleList.map( (v, i) => {
            if(v.roleType == roleType){
                v.isCurrent = true
            }else {
                v.isCurrent = false
            }
        })
        memberList.map( (item, j) => {
            item.isCheck = false
        })
        console.log('selects',selects)
        if(selects){
            selects.map((item, i) => {
                memberList.map((v, j) => {
                    if(item.roleType == roleType && v.openId == item.openId){
                        v.isCheck = true
                    }
                })
            })
        }

        this.setState({
            roleList
        })
        console.log('memberSelects',memberSelects)
    }
}

export default Store



