import React, { PropTypes } from 'react'
import Reflux from 'reflux'
import classnames from 'classnames'
import Store from './store'
import Actions from './actions'
import './styles'
import Icon from 'widgets/Icon'
import { Link } from 'react-router'

class FootbarPage extends React.Component {
    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store(this.actions)
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })

        this.footNav = [
            {
                name: 'project',
                label:'项目',
                icon:'project',
                link:'/project'
            },
            {
                name: 'team',
                label:'团队',
                icon:'plan',
                link:'/team'
            },
            {
                name: 'note',
                label:'随手记',
                icon:'note',
                link:'/note'
            },
            {
                name: 'memo',
                label:'备忘录',
                icon:'memo',
                link:'/memo'
            },
            {
                name:'mine',
                label:'我的',
                icon:'mine',
                link:'/mine'
            }
        ]
    }

    componentDidMount() {

    }

    componentWillUnmount() {
        // no del
        this.actions.removeEvents()
    }

    render() {
        let currentPathName = window.location.pathname
        return (
            <footer className="root-page-footbar">
                <ul>
                    {
                        this.footNav.map( (item, index)=> {
                            let isCurrent = new RegExp(item.link).test(currentPathName)
                            return (
                                <li key={ index }
                                    className={ classnames({'current': isCurrent })}>
                                    <Link to={ item.link }>
                                        <Icon icon={ item.icon } />
                                        <span>{ item.label }</span>
                                    </Link>
                                </li>
                            );
                        })
                    }
                </ul>
            </footer>
        )
    }

}

export default FootbarPage



