import React, { PropTypes } from 'react'
import Reflux from 'reflux'
import Store from './store'
import Actions from './actions'
import './styles'

import ItemDot from 'widgets/ItemDot'
import PullUp from 'widgets/PullUp'
import Button from 'widgets/Button'
import Icon from 'widgets/Icon'
import ItemButton from 'widgets/ItemButton'
import DropdownMore from 'services/DropdownMore'
import ModalDate from 'services/ModalDate'
import RoundSearch from 'services/RoundSearch'
import TeamSelect from 'services/TeamSelect'
import ModalComplete from 'services/ModalComplete'
import ModalTime from 'services/ModalTime'

class TeamPlanMeetingPage extends React.Component {
    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store(this.actions)
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })
    }

    componentDidMount() {
        this.actions.didMount()
    }

    componentWillUnmount() {
        // no del
        this.actions.removeEvents()
    }

    renderItem = (item, index) => {
        return (
            <ItemDot
                key={ index }
                style={ this.colos[item.taskType]}
                title={ item.taskContent}
                name={ item.project }
                type={ item.subproject }
                tag={ this.tags[item.taskType] }
                message={ item.taskReply }
                date={item.taskTime}
                btnMore={
                    <Button
                        icon="more"
                        type="icon"
                        onClick={ (event)=> { this.actions.moreToggle(event, item) }}
                    />
                }
            />
        )
    }

    renderTop() {
        return (
            <div className="reports-top">
                <div className="top-nav">
                    <div className="top-nav-team">
                        <span>{ this.state.currentTeam.teamName}</span>
                        <Button
                            icon="show"
                            type="icon"
                            className="right"
                            onClick={ (event)=> { this.actions.toggleTeam(event) }}
                        >
                        </Button>
                    </div>
                    <div className="top-nav-search">
                        <RoundSearch />
                    </div>
                </div>
                <div className="time-box" onClick={ () => { this.actions.selectTime() }}>
                    <Icon icon="time" />
                    <span> 时间区间：{ this.state.time.label }</span>
                </div>
            </div>
        )
    }

    render() {
        return (
            <div className="root-page-teamplanmeeting">
                <PullUp
                    api={ this.state.api }
                    params={ this.state.params }
                    dataField="schedules"
                    renderItem={ this.renderItem }
                    onScroll={ this.actions.handleScroll }
                >
                    {
                        this.renderTop()
                    }
                </PullUp>
                <TeamSelect />
                <DropdownMore />
                <ModalDate />
                <ModalComplete />
                <ModalTime />
            </div>
        )
    }

}

export default TeamPlanMeetingPage



