import React, { PropTypes } from 'react'
import Reflux from 'reflux'
import Store from './store'
import Actions from './actions'
import './styles'

import ItemButton from 'widgets/ItemButton'
import Button from 'widgets/Button'
import NoteList from 'services/NoteList'
import Footbar from 'services/Footbar'

class NotePage extends React.Component {
    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store(this.actions)
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })
    }

    componentDidMount() {

    }

    componentWillUnmount() {
        // no del
        this.actions.removeEvents()
    }

    render() {
        return (
            <div className="root-page-note">
                <main className="scroll mainer fixed-bottom">
                    <NoteList />
                </main>
                <div className="btn-save">
                    <Button onClick={ ()=> { this.actions.handleSave()}}>保存</Button>
                </div>
            </div>
        )
    }

}

export default NotePage



