import RootStore from 'core/Store'
import * as api from 'core/api'

class Store extends RootStore {
    constructor(actions) {
        super()
        this.listenables = actions
        this.state = {
            test:'default',
            task: JSON.parse(this.cookie.get('task')),
            taskReply:""
        }
        this.init()
    }

    init() {
    }

    onTestAction(value) {
        this.setState({
            test: value
        })
    }

    onRemoveEvents() {
        this.cookie.set('task',"")
        this.clearEvents()
    }

    onHandleChange(type, value, index) {
        console.log(type, value, index)
        let taskReply = this.state.taskReply
        switch (type) {
            case 'taskReply': {
                taskReply = value
            }
                break
        }

        this.setState({
            taskReply
        })
        console.log(taskReply)
    }

    onCancel() {
        this.$route.goBack()
    }

    onSave() {
        this.$ajax
            .post(this.api('project/addReply')(),{
                scheduleId: this.state.task.scheduleId,
                task_reply: this.state.taskReply
            })
            .then( res => {
                if(res.result === 'success'){
                    this.$route.push('/')
                }

            })
            .catch( error => {
                console.log('error:', error)
            })


    }

}


export default Store



