import RootStore from 'core/Store'
import * as api from 'core/api'
import utils from 'core/utils'

class Store extends RootStore {
    constructor(actions) {
        super();
        this.state = {
            isShow: false,
            style: {},
            list: [
                {
                    key: 'steps-1',
                    label: '前期工作',
                    color: '#c740fc',
                    isCheck: false
                },{
                    key: 'steps-2',
                    label: '方案设计',
                    color: '#f85f57',
                    isCheck: false
                },{
                    key: 'steps-3',
                    label: '初步设计',
                    color: '#41d119',
                    isCheck: false
                },{
                    key: 'steps-4',
                    label: '施工图设计',
                    color: '#176ceb',
                    isCheck: false
                },{
                    key: 'steps-5',
                    label: '施工配合',
                    color: '#176ceb',
                    isCheck: false
                },{
                    key: 'steps-6',
                    label: '暂缓',
                    color: '#176ceb',
                    isCheck: false
                },{
                    key: 'steps-7',
                    label: '完成',
                    color: '#176ceb',
                    isCheck: false
                }
                //a:咨询;b:方案；c:初设 d:方案
                //前期工作，方案设计，初步设计，施工图设计，施工配合，暂缓,完成
                /*{
                    key:'a',
                    label: '前期工作',
                    color: '#c740fc',
                    isCheck: false
                },
                {
                    key: 'b',
                    label: '方案设计',
                    color: '#f85f57',
                    isCheck: false
                },{
                    key: 'c',
                    label: '初步设计',
                    color: '#41d119',
                    isCheck: false
                },
                {
                    key: 'd',
                    label: '施工图设计',
                    color: '#176ceb',
                    isCheck: false
                }*/
            ]
        }
        this.listenables = actions

        this.initListen()
    }

    initListen() {
        console.log('changeState')
        this.$on('services/DropdownSteps/state', (key, { isShow, style }) => {
            console.log('isShow',isShow)
            this.setState({
                isShow,
                style
            })
            if(!isShow) {
                this.setState({
                    list: this.state.list.map( v => {
                        v.isCheck = false
                        return v
                    })
                })
                this.$emit('services/ProjectList/reset', {})
            }
        })
    }

    onHandleCheck(list) {
        let selectItem = {}
        list.forEach((item) => {
            if(item.isCheck) {
                selectItem = item
            }
        })
        if(!utils.isEmptyObject(selectItem)){
            this.$emit('services/DropdownSteps/selectItem', selectItem)
        }
    }

    onRemoveEvents() {
        this.clearEvents()
    }
}

export default Store



