/**
 * Input
 */

import React, { PropTypes } from 'react'
import classnames from 'classnames'

class Input extends React.Component {
    static propTypes = {
        className: PropTypes.string,
        placeholder: PropTypes.string,
        onChange: PropTypes.func,
        rightIcon: PropTypes.any,
        leftIcon: PropTypes.any,
        children: PropTypes.any
    }

    static defaultProps = {
        value:''
    }

    constructor(props) {
        super()
        this.state = {
            value: props.value,
            isCurrent: props.isCurrent
        }
    }

    componentDidMount() {

    }

    componentWillReceiveProps({ value,isCurrent }) {
        this.setState({
            value,
            isCurrent
        })
    }

    componentWillUnmount() {

    }

    handleChange = (e) => {
        let { value } = e.target
        this.setState({
            value
        })
        this.props.onChange && this.props.onChange(e, value)
    }

    render() {
        const { className, placeholder } = this.props
        return (
            <dl className={ classnames('root-widget-input','clear',className) } >
                {
                    this.props.leftIcon &&
                    <dt className={ classnames({'dt-current': this.state.isCurrent })}>{ this.props.leftIcon }</dt>
                }
                <dd>
                    {
                        this.props.rightIcon &&
                        <span className="right">{ this.props.rightIcon }</span>
                    }
                    <span className="input">
                        <input
                            placeholder={ placeholder }
                            type="text"
                            value={ this.state.value }
                            onChange={ this.handleChange}
                        />
                    </span>
                </dd>
            </dl>
        )
    }
}

export default Input;



