import React, { PropTypes } from 'react'
import Reflux from 'reflux'
import Store from './store'
import Actions from './actions'
import './styles'

// test Checkbox
import Checkbox from '../index'

class Demo extends React.Component {
    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store(this.actions)
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })
    }

    componentDidMount() {

    }

    componentWillUnmount() {
        this.actions.removeEvents()
    }

    render() {
        const list = [
            {
                key: 1,
                label: '测试啊及',
                isCheck: true
            },{
                key: 2,
                label: '测试aa啊及',
                isCheck: true
            },{
                key: 3,
                label: '测试==啊及',
                isCheck: true
            },{
                key: 4,
                label: '测试比啊',
                isCheck: true
            }
        ]
        return (
            <div className="root-widget-checkbox__demo__">
                <Checkbox
                    list={ list }
                    onCheck={ (list)=> {console.log(list)}}
                />
            </div>
        )
    }
}

export default Demo



