import React, { PropTypes } from 'react'
import Reflux from 'reflux'
import Store from './store'
import Actions from './actions'
import './styles'

import utils from 'core/utils'
import Steps from 'widgets/Steps'
import PullUp from 'widgets/PullUp'
import RoundSearch from 'services/RoundSearch'

class MemoStepsPage extends React.Component {
    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store(this.actions)
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })

    }

    componentDidMount() {
        //console.log('044400000')
    }

    componentWillUnmount() {
        // no del
        this.actions.removeEvents()
    }

	renderItem=(item, index)=>{console.log('=====',utils.format(item.date,"yyyy.MM.dd"))
        console.log('dddddd',item.date)
        let listTitle = {
            titleDate: utils.format(item.date || 1490976000000 ,"yyyy.MM.dd"),
            titleCon:`${item.projectName} | ${item.subprojectName}`
        }
		return (
            <Steps
                key={ index }
                listTitle={ listTitle }
                listCon={ item.content }
            />
		)
	}

    render() {
        return (
            <div className="root-page-memosteps">
                <PullUp
                    api={ this.state.api }
                    params={ this.state.params }
                    dataField="data"
                    renderItem={ this.renderItem }
                >
                    <RoundSearch />
                </PullUp>
            </div>
        )
    }

}

export default MemoStepsPage



