/**
 * ItemNumber
 */

import React, { PropTypes } from 'react'
import classnames from 'classnames'

import ItemSwitch from 'widgets/ItemSwitch'
import Button from 'widgets/Button'

class ItemNumber extends React.Component {
    static propTypes = {
        className: PropTypes.string,
        title: PropTypes.string,
        onDel: PropTypes.func,
        onAdd: PropTypes.func,
        onEdit: PropTypes.func,
        onChange: PropTypes.func,
        isCurrent: PropTypes.bool,
        children: PropTypes.any,
        moveX: PropTypes.number,
        onHandleChange: PropTypes.func
    }

    static defaultProps = {
        isCurrent: false,
        moveX:0,
    }

    constructor(props) {
        super()
        this.state = {
            moveX: props.moveX,
            isCurrent: props.isCurrent
        }
    }

    componentDidMount() {
        //this.props.onHandleChange && this.props.onHandleChange(this.state.moveX)
    }

    componentWillReceiveProps({ isCurrent, moveX }) {
        this.setState({
            isCurrent,
            moveX
        })
    }

    componentWillUnmount() {

    }

    handleEdit = (event) => {
        event.stopPropagation()
        event.preventDefault()
        this.props.onEdit && this.props.onEdit()
    }

    handleAdd = (event) => {
        event.stopPropagation()
        event.preventDefault()
        this.props.onAdd && this.props.onAdd()
    }

    handleDel = (event) => {
        event.stopPropagation()
        event.preventDefault()
        this.props.onDel && this.props.onDel()
    }

    renderTitle(title) {
        return (
            <h3 className={ classnames({'label':!this.props.desc })}>{ title }</h3>
        )
    }

    renderDesc(desc) {
        return (
            <p>{ desc }</p>
        )
    }

    renderOperation = () => {
        return [
            <Button className="btn-delete" icon="edit" type="icon"
                onClick={ this.handleDel }
            >
                <span>删除</span>
            </Button>,
            <Button className="btn-add" icon="add" type="icon"
                onClick={ this.handleAdd }
            >
                <span>新建任务</span>
            </Button>
        ]
    }

    renderContent({title, number}) {
        return (
            <div className="item-number-werapper"
                onClick={ this.handleEdit }
            >
                <div className="number">
                    <span>{ number }</span>
                </div>
                <div className="auto">
                    <p>{ title }</p>
                    <input
                        type="text"
                        value={ title }
                        onChange={ (e) => { this.props.onChange && this.props.onChange(e) } }
                    />
                </div>
            </div>
        )
    }

    renderSwitch({title, number}) {
        return (
            <ItemSwitch
                onRenderOperation={ this.renderOperation }
                moveX={ this.state.moveX }
                onHandleChange={ (moveX) => { this.props.onHandleChange && this.props.onHandleChange(moveX) }}
            >
                {this.renderContent({title, number})}
            </ItemSwitch>
        )
    }

    render() {
        const { className, title, number } = this.props
        return (
            <div className={ classnames('root-widget-itemnumber',className,{'current': this.state.isCurrent}) } >
                {
                    this.props.onDel || this.props.onAdd ?
                    this.renderSwitch({title, number}) :
                    this.renderContent({title, number})
                }
            </div>
        )
    }
}

export default ItemNumber;



