import RootStore from 'core/Store'
import * as api from 'core/api'

class Store extends RootStore {
    constructor(actions) {
        super();
        this.state = {
            api: this.api('team/task/people/analysis')(),
            params: {
                "openId": this.cookie.get("openId"),//登录者openId
                "memberOpenId": this.getMemberOpenId(),//被查看者openId
                "teamId": this.cookie.get("teamId"),//团队Id
                "period": "yearly" //时间范围（可选，默认为近两周(weekly)）
                // //返回超期、未完成、已完成三个数组，weekly：返回14点，monthly：
                // 返回30或31点(每点代表一天)；yearly：返回6点（每点代表一个月）
            }
        }
        this.listenables = actions

        this.init()
    }

    init() {

    }

    getMemberOpenId() {
        let pathname = this.$route.getCurrentLocation().pathname
        let params = pathname && pathname.split('/analysis/')[1]
        return params
    }

    onInit(myChart) {
        myChart.showLoading({
            text: '正在努力的读取数据中...',    //loading话术
        });

        this.$ajax.get(this.api('team/task/people/analysis')(), {
            params: this.state.params
        }).then(res => {
            myChart.hideLoading();
            let data = res.data.data
            this.drawChart(myChart, data)
        }).catch(error => {})

    }


    createEmply(num) {
        let arr = []
        for(var i=0; i< num; i++) {
            arr.push('')
        }
        return arr
    }

    drawChart(myChart, data) {
        let type = this.state.params.period
        let xAxisData = []
        if (type === 'yearly') {
            xAxisData = this.createEmply(6)

        } else if (type === 'weekly') {
            //xAxisData = ['二月', '四月', '六月', '八月', '十月', '十二月']
            xAxisData = this.createEmply(14)
        } else if (type === 'monthly') {
            xAxisData = this.createEmply(new Date().getDate())
        }

        var option = {
            tooltip: {
                trigger: 'axis'
            },
            legend: {
                data: ['超期', '未完成', '已完成']
            },
            toolbox: {
                show: true,
                feature: {
                    mark: {show: true},
                    dataView: {show: false, readOnly: false},
                    magicType: {show: false, type: ['line', 'bar', 'stack', 'tiled']},
                    restore: {show: false},
                    saveAsImage: {show: false}
                }
            },
            calculable: true,
            xAxis: [
                {
                    type: 'category',
                    boundaryGap: false,
                    data: xAxisData
                }
            ],
            yAxis: [
                {
                    type: 'value'
                }
            ],
            series: [
                {
                    name: '超期',
                    type: 'line',
                    stack: '总量',
                    data: data['overTime']
                },
                {
                    name: '未完成',
                    type: 'line',
                    stack: '总量',
                    data: data['unfinished']
                },
                {
                    name: '已完成',
                    type: 'line',
                    stack: '总量',
                    data: data['finished']
                }
            ]
        };

        myChart.setOption(option);
        //myChart.dispose();
    }

    onRemoveEvents() {
        this.clearEvents()
    }
}

export default Store



