import React, { PropTypes } from 'react'
import Reflux from 'reflux'
import Store from './store'
import Actions from './actions'
import './styles'

import ProjectHeader from 'services/ProjectHeader'
import Mainer from 'layouts/Mainer'
import ProjectList from 'services/ProjectList'

class ProjectPage extends React.Component {
    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store(this.actions)
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })
    }

    componentDidMount() {

    }

    componentWillUnmount() {
        // no del
        this.actions.removeEvents()
    }

    render() {
        return (
            <div className="root-page-project">
                <ProjectHeader />
                <Mainer paddingTop paddingBottom >
                    <ProjectList />
                </Mainer>
            </div>
        )
    }

}

export default ProjectPage



