/**
 * ButtonGroup
 */

import React, { PropTypes } from 'react'
import classnames from 'classnames'

class ButtonGroup extends React.Component {
    static propTypes = {
        className: PropTypes.string,
        cancelLabel: PropTypes.string,
        confirmLabel: PropTypes.string,
        onCancel: PropTypes.func,
        onConfirm: PropTypes.func,
    }

    static defaultProps = {
        cancelLabel:'取消',
        confirmLabel:'保存',
    }

    constructor(props) {
        super()
    }

    componentDidMount() {

    }

    componentWillUnmount() {

    }

    render() {
        const { className } = this.props
        return (
            <ul className={ classnames('root-widget-buttongroup','clear','li-left',className) } >
                <li
                    onClick={ () => { this.props.onCancel && this.props.onCancel()} }
                >{this.props.cancelLabel}</li>
                <li
                    onClick={ () => { this.props.onConfirm && this.props.onConfirm()} }
                >{this.props.confirmLabel}</li>
            </ul>
        )
    }
}

export default ButtonGroup;



