import RootStore from 'core/Store'
import * as api from 'core/api'

class Store extends RootStore {
    constructor(actions) {
        super()
        this.listenables = actions
        this.state = {
            inputValue:''
        }
        this.init()
        this.initListen()
    }

    init() {

    }

    initListen() {
        //services/DropdownType/selectItem
        this.$on('services/DropdownType/selectItem', (key, selectItem) => {
            console.log('selectItem==', selectItem)
        })
    }

    onEnter(event) {
        let inputValue = this.state.inputValue
        if(event.keyCode == 13) {
            this.$emit('services/ProjectTaskHeader/search',{
                inputValue
            })
        }
    }

    onInputChange(e) {
        let inputValue = e.target.value
        this.setState({
            inputValue
        })
    }

    onSearch() {

        let isSearch = !this.state.isSearch
        this.setState({
            isSearch
        })
        if(!isSearch) {
            this.$emit('services/ProjectTaskList/reset', {})
        }
    }

    onToggle(event) {
        let {left, top, width, height} = event.target.getBoundingClientRect()

        let style = {
            left: (left + width)+ "px",
            top: (top + height) +"px",
            transform: "translateX(-100%)"
        }

        let isShow = !this.state.isShow
        this.setState({
            isShow
        })
        this.$emit('services/DropdownType/state',{
            isShow,
            style
        })
        if(!isShow) {
            this.$emit('services/ProjectTaskList/reset', {})
        }
    }

    onRemoveEvents() {
        this.clearEvents()
    }
}

export default Store



