import RootStore from 'core/Store'
import actions from './actions'

class Store extends RootStore {
    constructor(actions) {
        super();
        this.listenables = actions
        this.state = {
            api: this.api('project/list')(),
            params: {
                openId: this.cookie.get('openId'),
                pageSize: 20,
                currentPageNumber: 1
            }
        }

        this.init()
    }

    init() {
        this.$on('services/DropdownSteps/selectItem', (key, selectItem) => {
            this.setState({
                params: Object.assign({}, this.state.params, {
                    teamStatus: selectItem.label,
                    project: null,
                    currentPageNumber: 1
                })
            })
        })

        this.$on('services/ProjectHeader/search', (key, { inputValue }) => {
            this.setState({
                params: Object.assign({}, this.state.params, {
                    teamStatus: null,
                    project: inputValue,
                    currentPageNumber: 1
                })
            })
        })

        this.$on('services/ProjectList/reset', (key, {}) => {
            this.setState({
                params: Object.assign({}, this.state.params, {
                    teamStatus: null,
                    project: null,
                    currentPageNumber: 1
                })
            })
        })
    }

    onHandleEdit = ( item ) => {
        this.cookie.set('curproject',JSON.stringify( item ))
        this.cookie.set('curTeam',item.teamId)
        this.cookie.set('curTeam',item.teamStatus)

        this.$route.push(`/project/edit/${item.projectId}/${item.subprjectId}`)
    }

    onHandleShowTask = (projectId, subprjectId) => {
        this.$route.push(`/project/task/${projectId}/${subprjectId}`)
    }

    onAdd() {
        this.$route.push('/project/add')
    }

    onRemoveEvents() {
        this.clearEvents()
        this.cookie.remove('currproject')
    }
}

export default Store



