import React, { PropTypes } from 'react'
import Store from './store'
import Actions from './actions'
import './styles'

import Icon from 'widgets/Icon'

class TeamTaskProjectAnalysisDetailPage extends React.Component {
    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store(this.actions)
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })
    }

    componentDidMount() {
        var myChart = echarts.init(this.refs.myCanvas);
        this.actions.init(myChart);
    }

    componentWillUnmount() {
        // no del
        this.actions.removeEvents()
    }

    render() {
        return (
            <div className="root-page-teamtaskprojectanalysisdetail">
                <p className="time-icon">
                    <Icon icon="time" />
                    世间筛选
                </p>
                <div className="time-box">
                    <span>近一周</span>
                </div>
                <canvas id="myCanvas" ref="myCanvas" width="480" height="360" ></canvas>
                <p className="time">时间</p>
            </div>
        )
    }

}

export default TeamTaskProjectAnalysisDetailPage



