import React, { PropTypes } from 'react'
import Store from './store'
import Actions from './actions'
import './styles'

import Input from 'widgets/Input'
import Icon from 'widgets/Icon'
import ButtonGroup from 'widgets/ButtonGroup'
import ModalDate from 'services/ModalDate'
import ProjectSelect from 'services/ProjectSelect'
import TaskmemberSelect from 'services/TaskmemberSelect'
import Checkbox from 'widgets/Checkbox'
import Modal from 'widgets/Modal'

class TaskAddPage extends React.Component {
    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store(this.actions)
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })
    }

    componentDidMount() {

    }

    componentWillUnmount() {
        // no del
        this.actions.removeEvents()
    }

    renderIconColor() {
        return (
            <div>
                <span style={{backgroundColor:'#c740fc'}} className="icon-color"></span>
            </div>
        )
    }

    renderTagList() {
        return (
            <div>
                {
                    <Checkbox
                        list={ this.state.tagList }
                        onCheck={ (list) => {
                            this.actions.handleCheck(list)
                        }}
                        one='true'

                    />
                }
            </div>
        )
    }

    render() {
        return (
            <div className="root-page-taskadd">
                <div className="task-add-list">
                    <Input
                        placeholder="选择项目名称:"
                        leftIcon={ <Icon icon="project" /> }
                        rightIcon={ <Icon icon="show" onClick={ (event)=> { this.actions.projectToggle(event) }}/>}
                        value={ this.state.projectSelect.label }
                        onChange={ (e, value) => { this.actions.handleChange('teamId', value) } }
                    />
                    <ProjectSelect isProject = { this.state.isProject } />
                    <Input
                        placeholder="选择子项目名称:"
                        leftIcon={ this.renderIconColor() }
                        rightIcon={ <Icon icon="show" onClick={ (event)=> { this.actions.subprojectToggle(event) }}/>}
                        value={ this.state.subprojectSelect.label }
                        onChange={ (e, value) => { this.actions.handleChange('subprojectId' ,value) } }
                    />
                    <Input
                        placeholder="任务内容："
                        leftIcon={ <Icon icon="list" /> }
                        value={ this.state.formData.taskContent }
                        onChange={ (e, value) => { this.actions.handleChange('taskContent' ,value) } }
                    />
                    <Input
                        placeholder="标签"
                        leftIcon={ <Icon icon="tag" /> }
                        value={ this.state.tagSelect.label }
                        rightIcon={ <Icon icon="show" onClick={ (event)=> { this.actions.tagToggle(event) }}/>}
                        onChange={ (e, value) => { this.actions.handleChange('taskType' ,value) } }
                    />
                    <dl>
                        <dt>
                            <Icon icon="add-user" onClick={ (event)=> { this.actions.memberToggle(event) }}/>
                        </dt>
                        <dd><p>设定人员</p></dd>
                    </dl>
                    <Modal isShow={ this.state.isShowmodal }
                           onConfirm={()=>this.actions.confirm()}
                           onCancel={() => this.actions.cancel()
                           }>{ this.renderTagList()}</Modal>
                    <TaskmemberSelect/>
                    <dl>
                        <dt>
                            <Icon icon="date"/>
                        </dt>
                        <dd>
                            <p>
                                截至日期：
                                <a href="javascript:void(0)"
                                   onClick={() => { this.actions.handleClick() } }>
                                    { this.state.taskTime }</a>
                            </p>
                            <ModalDate />
                        </dd>
                    </dl>

                    <Input
                        placeholder="留言:"
                        leftIcon={ <Icon icon="message" /> }
                        value={ this.state.formData.taskReply }
                        onChange={ (e, value) => { this.actions.handleChange('taskReply' ,value) } }
                    />
                    {/*<MemberSelect />*/}
                </div>
                <div className="task-add-bottom">
                    <ButtonGroup
                        onConfirm={ ()=> { this.actions.handleSave() }}
                        onCancel={ ()=> { this.actions.handleCancel() }}
                    />
                </div>
            </div>
        )
    }

}

export default TaskAddPage



