import React, { PropTypes } from 'react'
import Reflux from 'reflux'
import Store from './store'
import Actions from './actions'
import './styles'

import Input from 'widgets/Input'
import Icon from 'widgets/Icon'
import ButtonGroup from 'widgets/ButtonGroup'
import DropdownColor from 'services/DropdownColor'
import MemberEdit from 'services/MemberEdit'

class ProjectEditPage extends React.Component {
    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store(this.actions)
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })
    }

    componentDidMount() {
        this.actions.insertInfo()
    }

    componentWillUnmount() {
        // no del
        this.actions.removeEvents()
    }

    renderIconColor(index, status) {
        return (
            <div>
                <span style={{backgroundColor:status}} className="icon-color" onClick={(event)=> { this.actions.stageToggle(event, index) }}></span>
                <DropdownColor />
            </div>
        )
    }

    renderIconAdd() {
        return (
            <span className="edit">
                <Icon icon="add" onClick={ () => { this.actions.addSub() } }/>
            </span>
        )
    }

    render() {
        return (
            <div className="root-page-projectedit">
                <div className="project-edit-list">
                    <Input
                        leftIcon={ <Icon icon="plan" /> }
                        value={ this.state.curproject.teamName }
                        readonly="true"
                    />
                    <Input
                        placeholder="项目名称:"
                        leftIcon={ <Icon icon="project" /> }
                        value={ this.state.project }
                        onChange={ (e, value) => { this.actions.changeProject(e, value) } }
                    />

                    {/*<Input
                        placeholder=" 子项目名称:"
                        leftIcon={ this.renderIconColor() }
                        rightIcon={ <Icon icon="add-user" />}
                        value={ this.state.value }
                        onChange={ (e, value) => { this.actions.handleChange(e, value) } }
                    />
                    <Input
                        placeholder=" 子项目名称:"
                        leftIcon={ this.renderIconAdd() }
                        rightIcon={ <Icon icon="add-user" />}
                        value={ this.state.value }
                        onChange={ (e, value) => { this.actions.handleChange(e, value) } }
                    />*/}
                    {
                        this.state.inputs.map((item, index) => {
                            return (
                                <Input
                                    key={ index }
                                    placeholder=" 子项目名称:"
                                    leftIcon={ item.leftIcon === 'add' ? this.renderIconAdd(): this.renderIconColor(index, item.status) }
                                    rightIcon={ <Icon icon={ item.rightIcon } onClick={ (event)=> { this.actions.memberToggle(event, index) }}/>}
                                    value={ item.value }
                                    isCurrent = { this.state.dtCurrent && this.state.dtIndex == index }
                                    onChange={ (e, value) => { this.actions.changeSubpro(e, value, index) } }
                                />
                            )
                        })
                    }
                    <MemberEdit />
                </div>
                <div className="project-edit-bottom">
                    <ButtonGroup
                        onConfirm={ ()=> { this.actions.handleSave() }}
                        onCancel={ ()=> { this.actions.handleCancel() }}
                    />
                </div>
            </div>
        )
    }

}

export default ProjectEditPage



