import React, { PropTypes } from 'react'
import Reflux from 'reflux'
import Store from './store'
import Actions from './actions'
import './styles'

import ItemDot from 'widgets/ItemDot'
import PullUp from 'widgets/PullUp'
import Button from 'widgets/Button'
import DropdownMore from 'services/DropdownMore'
import ModalDate from 'services/ModalDate'
import ModalComplete from 'services/ModalComplete'
import ModalMember from 'services/ModalMember'

class MineListPage extends React.Component {
    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store(this.actions)
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })
        this.tags = {
            a:"进度安排",
            b:"出图提醒",
            c:"会议通知",
            d:"外部条件",
            e:"内部协调"
        }
        this.colos = {
            a:"red",
            b:"blue",
            c:"violet",
            d:"yellow",
            e:"purple",
            f:"green",
            h:"grey"
        }
    }

    componentDidMount() {

    }

    componentWillUpdate(nextProps,  nextState) {
        console.log(this.state.api,nextState.api,
            this.state.params, nextState.params)
        return this.state.api !== nextState.api || this.state.params !== nextState.params
    }

    componentWillUnmount() {
        // no del
        this.actions.removeEvents()
    }

    renderItem = (item, index) => {
        return (
            <ItemDot
                key={ index }
                style={ this.colos[item.taskType]}
                title={ item.taskContent}
                name={ item.project }
                type={ item.subproject }
                tag={ this.tags[item.taskType] }
                message={ item.taskReply }
                date={item.taskTime}
                btnMore={
                    <Button
                        icon="more"
                        type="icon"
                        onClick={ (event)=> { this.actions.moreToggle(event, item) }}
                    />
                }
            />
        )
    }

    render() {
        return (
            <div className="root-page-minelist">
                <PullUp
                    dataField="schedules"
                    api={ this.props.api }
                    params={ this.props.params }
                    renderItem={ this.renderItem }>
                    {
                        this.props.children && this.props.children
                    }
                </PullUp>
                <DropdownMore />
                <ModalDate />
                <ModalComplete />
                <ModalMember />
            </div>
        )
    }

}

export default MineListPage



