/**
 * TeamReportsContent
 */

import React, { PropTypes } from 'react'
import classnames from 'classnames'
import { Link } from 'react-router'

class TeamReportsContent extends React.Component {
    static propTypes = {
        className: PropTypes.string,
        time: PropTypes.string,
        current: PropTypes.string,
        data: PropTypes.array,
        list: PropTypes.array,
        onCheck: PropTypes.func,
        children: PropTypes.any
    }

    static defaultProps = {
        time:'近1个月',
        data: [],
        list:[]
    }

    constructor(props) {
        super()
        this.state = {
            test: props.test,
            isShowCheck: false
        }
    }

    componentDidMount() {

    }

    componentWillReceiveProps({ time,data }) {
        this.setState({
            time,
            data
        })
    }

    componentWillUnmount() {

    }

    renderBottom() {
        return (
          <div className="reports-bottom">
                <div className="tag-box">
                    <p>已超期</p>
                    <p>未完成</p>
                </div>
                  <ul className="data-map">
                   { this.props.data.map((item, i) => {
                       console.log('item.memberName', item.memberName, item,item.projectName)
                     return (
                        <li key={'r_' + i}>
                            {
                                item.memberName ?
                                    <span className="label">{ item.memberName }</span>
                                    :
                                    <div className="label">
                                        <span>{item.projectName}</span>
                                        <span>{item.projectName}</span>
                                    </div>
                            }
                            <div className="data-box">
                                <Link to={item.link}>
                                    <p className="timeout" style={{width: item.percentagetOverTime + '%' }} >{item.taskOverTime ? item.taskOverTime : ''}</p>
                                    <p className="undone" style={{width: item.percentagetUnfinished + '%' }} >{item.taskUnfinished ? item.taskUnfinished : ''}</p>
                                </Link>
                            </div>
                        </li>
                    )
                   })}
                </ul>
          </div>
        )
    }

    render() {
        const { className } = this.props
        return (
            <div className={ classnames('root-widget-teamreportscontent',className) } >
                {this.renderBottom()}
            </div>
        )
    }
}

export default TeamReportsContent;



