import React, { PropTypes } from 'react'
import Reflux from 'reflux'
import Store from './store'
import Actions from './actions'
import './styles'

import ProjectTaskHeader from 'services/ProjectTaskHeader'
import ProjectTask from 'services/ProjectTask'
import Footbar from 'services/Footbar'

class ProjectTaskPage extends React.Component {
    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store(this.actions)
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })
    }

    componentDidMount() {

    }

    componentWillUnmount() {
        // no del
        this.actions.removeEvents()
    }

    render() {
        return (
            <div className="root-page-projecttask">
                <ProjectTaskHeader />
                <ProjectTask />
                <Footbar current="project" />
            </div>
        )
    }

}

export default ProjectTaskPage



