import RootStore from 'core/Store'
import * as api from 'core/api'

class Store extends RootStore {
    constructor(actions) {
        super()
        this.listenables = actions
        this.state = {
            isShow:false,
            memberList:[],
            memberSelect:[],
            memberSelects:[],
            index:0,
            roleList: [
                {
                    roleType:'a',
                    isCurrent:true,
                    roleName:"A. 项目负责人"
                },{
                    roleType:'b',
                    isCurrent:false,
                    roleName:"B. 项目助理"
                },{
                    roleType:'c',
                    isCurrent:false,
                    roleName:"C. 设计人员"
                },{
                    roleType:'d',
                    isCurrent:false,
                    roleName:"D. 其他"
                }

            ]
        }
        this.init()
    }

    init() {
        this.$on('services/MemberSelect/teamId',(key,{ teamId }) =>{
            this.onInsertMemberList(teamId)
        })

        this.$on('services/MemberSelect/memberSelects',(key, { isShow,index,memberSelects }) => {
            if (isShow){
                this.onInsertMemberList(this.state.teamId)
            }
            this.setState({
                isShow,
                index,
                memberSelects
            })
            let selects = memberSelects[index] || [],
                memberList = this.state.memberList,
                roleList = this.state.roleList
            roleList.map((item, index) => {
                if(item.roleType == 'a'){
                    item.isCurrent = true
                }else {
                    item.isCurrent = false
                }
            })
            memberList.map((item, i) => {
                item.isCheck = false
            })
            console.log(selects)
            if(selects){
                selects.map((item, i) => {
                    memberList.map((v, j) => {
                        if(item.roleType == 'a' && v.openId == item.openId){
                            v.isCheck = true
                        }
                    })
                })
            }
            this.setState({
                memberList,
                roleList
            })
        })
    }

    onTestAction(value) {
        this.setState({
            test: value
        })
    }

    onRemoveEvents() {
        this.clearEvents()
    }

    onInsertMemberList(id){
        this.$ajax
            .get(this.api('project/allMember')(),{
                params: {teamId: id}
            })
            .then( res => {
                if(res.status === 200){
                    console.log(res)
                    let members =  res.data.users,
                        memberList = new Array(),
                        index = this.state.index

                    for (let i=0; i<members.length;i++){
                        var item = {
                            'isCheck': false,
                            'key': i,
                            'openId':members[i].openId,
                            'label': members[i].userName
                        }
                        memberList[i]=item
                    }

                    this.setState({
                        memberList
                    })
                }
            })
            .catch( error => {
                console.log('error:', error)
            })
    }

    onHandleCheck(list) {
        let memberSelect = this.state.memberSelect,
            memberSelects = this.state.memberSelects,
            index = this.state.index,
            roleList = this.state.roleList,
            roleType = '',
            select = [],
            j = 0

        roleList.map((item, i) => {
            if(item.isCurrent == true){
                roleType = item.roleType
            }
        })
        list.map((item,i) => {
            if(item.isCheck){
                select[j] = Object.assign({},list[i],{ roleType: roleType})
                j ++
            }
        })
        if(memberSelect.length !== 0 && select.length!==0){
            memberSelect.map((v, j) => {
                select.map((m, n) => {
                    if(v.roleType == roleType && v.openId == m.openId){
                        select.splice(n, 1)
                    }
                })

            })
        }
        memberSelect = memberSelect.concat(select)

        memberSelects[index] = memberSelect
        this.setState({
            memberSelect: memberSelect,
            memberSelects: memberSelects
        })

    }

    onConfirm(){
        let memberSelect = [],
            memberSelects = this.state.memberSelects
        this.setState({
            isShow: false,
            memberSelect
        })
        this.$emit('pages/ProjectAdd/memberSelects', { memberSelects })
        this.$emit('pages/ProjectEdit/memberSelects', { memberSelects })
    }

    onCancel(){
        this.setState({
            isShow: false
        })
    }

    onSwitchTab( roleType ){
        let roleList = this.state.roleList,
            memberList = this.state.memberList,
            index = this.state.index,
            memberSelects = this.state.memberSelects,
            selects = memberSelects[index]
        roleList.map( (v, i) => {
            if(v.roleType == roleType){
                v.isCurrent = true
            }else {
                v.isCurrent = false
            }
        })
        memberList.map( (item, j) => {
            item.isCheck = false
        })
        console.log('selects',selects)
        if(selects){
            selects.map((item, i) => {
                memberList.map((v, j) => {
                    if(item.roleType == roleType && v.openId == item.openId){
                        v.isCheck = true
                    }
                })
            })
        }

        this.setState({
            roleList
        })
        console.log('memberSelects',memberSelects)
    }
}

export default Store



