import React, { PropTypes } from 'react'
import Reflux from 'reflux'
import Store from './store'
import Actions from './actions'
import './styles'

import HeaderTab from 'widgets/HeaderTab'
import RoundSearch from 'services/RoundSearch'

class MemoHeaderPage extends React.Component {
    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store(this.actions)
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })
        this.tabs = [
	        {
		        name: 'memorabilia',
		        label:'大事记',
		        link:'/memo/memorabilia'
	        },
	        {
		        name: 'personal',
		        label:'个人',
		        link:'/memo/personal'
	        }
        ]
    }

    componentDidMount() {
        this.actions.setCurrentRoute(this.tabs)
    }

    componentWillUnmount() {
        // no del
        this.actions.removeEvents()
    }

    render() {
        return (
            <div className="root-page-memoheader">
                <HeaderTab
                    tab={ this.tabs }
                    current={ this.state.current }
                    onHandleClick={ () => this.actions.setCurrentRoute(this.tabs) }
                />
            </div>
        )
    }

}

export default MemoHeaderPage



