import RootStore from 'core/Store'

class Store extends RootStore {
    constructor(actions) {
        super()
        this.state = {
            isShow: false
        }
        this.listenables = actions
        this.initListen()
    }

    initListen() {
        this.$on('services/DropdownType/selectList', (key, { selectList }) => {
            console.log('selectList', selectList)
        })
    }

    onToggle(event) {
        let {left, top, width, height} = event.target.getBoundingClientRect()
        let style = {
            left: left+ "px",
            top: (top + height) +"px"
        }
        let isShow = !this.state.isShow
        this.setState({
            isShow
        })
        this.$emit('services/DropdownType/state',{
            isShow,
            style
        })
    }

    onRemoveEvents() {
        this.clearEvents()
    }

}

export default Store



