import React, { PropTypes } from 'react'
import Reflux from 'reflux'
import Store from './store'
import Actions from './actions'
import './styles'

import Dropdown from 'widgets/Dropdown'

class DropdownMorePage extends React.Component {
    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store(this.actions)
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })
        this.list = [
            {
                name: 'complete',
                label: '完成任务'
            },
            {
                name: 'assign',
                label: '转移指派'
            },
            {
                name: 'delay',
                label: '延期任务'
            },
            {
                name: 'message',
                label: '写新留言'
            }
        ]
    }

    componentDidMount() {

    }

    componentWillUnmount() {
        // no del
        this.actions.removeEvents()
    }

    render() {
        return (
            <Dropdown
                className="root-page-dropdownmore"
                isShow={ this.state.isShow }
                style={ this.state.style }
            >
                <ul>
                    <li onClick={ (event) => { this.actions.complete(event) }}>完成任务</li>
                    <li onClick={ (event) => { this.actions.assign(event) }}>转移指派</li>
                    <li onClick={ (event) => { this.actions.delay(event) }}>延期任务</li>
                    <li onClick={ (event) => { this.actions.message(event) }}>写新留言</li>
                    {
                       /* this.state.list &&
                        this.state.list.map( (item, index) => {
                            return (
                                <li key={ index }
                                    onClick={ (event) => { this.actions.selectItem(event, item) }}
                                >{ item.label }</li>
                            )
                        })*/
                    }
                </ul>
            </Dropdown>
        )
    }

}

export default DropdownMorePage



