import React, { PropTypes } from 'react'
import Reflux from 'reflux'
import Store from './store'
import Actions from './actions'
import './styles'

import Icon from 'widgets/Icon'
import ButtonGroup from 'widgets/ButtonGroup'

class TeamManpowerSettingPage extends React.Component {
    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store(this.actions)
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })
    }

    componentDidMount() {

    }

    componentWillUnmount() {
        // no del
        this.actions.removeEvents()
    }

    render() {
        return (
            <div className="root-page-teammanpowersetting">
                <h3 className="title"> <Icon icon="mine" />现有人员</h3>
                <ul className="list-box">
                   { this.state.nowPeople.map((name, index) => <li key={ index }>{name}</li>) }
                </ul>
                <ul className="project-box">
                   {this.state.projectList.map((item, index) => {
                        return (
                            <li
                                key={ index }
                                className={this.state.checkItem === item.value ? 'active' : ''}
                                onClick={() => this.actions.projectItem(item.value) }
                            >
                               <dl>
                                   <dt>{item.label}. 项目负责人<span>（查看所有项目，及修子项目）</span></dt>
                                   <dd>
                                       <p className="line-search"><Icon icon="filter" /> </p>
                                       <ul className="conten-box">
                                           <li>
                                               <h4>设计：</h4>
                                              {[1,2,3].map((item,index) => {
                                                return (
                                                    <div className="text-box" key={ index }>
                                                       <span className="text">李四</span>
                                                       <span className="icon">
                                                           <Icon icon="tick" />
                                                       </span>
                                                   </div>
                                                )
                                              })}
                                           </li>
                                       </ul>
                                        <ButtonGroup
                                            onConfirm={ ()=> { this.actions.handleSave() }}
                                            onCancel={ ()=> { this.actions.handleCancel() }}
                                        />
                                   </dd>
                               </dl>
                           </li>
                       )})}
                </ul>
            </div>
        )
    }

}

export default TeamManpowerSettingPage



