function requireAuth() {
    return (nextState, replace, next) => {
        let openId = Cookie.get('openId')
        if (!openId) {
            window.location.href = "http://www.chenlinallen.com/login/getOauth/"
        } else {
            next()
        }
    };
}

import Demo from 'pages/__demo__'
import demoRoutes from './demoRoutes'
import Mainer from 'pages/Mainer'
import Project from 'pages/Project'
import ProjectAdd from 'pages/ProjectAdd'
import ProjectEdit from 'pages/ProjectEdit'
import ProjectTask from 'pages/ProjectTask'
import TaskreplyAdd from 'pages/TaskreplyAdd'

import Team from 'pages/Team'
import TeamTask from 'pages/TeamTask'

import TeamTaskPeople from 'pages/TeamTaskPeople'
import TeamTaskPeopleDetail from 'pages/TeamTaskPeopleDetail'
import TeamTaskAnalysis from 'pages/TeamTaskAnalysis'
import TeamTaskStatistics from 'pages/TeamTaskStatistics'

import TeamTaskProject from 'pages/TeamTaskProject'
import TeamTaskProjectDetail from 'pages/TeamTaskProjectDetail'
import TeamTaskProjectAnalysis from 'pages/TeamTaskProjectAnalysis'
import TeamTaskProjectStatistics from 'pages/TeamTaskProjectStatistics'

import TeamManpower from 'pages/TeamManpower'
import TeamManpowerSetting from 'pages/TeamManpowerSetting'
import TeamPlan from 'pages/TeamPlan'
import TeamPlanMeeting from 'pages/TeamPlanMeeting'
import TeamPlanPlot from 'pages/TeamPlanPlot'
import TaskAdd from 'pages/TaskAdd'

import Note from 'pages/Note'

import Memo from 'pages/Memo'
import MemoMemorabilia from 'pages/MemoMemorabilia'
import MemoPersonal from 'pages/MemoPersonal'
import MemoDetail from 'pages/MemoDetail'

import Mine from 'pages/Mine'
import MineUndone from 'pages/MineUndone'
import MineRemoved from 'pages/MineRemoved'
import MineSchedule from 'pages/MineSchedule'

import Error from 'pages/Error'

const routes = [
    {
        path: '/',
        component: Mainer,
        childRoutes: [
            {
                path: 'project',
                component: Project
            },
            {
                path: 'note',
                component: Note
            },
            {
                path: 'memo',
                component: Memo,
                indexRoute: {
                    onEnter: (nextState, replace) => replace('/memo/memorabilia')
                },
                childRoutes: [
                    {
                        path: 'memorabilia',
                        component: MemoMemorabilia
                    },
                    {
                        path: 'personal',
                        component: MemoPersonal
                    },
                    {
                        path: 'memorabilia/:id',
                        component: MemoDetail
                    }
                ]
            },
            {
                path: 'mine',
                component: Mine,
                indexRoute: {
                    onEnter: (nextState, replace) => replace('/mine/undone')
                },
                childRoutes: [
                    {
                        path: 'undone',
                        component: MineUndone
                    },
                    {
                        path: 'removed',
                        component: MineRemoved
                    },
                    {
                        path: 'schedule',
                        component: MineSchedule,
                    }
                ]
            }
        ]
    },
    {
        path: 'mine/schedule/add',
        component: TaskAdd
    },
    {
        path: 'project/add',
        component: ProjectAdd
    },
    {
        path: 'project/edit/:projectId/:subprjectId',
        component: ProjectEdit
    },
    {
        path: 'project/Task/:projectId/:subprjectId',
        component: ProjectTask
    },
    {
        path: 'project/Task/reply',
        component: TaskreplyAdd
    },
    {
        path: 'team',
        indexRoute: {
            onEnter: (nextState, replace) => replace('/team/task/people')
        },
        component: Team,
        childRoutes: [
            {
                path: 'task',
                component: TeamTask,
                indexRoute: {
                    onEnter: (nextState, replace) => replace('/team/task/people')
                },
                childRoutes: [
                    {
                        path: 'people',
                        component: TeamTaskPeople
                    },
                    {
                        path: 'project',
                        component: TeamTaskProject
                    }
                ]
            },
            {
                path: 'taskPeopleDetail',
                component: TeamTaskPeopleDetail,
                childRoutes: [
                    {
                        path: 'analysis/:memberOpenId',
                        component: TeamTaskAnalysis
                    },
                    {
                        path: 'statistics/:memberOpenId',
                        component: TeamTaskStatistics
                    }
                ]
            },
            {
                path: 'taskProjectDetail',
                component: TeamTaskProjectDetail,
                childRoutes: [
                    {
                        path: 'analysis/:subprojectId',
                        component: TeamTaskProjectAnalysis
                    },
                    {
                        path: 'statistics/:subprojectId',
                        component: TeamTaskProjectStatistics
                    }
                ]
            },
            {
                path: 'manpower',
                component: TeamManpower
            },
            {
                path: 'plan',
                component: TeamPlan,
                indexRoute: {
                    onEnter: (nextState, replace) => replace('/team/plan/Meeting')
                },
                childRoutes: [
                    {
                        path: 'meeting',
                        component: TeamPlanMeeting
                    },
                    {
                        path: 'plot',
                        component: TeamPlanPlot
                    }
                ]
            }
        ]
    },
    {
        path: 'team/manpower/setting',
        component: TeamManpowerSetting
    },
    {
        path: '__demo__',
        component: Demo,
        childRoutes: demoRoutes
    },
    {
        path: '*',
        component: Error
    }
]

export default routes



