import RootStore from 'core/Store'
import * as api from 'core/api'

class Store extends RootStore {
    constructor(actions) {
        super();
        this.state = {
            test:'default'
        }
        this.listenables = actions
        this.initOnData()
    }

    onRemoveEvents() {
        console.log('onRemoveEvents:+++++')
        this.clearEvents()
        // this.$off('pages/Team/user',this.user)
       // this.$off('pages/Team/list',this.list)
    }

    initOnData() {

        this.$on('pages.Team', function(msg, data ) {
            console.log( msg, data );
        })


    }

    onTestAction(value) {
        this.setState({
            test: value
        })
    }
}

export default Store



