/**
 * RoundSearch
 */

import React, { PropTypes } from 'react'
import classnames from 'classnames'
import Icon from '../Icon/index'
import Checkbox from 'widgets/Checkbox'

class RoundSearch extends React.Component {
    static propTypes = {
        className: PropTypes.string,
        children: PropTypes.any,
        search: PropTypes.func,
        isCheckBox: PropTypes.bool,
        list: PropTypes.array
    }

    static defaultProps = {
        isCheckBox: false
    }

    constructor(props) {
        super()
        this.state = {
            
        }
    }

    componentDidMount() {
        //console.log('000000555555555555')

    }

    componentWillReceiveProps({  }) {
        this.setState({
           isShowCheck: false
        })
    }

    componentWillUnmount() {

    }

    handleCheck() {
        this.setState({
            isShowCheck: !this.state.isShowCheck
        })
    }

    renderCheckbox() {
        return (
            <div className={classnames({"checkbox-box": true, active: true})} onClick={() => this.handleCheck() } >
                <Icon icon="filter" />
               {this.state.isShowCheck ? <Checkbox list={this.props.list} onCheck={this.props.onCheck} /> : null}
            </div>
        )
    }

    render() {
        const { className,search, isCheckBox, ...props } = this.props
        let rootClassName = {
            'root-widget-roundsearch': true,
            [className]: !!className, 
            checkbox: isCheckBox
        }
        return (
            <div className={ classnames(rootClassName) } { ...props }>
                <div className="search-inner">
                    <span className="search-icon" onTouchEnd = {() => search(this.refs.searchVal.value) }><Icon icon="search"/></span>
                    <span className="search">
                        <input ref="searchVal" className="search-text" onKeyDown={ (e) => { e.keyCode == 13 &&  search(this.refs.searchVal.value) } } type="text"/>
                    </span>
                </div>
                {isCheckBox ? this.renderCheckbox() : null}
            </div>
        )
    }
}

export default RoundSearch;



