import RootStore from 'core/Store'
import * as api from 'core/api'

class Store extends RootStore {
    constructor(actions) {
        super();
        this.state = {
            value:'',
            type: ''
        }
        this.listenables = actions

        this.init()
    }

    init() {
        this.$on('components/services/RoundSearch/type', (key, { type }) => {
            console.log("components/services/RoundSearch/type", type)
            this.setState({
                type
            })
        })
    }

    onSearch(value) {
        switch (this.state.type) {
            case 'memorabilia': {
                this.$emit('components/services/RoundSearch/memorabilia', {
                    value
                })
            }
            case 'memorabiliaDetail': {
                this.$emit('components/services/RoundSearch/memorabiliaDetail', {
                    value
                })
            }
            case 'personal': {
                this.$emit('components/services/RoundSearch/personal', {
                    value
                })
            }
            case 'teamPeople': {
                this.$emit('components/services/RoundSearch/teamPeople', {
                    value
                })
            }
            case 'teamProject': {
                this.$emit('components/services/RoundSearch/teamProject', {
                    value
                })
            }
            case 'planPlot': {
                this.$emit('components/services/RoundSearch/planPlot', {
                    value
                })
            }
            case 'planMeeting': {
                this.$emit('components/services/RoundSearch/planMeeting', {
                    value
                })
            }
        }
    }

    onRemoveEvents() {
        this.clearEvents()
    }
}

export default Store



