import React, { PropTypes } from 'react';
import Reflux from 'reflux';
import Store from './store';
import Actions from './actions';
import './styles';

// test Icon
import Icon from '../index'

class Demo extends React.Component {
    constructor(props) {
        super(props);
        this.actions = new Actions();
        this.store = new Store(this.actions);
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })

        this.icons = [
            "talk"
            ,"tags"
            ,"add"
            ,"show"
            ,"more"
            ,"mine"
            ,"tick"
            ,"list"
            ,"filter"
            ,"project"
            ,"scheduling"
            ,"delete"
            ,"time"
            ,"checkbox"
            ,"date"
            ,"clock"
            ,"note"
            ,"tag"
            ,"edit"
            ,"plan"
            ,"memo"
            ,"message"
            ,"task"
            ,"help"
            ,"search"
            ,"head"
            ,"team"
            ,"reports"
            ,"add-user"
        ]
    }

    componentDidMount() {

    }

    componentWillUnmount() {
        this.actions.removeEvents()
    }

    render() {
        return (
            <div className="root-widget-icon__demo__">
                <ul>
                   {this.icons.map(icon =>  <li key={icon} ><Icon icon={icon} /><p>{icon}</p></li> )}
                </ul>
            </div>
        )
    }
}

export default Demo



