import React, { PropTypes } from 'react'
import Reflux from 'reflux'
import Store from './store'
import Actions from './actions'
import './styles'

// test ItemDot
import ItemDot from '../index'
import Button from 'widgets/Button'

class Demo extends React.Component {
    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store(this.actions)
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })
    }

    componentDidMount() {

    }

    componentWillUnmount() {
        this.actions.removeEvents()
    }

    render() {
        const listData = [
            {
                style: "grey",
                title: "界面风格确定",
                name: "Wechat|PM",
                type: "UI设计",
                tag: "近期出图回文",
                message: "今天天气不错......",
                date: "2016年12月20日"
            },
            {
                style: "red",
                title: "界面风格确定",
                name: "Wechat|PM",
                type: "UI设计",
                tag: "近期出图回文",
                message: "今天天气不错......",
                date: "2016年12月20日"
            },
            {
                style: "blue",
                title: "界面风格确定",
                name: "Wechat|PM",
                type: "UI设计",
                tag: "近期出图回文",
                message: "今天天气不错......",
                date: "2016年12月20日"
            }
        ]
        return (
            <div className="root-widget-itemdot__demo__ scroll">
                {
                    listData.map((item, index) => {
                        return (
                            <ItemDot
                                key={ index }
                                itemData={ item }
                                btnHelp={ <Button icon="help" type="icon" /> }
                                btnMore={ <Button icon="more" type="icon" /> }
                            />
                        )
                    })
                }
            </div>
        )
    }
}

export default Demo



