import React, { PropTypes } from 'react'
import Store from './store'
import Actions from './actions'
import './styles'

import Input from 'widgets/Input'
import Icon from 'widgets/Icon'
import ButtonGroup from 'widgets/ButtonGroup'
import Checkbox from 'widgets/Checkbox'
import PullUp from 'widgets/PullUp'

class TaskreplyAddPage extends React.Component {
    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store(this.actions)
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })
        this.tags = {
            a:"进度安排",
            b:"出图提醒",
            c:"会议通知",
            d:"外部条件",
            e:"内部协调"
        }
    }

    componentDidMount() {

    }

    componentWillUnmount() {
        // no del
        this.actions.removeEvents()
    }

    render() {
        return (
            <div className="root-page-taskreplyadd scroll">
                <div className="task-add-list">
                    <Input
                        placeholder="任务内容："
                        leftIcon={ <Icon icon="list" /> }
                        value={ this.state.task.taskContent }
                    />
                    <Input
                        placeholder="标签"
                        leftIcon={ <Icon icon="tag" /> }
                        value={ this.tags[this.state.task.taskType] }
                    />
                    <dl>
                        <dt>
                            <Icon icon="add-user"/>
                        </dt>
                        <dd><p>设定人员</p></dd>
                    </dl>

                    <dl>
                        <dt>
                            <Icon icon="date"/>
                        </dt>
                        <dd>
                            <p>
                                截至日期：
                                { this.state.task.taskTime }
                            </p>
                        </dd>
                    </dl>

                    <Input
                        placeholder="留言:"
                        leftIcon={ <Icon icon="message" /> }
                        value={ this.state.taskReply }
                        onChange={ (e, value) => { this.actions.handleChange('taskReply' ,value) } }
                    />
                    <dl>
                        <dt>
                        </dt>
                        <dd className="reply"><p>{ this.state.task.taskReply }</p></dd>
                    </dl>
                </div>
                <div className="task-add-bottom">
                    <ButtonGroup
                        onConfirm={ ()=> { this.actions.save() }}
                        onCancel={ ()=> { this.actions.cancel() }}
                    />
                </div>
            </div>
        )
    }

}

export default TaskreplyAddPage



