import RootStore from 'core/Store'
import * as api from 'core/api'

class Store extends RootStore {
    constructor(actions) {
        super()
        this.listenables = actions
        this.state = {
            isShow:false,
            subprojectId:-1,
            memberList:[],
            memberSelect:[]
        }
        this.init()
    }

    init() {
        this.$on('services/TaskmemberSelect/isShow',(key, { isShow, subprojectId }) => {
            this.onInsertMemberList(subprojectId)
        })
    }

    onTestAction(value) {
        this.setState({
            test: value
        })
    }

    onRemoveEvents() {
        this.clearEvents()
    }

    onInsertMemberList(subprojectId){
        this.$ajax
            .get(this.api('project/subMembers')(),{
                params: {
                    subprojectId: subprojectId
                }
            })
            .then( res => {
                if(res.status === 200){
                    let members = res.data.subprojectMembers,
                        memberList = new Array(),
                        memberSelect = this.state.memberSelect
                    console.log(memberSelect)
                    members.map((item, i) => {
                        let isCheck = false
                        memberSelect.map((v, j) => {
                            if (item.openId == v.openId){
                                console.log(item.openId)
                                isCheck = true
                            }
                        })
                        var item = {
                            'isCheck': isCheck,
                            'key': i,
                            'openId':members[i].openId,
                            'label': members[i].userName
                        }
                        memberList[i]=item
                    })

                    this.setState({
                        memberList,
                        isShow: true
                    })
                    console.log(res)
                }
            })
            .catch( error => {
                console.log('error:', error)
            })
    }

    onHandleCheck(list) {
        for(let i=0; i<list.length; i++){
            if(list[i].isCheck === true){
                this.setState({
                    memberSelect: list[i]
                })
            }
        }
    }

    onConfirm(){
        let list = this.state.memberList,
            memberSelect = [],
            index = 0

        list.map((item, i) => {
            if(item.isCheck === true){
                memberSelect[index] = {
                    openId: item.openId
                }
                index ++
            }
        })

        this.$emit('pages/TaskAdd/memberSelect', {memberSelect})
        this.setState({
            isShow: false,
            memberSelect
        })
    }

    onCancel(){
        this.setState({
            isShow: false
        })
    }
}

export default Store



