/**
 * ItemThumb
 */

import React, { PropTypes } from 'react'
import classnames from 'classnames'

import ItemSwitch from 'widgets/ItemSwitch'
import Button from 'widgets/Button'

class ItemThumb extends React.Component {
    static propTypes = {
        className: PropTypes.string,
        title: PropTypes.string,
        desc: PropTypes.string,
        onEdit: PropTypes.func,
        onDetail: PropTypes.func,
        thumb: PropTypes.any,
    }

    constuctor() {
        this.state = {
            test: ''
        }
    }

    handleEdit = (event) => {
        event.stopPropagation()
        event.preventDefault()
        this.props.onEdit && this.props.onEdit()
    }

    handleDetail = (event) => {
        event.stopPropagation()
        event.preventDefault()
        this.props.onDetail && this.props.onDetail()
    }

    renderThumb(thumb) {
        return (
            <div className="thumb">{ thumb }</div>
        )
    }

    renderTitle(title) {
        return (
            <h3 className={ classnames({'label':!this.props.desc })}>{ title }</h3>
        )
    }

    renderDesc(desc) {
        return (
            <p>{ desc }</p>
        )
    }

    renderOperation = () => {
        return [
            <Button
                className="btn-delete"
                icon="edit"
                type="icon"
                onClick={ this.handleEdit }
            >
                <span>编辑</span>
            </Button>
        ]
    }

    renderContent({thumb, title, desc}) {
        return (
            <div className="item-thumb-werapper">
                { thumb ? this.renderThumb(thumb) : null }
                <div className="auto">
                    { title ? this.renderTitle(title) : null }
                    { desc ? this.renderDesc(desc) : null }
                </div>
            </div>
        )
    }

    renderSwitch({thumb, title, desc}) {
        return (
            <ItemSwitch onRenderOperation={ this.renderOperation }>
                {this.renderContent({thumb, title, desc})}
            </ItemSwitch>
        )
    }

    render() {
        const { className, thumb, title, desc } = this.props
        return (
            <div
                className={ classnames('root-widget-itemthumb',className) }
                onClick={ this.handleDetail }
            >
                {
                    this.props.onEdit ?
                    this.renderSwitch({thumb, title, desc}) :
                    this.renderContent({thumb, title, desc})
                }
            </div>
        )
    }
}

export default ItemThumb;



