import React, { PropTypes } from 'react'
import Reflux from 'reflux'
import Store from './store'
import Actions from './actions'
import './styles'

import TeamHeader from 'services/TeamHeader'
import TeamFooter from 'services/TeamFooter'

class TeamPage extends React.Component {
    constructor(props, context) {
        super(props, context)
        this.actions = new Actions()
        this.store = new Store(this.actions)
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })
    }

    componentDidMount() {
        console.log('componentDidMount');
        this.actions.didMount()
    }

    componentDidUpdate() {

    }

    componentWillUnmount() {
        this.actions.removeEvents()
        this.actions = null
        this.store = null
    }

    handleClick() {
        this.actions.testAction('wujian')
    }

    render() {
        return (
            <div className="root-page-team">
                { this.props.children }
                <TeamFooter />
            </div>
        )
    }

}

export default TeamPage



