/**
 * Steps
 */

import React, { PropTypes } from 'react'
import classnames from 'classnames'

class Steps extends React.Component {
    static propTypes = {
        className: PropTypes.string,
        list: PropTypes.array,
    }

    constuctor() {
        this.state = {
            test: ''
        }
    }

    renderTitle(listTitle){
        return(
            <div className="list-title">
                { listTitle.titleDate ? this.renderTitleDate( listTitle.titleDate ) : null}
                { listTitle.titleCon ? this.renderTitleCon( listTitle.titleCon ) : null}
            </div>
        )
    }

    renderTitleDate(date){
        return(
            <span className="title-date">{ date }</span>
        )
    }

    renderTitleCon(titleCon){
        return(
            <span className="title-con">{ titleCon }</span>
        )
    }

    renderCon(listCon){
        return(
            <div className="list-con">{ listCon }</div>
        )
    }

    render() {
        const { className, listTitle,listCon, ...props } = this.props
        return (
            <div className={ classnames('root-widget-steps',className) } { ...props }>
           {/*     <ul>
		            {
			            list.map( (item, index)=> {
				            return (
                                <li key={ index }>
                                    <div className="list" >
							            { item.listTitle ? this.renderTitle(item.listTitle) : null }
                                    </div>
						            { item.listCon ? this.renderCon(item.listCon) : null}
                                </li>
				            );
			            })
		            }
                </ul>*/}
                <div className="list" >
		            { listTitle ? this.renderTitle(listTitle) : null }
                </div>
	            { listCon ? this.renderCon(listCon) : null}
            </div>

        )
    }
}

export default Steps;



