/**
 * Modal
 */

import React, { PropTypes } from 'react'
import classnames from 'classnames'

class Modal extends React.Component {
    static propTypes = {
        className: PropTypes.string,
        cancelLabel: PropTypes.oneOfType([
            React.PropTypes.string,
            React.PropTypes.bool
        ]),
        confirmLabel: PropTypes.oneOfType([
            React.PropTypes.string,
            React.PropTypes.bool
        ]),
        isShow: PropTypes.bool.isRequired,
        onCancel: PropTypes.func,
        onConfirm: PropTypes.func,
        children: PropTypes.any
    }

    static defaultProps = {
        cancelLabel:'取消',
        confirmLabel:'确认',
        isShow: false
    }

    constructor(props) {
        super()
        this.state = {
            isShow: props.isShow,
            cancelLabel: props.cancelLabel,
            confirmLabel: props.confirmLabel
        }
    }

    componentDidMount() {

    }

    componentWillReceiveProps({ isShow }) {
        this.setState({
            isShow
        })
    }

    componentWillUnmount() {

    }

    handleCancel = () => {
        if(this.props.onCancel) {
            this.props.onCancel()
        } else {
            this.setState({
                isShow: false
            })
        }
    }

    handleConfirm = () => {
        if(this.props.onConfirm) {
            this.props.onConfirm()
        } else {
            this.setState({
                isShow: false
            })
        }
    }

    render() {
        const { className } = this.props
        return (
            <div className={ classnames('root-widget-modal',{'show': this.state.isShow },className) } >
                <div className="modal-mask"></div>
                <div className="modal-wrapper">
                    <div className="modal-mainer">
                        { this.props.children }
                    </div>
                    <div className="modal-footer">
                        <ul>
                            {
                                this.state.cancelLabel &&
                                <li onClick={ this.handleCancel } >{ this.state.cancelLabel }</li>
                            }
                            {
                                this.state.confirmLabel &&
                                <li onClick={ this.handleConfirm } >{ this.state.confirmLabel }</li>
                            }
                        </ul>
                    </div>
                </div>
            </div>
        )
    }
}

export default Modal;



