import { browserHistory } from 'react-router'

const push = (url) => {
    browserHistory.push(url)
}
const goBack = () => {
    browserHistory.goBack()
}

const goForward = () => {
    browserHistory.goForward()
}

const go = (e) => {
    browserHistory.go(e)
}

const getCurrentLocation = () => {
    return browserHistory.getCurrentLocation()
}

//console.log('browserHistory==+++',browserHistory, browserHistory.getCurrentLocation())

export  default {
    push,
    goBack,
    goForward,
    go,
    getCurrentLocation
}



