import React, { PropTypes } from 'react'
import Store from './store'
import Actions from './actions'
import './styles'
import classnames from 'classnames'

import Checkbox from 'widgets/Checkbox'
import Modal from 'widgets/Modal'
import Icon from 'widgets/Icon'
import {Link} from 'react-router'

class MemberEditPage extends React.Component {
    static propTypes = {
        isCheckBox: PropTypes.bool,
        onHandleClick: PropTypes.func
    }

    static defaultProps = {
        isCheckBox: false
    }

    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store(this.actions)
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })

    }

    /*componentWillReceiveProps({curMembers}) {
     this.setState({
     curMembers
     })
     console.log(curMembers)
     }*/

    componentDidMount() {
        this.actions.insertMemberList()
        this.actions.insertSubMembers()
    }

    componentWillUnmount() {
        // no del
        this.actions.removeEvents()
    }

    renderMemberList() {
        return (
            <div>
                <div>
                    <Icon icon="mine"/> 现有人员：
                    <div className="cur-members">
                        {
                            this.state.curMembers == null ? '' : this.state.curMembers.map((item, index) => {
                                    return <span key={index}>{ item.userName }   </span>
                                })
                        }
                    </div>

                </div>
                <ul className="role-list">
                    {
                        this.state.roleList.map(( item,index ) => {
                            return <li key={ index }
                                       className={ classnames({'current': item.isCurrent}) }
                                       data={item.roleType}
                                       onClick={ () => {this.actions.switchTab(item.roleType) }}>{ item.roleName }</li>
                        })
                    }
                </ul>
                <div className="checkbox">
                    {
                        <Checkbox
                            list={ this.state.memberList }
                            onCheck={ (list) => {
                                this.actions.handleCheck(list)
                            }}

                        />
                    }
                </div>

            </div>
        )
    }

    render() {
        return (
            <div className="root-page-memberedit">
                <Modal isShow={ this.state.isShow }
                       className='memberList'
                       onConfirm={()=>this.actions.confirm()}
                       onCancel={() => this.actions.cancel()
                       }>{ this.renderMemberList()}</Modal>
            </div>
        )
    }

}

export default MemberEditPage



